import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { NotificationService } from '../../../../../../services/notification/notification.service';
import { Router } from '@angular/router';

interface model {
  id: number,
  pseudo: string,
  firstname: string,
  lastname: string,
  email: string,
  credit: any
}

@Component({
  selector: 'app-paiement-admin',
  templateUrl: './paiement-admin.component.html',
  styleUrls: ['./paiement-admin.component.scss']
})
export class PaiementAdminComponent implements OnInit {
  currentSelect: model = {
    id: 0,
    pseudo: null,
    firstname: null,
    lastname: null,
    email: null,
    credit: {}
  }

  creditSent: number = 0

  list: model[] = []

  error: string  = null

  constructor(
    private adminService: AdminService,
    private notificationService: NotificationService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.getModelsActif()
  }


  select(event) {
    const value = event.target.value;
    let index = parseInt(value)
    this.currentSelect = this.list[index]
  }

  getModelsActif() {
    this.adminService.getModelsActif().subscribe(
      (data: any) => {
        console.log(data)
        this.list = data
      }, (error) => {
        console.log(error)
      }
    )
  }

  savePay() {
    this.error = null

    if (this.creditSent === 0 || this.creditSent < 0 || isNaN(parseInt(this.creditSent+''))) {
      this.error = 'Montant invalide'
      return false
    }
    if (this.currentSelect.id === 0) {
      this.error = 'Veuillez slectionner le modle '
      return false
    }
    if (this.creditSent > this.currentSelect.credit.credit ) {
      this.error = 'Montant excdant le solde  payer'
      return false
    }
    this.adminService.payModel(this.currentSelect.id, this.currentSelect.pseudo,
      this.currentSelect.email, this.creditSent).subscribe(
      (data: any) => {
        console.log(data)
        if (data.success) {
          this.notificationService.info('Paiement', 'Paiement effectu')
          this.router.navigate(['/admin/list-paiement'])
        }
        else if (data.error) {
          this.notificationService.errorMsg(data.message)
        }
      }, (error) => {
        console.log(error)
      }
    )
  }
}
