import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class RoomTipsService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient
  ) { }

  debiterRoom(creditId: number, credit: number): Observable<any> {
    const data = {
      creditId,
      credit
    }
    return this.http.post(`${this.endpoint}/room-tips/payCostToEnter`, data, httpOptions)
  }

  createRoom(tips: string, description: string): Observable<any> {
    const data = {
      tips,
      description
    }
    return this.http.post(`${this.endpoint}/room-tips/createRoom`, data, httpOptions)
  }

  getRoomModel(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-tips/model/${id}`, httpOptions)
  }

  getStatRoom(idRoom: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-tips/stat/${idRoom}`, httpOptions)
  }

  updateActif(roomId: number, joined: boolean, type_room: string, peerId: string = null): Observable<any> {
    const data = {
      roomId,
      joined,
      type_room,
      peerId
    }
    // console.log("Update actif ", data);
    return this.http.post(`${this.endpoint}/room-tips/updateActif`, data, httpOptions)
  }

  updateGain(roomId: number, gain: number) {
    const data = {
      roomId,
      gain
    }
    return this.http.post(`${this.endpoint}/room-tips/updateGain`, data, httpOptions)
  }

  getActifs(roomId: number) {
    return this.http.get(`${this.endpoint}/actif-room-tips/${roomId}`, httpOptions)
  }

  joinRoom(roomId: number, joined: boolean, type_room: string, peerId: string = null) {
    const data = {
      roomId,
      joined,
      type_room,
      peerId
    }
    return this.http.post(`${this.endpoint}/actif-room-tips`, data, httpOptions)
  }

  getLastDescriptions() {
    return this.http.get(`${this.endpoint}/room-tips/lastDescriptions`, httpOptions)
  }

}
