import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import * as AOS from 'aos';
import { TIP } from 'src/app/services/tips/tips.service';
import { TipsService } from '../../../services/tips/tips.service';
import { BetweenShopService } from '../../../services/between-shop/between-shop.service';
import { NotificationService } from '../../../services/notification/notification.service';

@Component({
  selector: 'app-tips',
  templateUrl: './tips.component.html',
  styleUrls: ['./tips.component.scss']
})
export class TipsComponent implements OnInit {
  tipsNormaux: TIP[] = [];
  tipsVIP: TIP[] = [];

  currentSymbole: string = null;
  currentCredit: string = null;
  currentDesignation: string = null;
  creditSelected: number = 0;
  isVIPItem: boolean = false;

  @Output() newItemEvent = new EventEmitter<TIP>();
  @Output() closeItemEvent = new EventEmitter<any>();

  listItem: any[] = [];

  constructor(
    public tipsService: TipsService,
    public betweenShopService: BetweenShopService,
    private notificationService: NotificationService
  ) {
    this.tipsNormaux = this.tipsService.tipsNormaux;
    this.tipsVIP = this.tipsService.tipsVIP;
  }

  ngOnInit(): void {
    AOS.init();
    this.getMyItems();
  }

  async getMyItems() {
    this.betweenShopService.getItems().subscribe(
      (data: any[]) => {
        this.tipsVIP.forEach(
          (tips) => {
            data.forEach(
              (item) => {
                if (item.symbol === tips.symbole) {
                  this.listItem.push(tips);
                  // console.log(this.listItem);
                }
              }
            )
          }
        )

        this.listItem = data;
        // console.log(this.listItem);
      }, (error) => {
        console.log(error);
      }
    )
  }

  isItem(symbol: string) {
    const index = this.listItem.findIndex(
      (element) => element.symbol === symbol
    )
    return (index >= 0) ? true : false;
  }

  hoverItems(symbole: string, credit: number, designation: string, vip: boolean) {
    this.currentSymbole = symbole;
    this.currentCredit = credit + ' crdits';
    this.currentDesignation = designation;
    this.creditSelected = credit;
    this.isVIPItem = vip;
  }

  outItems() {
    this.currentSymbole = null;
    this.currentCredit = null;
    this.creditSelected = 0;
  }


  selectItem() {
    if (!this.currentSymbole || !this.currentCredit) return false;

    if (this.isVIPItem && !this.isItem(this.currentSymbole)) {
      this.notificationService.infoMsg('Item  debloquer dans Between Shop');
      return false;
    }

    const data: TIP = {
      symbole: this.currentSymbole,
      credit: this.creditSelected,
      src: null,
      designation: this.currentDesignation,
      vip: this.isVIPItem
    }
    this.newItemEvent.emit(data);
  }

  closeTips() {
    this.closeItemEvent.emit();
  }
}
