import { Component, Input, IterableDiffers, OnInit, ViewChild } from '@angular/core';
import { Chat } from '../../../../interfaces/chat.interface';
import * as $ from 'jquery';
import { SocketService } from '../../../../services/socket/socket.service';
import { ClientService } from '../../../../services/client/client.service';
import { MatMenuTrigger } from '@angular/material/menu';
import { BanishService } from '../../../../services/banish/banish.service';
import { TIP, TipsService } from 'src/app/services/tips/tips.service';

interface message {
  source: string,
  name: string,
  url: string,
  message: string,
  color: string
}

@Component({
  selector: 'app-live-free-chat',
  templateUrl: './live-free-chat.component.html',
  styleUrls: ['./live-free-chat.component.scss']
})
export class LiveFreeChatComponent implements OnInit {
  @Input()
  modelId: number;

  @Input()
  pseudoModel: string;

  @Input()
  leaved: boolean;

  @Input()
  passedToPrivate: boolean;

  @Input()
  profil: string;

  @Input()
  showMenu: string;

  @Input()
  roomId: string;

  // gift
  @Input()
  isModel: string;

  // gift
  @Input()
  clientId: number;

  @Input()
  live: string;

  room: number;

  user = "assets/images/user_chat.png";
  listColor = [];

  @Input()
  messages: Chat[] = [];

  currentPseudo: string = null;

  iterableDiffer: any;

  @ViewChild(MatMenuTrigger)
  contextMenu: MatMenuTrigger;

  contextMenuPosition = { x: '0px', y: '0px' };

  tipsNormaux: TIP[] = [];
  tipsVIP: TIP[] = [];

  constructor(
    private iterableDiffers: IterableDiffers,
    private socketService: SocketService,
    private clientService: ClientService,
    private banishService: BanishService,
    private tipsService: TipsService) {
    this.iterableDiffer = iterableDiffers.find([]).create(null);
    this.tipsNormaux = this.tipsService.tipsNormaux;
    this.tipsVIP = this.tipsService.tipsVIP;
  }


  ngOnInit(): void {
    if (this.profil === undefined) this.profil = this.user;
    if (this.messages.length > 0) {
      $(document).ready(function () {
        setTimeout(
          () => {
              $('#main_chat').animate({
                scrollTop: $('#main_chat .msg:last-child').position().top
              }, 10);
          }, 1000
        )
      });
    }
  }


  ngDoCheck() {
    let changes = this.iterableDiffer.diff(this.messages);
    if (changes) {
      // this.socketService.soundOutcome()
      $("#main_chat").animate({ scrollTop: $('#main_chat').prop("scrollHeight")}, 10);
    }
  }

  getPseudo(clientId: number) {
    this.clientService.getClient(clientId).subscribe(
      (data) => {
        this.currentPseudo = data.pseudo;
        // console.log(this.currentPseudo);
      }
    )
  }

  showPseudo() {

  }

  clearPseudo() {
    this.currentPseudo = null;
  }

  onContextMenu(event: MouseEvent, item: any) {
    event.preventDefault();
    this.contextMenuPosition.x = event.clientX + 'px';
    this.contextMenuPosition.y = event.clientY + 'px';
    this.contextMenu.menuData = { 'item': item };
    this.contextMenu.menu.focusFirstItem('mouse');
    this.contextMenu.openMenu();
  }

  banishClient(item: any) {
    if (!item.id_source || item.id_source == this.modelId) return null
    const clientId = item.id_source
    // console.log('banish client : ', item.id_source, ' model :', this.modelId)
    this.banishService.banishClient(this.modelId, clientId).subscribe(
      (data) => {
        this.socketService.banishClient(this.roomId, clientId)
        // console.log(data)
      }
    )
  }

  getImageSource(symbol: string, message: string) {
    // console.log(symbol);
    // console.log(this.tipsNormaux);
    let tips = null;
    if (message === 'vip') {
      tips = this.tipsVIP.find(tips => tips.symbole === symbol);
    } else {
      tips = this.tipsNormaux.find(tips => tips.symbole === symbol);
    }
    // console.log(tips);
    return tips.src;
  }

  compareDate(msgCreated: Date) {
    if (this.isModel === 'true') return true;
    const lastStoreDate = this.clientService.getLastChat();
    const lastDate = new Date(lastStoreDate);
    const comparedDate = new Date(msgCreated);
    if (comparedDate < lastDate) return false;
    else return true;
    // console.log('Last chat date from ',comparedDate, ' To ', lastDate);
    return true;
  }
}

