import { Component, OnInit } from '@angular/core';
import { ModelService } from '../../../services/model/model.service';
import { Router } from '@angular/router';
import { context } from 'src/app/interfaces/get-model.interface';
import { GetModel } from '../../../interfaces/get-model.interface';
import { NotificationService } from '../../../services/notification/notification.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { MatDialog } from '@angular/material/dialog';
import { ProfilModelDetailComponent } from '../../../modals/profil-model-detail/profil-model-detail.component';

@Component({
  selector: 'app-home-client',
  templateUrl: './home-client.component.html',
  styleUrls: ['./home-client.component.scss']
})
export class HomeClientComponent implements OnInit {
  defaultImage = './../../../../assets/images/blured.jpg';

  image = "assets/images/image.png";
  section: number = 8;

  totalInLive: number = 0;
  paginationLive: number[] = [];
  photoLive: string[] = [];
  indiceLive: number = 1;

  totalInChat: number = 0;
  paginationChat: number[] = [];
  photoChat: string[] = [];
  indiceChat: number = 1;

  totalOffline: number = 0;
  paginationOffline: number[] = [];
  photoOffline: string[] = [];
  indiceOffline: number = 1;

  // count = [0, 1, 2, 3, 4, 5, 6, 7];

  OFFLINE = 'Hors ligne';
  INLINE = 'En ligne';
  LIVE  = "En live";

  constructor(
    private modelSrv: ModelService,
    private router: Router,
    private notificationService: NotificationService,
    private popupService: PopupService,
    private profilService: ProfilService,
    private dialog: MatDialog
    ) {
    }

  ngOnInit(): void {
    this.init();
  }


  init() {
    this.getTotal().then(
      (data) => {
        this.initPagination();
      }
    );
  }

  async getTotal() {
    await this.modelSrv.count().subscribe(
      (data) => {
        // console.log(data);
        this.totalOffline = data.offline;
        this.totalInChat = data.chat;
        this.totalInLive = data.live;
      }
    )
  }

  initPagination() {
    var margeLive = Math.ceil((this.totalInLive / this.section) - 0.1);
    var margeChat = Math.ceil((this.totalInChat / this.section) - 0.1);
    var margeOffline = Math.ceil((this.totalOffline / this.section) - 0.1);

    margeLive = margeLive < 1 ? 1 : margeLive;
    margeChat = margeChat < 1 ? 1 : margeChat;
    margeOffline = margeOffline < 1 ? 1 : margeOffline;

    for (let index = 1; index <= margeLive; index++) this.paginationLive.push(index);
    for (let index = 1; index <= margeChat; index++) this.paginationChat.push(index);
    for (let index = 1; index <= margeOffline; index++) this.paginationOffline.push(index);
    this.initPhoto(context.LIVE, 1);
    this.initPhoto(context.INLINE, 1);
    this.initPhoto(context.OFFLINE, 1);
  }

  async initPhoto(contxt: any, indice: number) {
    var debut = indice - 1;
    debut = debut * this.section;

    const model: GetModel = {
      context: contxt,
      begin: debut,
      end: debut + this.section
    }

    switch (contxt) {
      case context.LIVE :
        if (debut > this.totalInLive) {this.indiceLive--; return null;}
        this.indiceLive = indice;
        await this.modelSrv.getList(model).subscribe(
          (data) => {
            // console.log(data);
            this.photoLive = data;
          }
        );
        break;

      case context.INLINE :
        if (debut > this.totalInChat) {this.indiceChat--; return null;}
        this.indiceChat= indice;
        await this.modelSrv.getList(model).subscribe(
            (data) => {
              // console.log('In chat ', data);
              this.photoChat = data;
            }, (error) => {
              console.log(error)
            }
          );
        break;

      case context.OFFLINE :
        if (debut > this.totalOffline) {this.indiceOffline--; return null;}
        this.indiceOffline= indice;
        await this.modelSrv.getList(model).subscribe(
          (data) => {
            // console.log(data);
            this.photoOffline = data;
          }
        );
        break;

      default:
        break;
    }
  }

  prev(contxt: any) {
    switch (contxt) {
      case context.LIVE :
        if (this.indiceLive < 2) return false;
        this.indiceLive--;
        this.initPhoto(contxt, this.indiceLive);
        break;

      case context.INLINE :
        if (this.indiceChat < 2) return false;
        this.indiceChat--;
        this.initPhoto(contxt, this.indiceChat);
        break;

      case context.OFFLINE :
        if (this.indiceOffline < 2) return false;
        this.indiceOffline--;
        this.initPhoto(contxt, this.indiceOffline);
        break;

      default:
        break;
    }
  }

  next(contxt: any) {
    switch (contxt) {
      case context.LIVE :
        this.indiceLive++;
        this.initPhoto(contxt, this.indiceLive);
        break;

      case context.INLINE :
        this.indiceChat++;
        this.initPhoto(contxt, this.indiceChat);
        break;

      case context.OFFLINE :
        this.indiceOffline++;
        this.initPhoto(contxt, this.indiceOffline);
        break;

      default:
        break;
    }
  }

  async liveFree(id: number) {
    this.profilService.getSpecificProfil(id).subscribe(
      (data) => {
        const status = data ? data.status : null;
        if (status === 'En ligne') {
          this.navigateTo('/client/live/free', id);
        } else {
          this.popupService.info(null, 'LIVE CHAT','MODELE INDISPONIBLE');
        }
      }
    );
  }

  livePrivate(id: number, pseudo: string, status: string) {
    // console.log(status);
    this.profilService.getSpecificProfil(id).subscribe(
      (data) => {
        const newStatus = data ? data.status : null;
        if (status === newStatus) {
          if (status === 'En live') {
            this.navigateTo('/client/live/private', id);
          } else if (status === 'En tips') {
            this.navigateTo('/client/live/tips-clients', id);
          } else if (status === 'En live choice') {
            this.popupService.info(null, 'LIVE OCCUPE', `${pseudo} est en live CHOICE US en ce moment`);
          } else if (status === 'En vip') {
            this.popupService.info(null, 'LIVE OCCUPE', `${pseudo} est en live VIP en ce moment`);
          }
        } else {
          this.popupService.info(null, 'LIVE CHOISI','MODELE INDISPONIBLE POUR CE LIVE');
        }
      }
    );
  }

  navigateTo(url: string, modelId: number) {
    this.router.navigate([`${url}`, {model: modelId}]);
  }


  getProfil(id: number, pseudo: string, path_soft: string, year_birth: number) {
    const dialogRef = this.dialog.open(ProfilModelDetailComponent, {
      width: '100%',
      height: '100%',
      data: {
        modelId : id,
        modelPseudo : pseudo,
        modelProfil : path_soft,
        modelAge: year_birth
      }
    });
    dialogRef.afterClosed().subscribe(result => {});
  }
}
