import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../../../services/admin/admin.service';

@Component({
  selector: 'app-main-left-dashboard-admin',
  templateUrl: './main-left-dashboard-admin.component.html',
  styleUrls: ['./main-left-dashboard-admin.component.scss']
})
export class MainLeftDashboardAdminComponent implements OnInit {
  demandes = [
    {
      title : 'Demande Inscription',
      value: 0
    },
    {
      title : 'Demande de dsactivation',
      value: 0
    },
    {
      title : 'Demande de suppression',
      value: 0
    },
  ]

  // notifications = [
  //   {
  //     title : 'Nouveaux Messages',
  //     value: 15
  //   },
  //   {
  //     title : 'Nouveaux Demandes',
  //     value: 5
  //   }
  // ]

  clients = [
    {
      title : 'Nouveaux clients',
      value: 0
    },
    {
      title : 'Nombre total des clients',
      value: 0
    }
  ]

  modeles = [
    {
      title : 'Nouveaux modles',
      value: 0
    },
    {
      title : 'Nombre total des modles',
      value: 0
    }
  ]

  show = [
    {
      title: 'Show Priv',
      value: 0
    },
    {
      title: 'Show VIP',
      value: 0
    },
    {
      title: 'Chiffre d\'affaire',
      value: '0 '
    }
  ]

  constructor(
    private adminService: AdminService
  ) { }

  ngOnInit(): void {
    this.countModelInscription()
    this.countModelDesactivation()
    this.countModelSuppression()
    this.countClientActif()
    this.countModelActif()
    this.countNewLastClients()
    this.countNewLastModels()
    this.countRoomPrivate()
    this.countRoomVIP()
    this.getChiffreAffaire()
  }

  countModelInscription() {
    this.adminService.countModelState('En attente').subscribe(
      (data: any) => {
        this.demandes[0].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countModelDesactivation() {
    this.adminService.countModelState('Desactivation').subscribe(
      (data: any) => {
        this.demandes[1].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countModelSuppression() {
    this.adminService.countModelState('Suppression').subscribe(
      (data: any) => {
        this.demandes[2].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countModelActif() {
    this.adminService.countModelActif().subscribe(
      (data: any) => {
        this.modeles[1].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countClientActif() {
    this.adminService.countClientActif().subscribe(
      (data: any) => {
          this.clients[1].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countNewLastClients() {
    this.adminService.newLastClients().subscribe(
      (data: any) => {
          this.clients[0].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countNewLastModels() {
    this.adminService.newLastModels().subscribe(
      (data: any) => {
          this.modeles[0].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countRoomPrivate() {
    this.adminService.countRoom('private').subscribe(
      (data: any) => {
        this.show[0].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  countRoomVIP() {
    this.adminService.countRoom('vip').subscribe(
      (data: any) => {
        this.show[1].value = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  getChiffreAffaire() {
    this.adminService.getChiffreAffaire().subscribe(
      (data : {credit: number, montant: string}) => {
        // console.log(data)
        this.show[2].value = data.montant
      }, (error) => {
        console.log(error)
      }
    )
  }
}
