import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { PaiementService } from '../../../../../../services/paiement/paiement.service';
import { NotificationService } from '../../../../../../services/notification/notification.service';
import { MatDialog } from '@angular/material/dialog';
import { SuiviPayModelComponent } from '../../../../../../modals/suivi-pay-model/suivi-pay-model.component';

@Component({
  selector: 'app-suivi-models-admin',
  templateUrl: './suivi-models-admin.component.html',
  styleUrls: ['./suivi-models-admin.component.scss']
})
export class SuiviModelsAdminComponent implements OnInit {
  today = new Date().toLocaleDateString();
  dayToday = new Date().getDate();
  monthToday = new Date().getMonth() + 1;
  yearToday = new Date().getFullYear();

  models: any[] = [];
  origModels: any[] = [];
  filterPseudo: any = '';

  four = './../../../../../../../assets/icons/more.svg';
  search = './../../../../../../assets/icons/admin-icons/search.png'

  constructor(
    private adminService: AdminService,
    public paymentService: PaiementService,
    private notificationService: NotificationService,
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.getModels();
  }

  getModels() {
    this.adminService.getCreditActifsModels().subscribe(
      (data: any) => {
        this.models = data;
        this.origModels = data;
        this.models.forEach( (model) => {
          model.createdAt = this.adminService.formatDate(model.createdAt);
          model.nextPayment = this.nextPayment(model.credit.lastPayment);
          model.payCredit = 0;
          model.soldeCredit = model.credit.credit;
        });
        // console.log(this.models);
      }, (error) => {
        console.log(error);
      }
    )
  }

  payModel(index: number, idModel: number, pseudoModel: string, emailModel: string) {
    console.log(index);
    const payCredit = this.models[index].payCredit;
    const soldeCredit = this.models[index].soldeCredit;
    if (payCredit > this.models[index].credit.credit || payCredit <= 0) {
      this.notificationService.errorMsg('Paiement crdits invalide');
      return null;
    }

    this.adminService.payCreditModel(idModel, pseudoModel, emailModel, payCredit, this.today).subscribe(
      (data) => {
        // console.log(data);
        this.getModels();
        this.notificationService.successMsg(`${payCredit} crdits pays  ${pseudoModel}`);
      }, (error) => {
        console.log(error);
      }
    )
  }

  nextPayment(lastPayment: string) {
    let calculateDay:  number = 0;
    let calculateMonth: string = null;
    if (!lastPayment || lastPayment === undefined) {
      calculateDay = this.dayToday;
      calculateMonth = this.monthToday.toString();
    } else {
      calculateDay = parseInt(lastPayment.substring(0,2));
      calculateMonth = lastPayment.substring(3,5);
    }

    if (calculateDay >= 29) {
      const nextPay = '14 au 28 /' + (this.monthToday + 1) + '/ ' + this.yearToday;
      return nextPay;
    } else if (calculateDay <= 13) {
      const nextPay = '14 au 28 /' + this.monthToday + '/ ' + this.yearToday;
      return nextPay;
    } else {
      const nextPay = '29 /' + calculateMonth + '/' + this.yearToday + ' au 13 /' + (this.monthToday + 1) + '/' + this.yearToday;
      return nextPay;
    }
  }

  canBePaid(lastPayment: string) {
    let theDay:  number = 0;
    let theMonth: number = 0;
    if (!lastPayment || lastPayment === undefined) {
      theDay = this.dayToday;
      theMonth = this.monthToday;
    } else {
      theDay = parseInt(lastPayment.substring(0,2));
      theMonth = parseInt(lastPayment.substring(3,5));
    }

    if (theMonth < this.monthToday) {
      return true;
    } else {
      if (theDay >= 14) {
        if (theDay <= 28) {
          if (this.dayToday >= 29) {
            if (this.dayToday <= 13) {
              return true;
            }
          }
        } else {
          if (this.dayToday >= 14) {
            if (this.dayToday <= 28) {
              return true;
            }
          }
        }
      }
      else {
        if (this.dayToday >= 14) {
          if (this.dayToday <= 28) {
            return true;
          }
        }
      };
    }

    return false;
  }

  wait() {
    this.notificationService.infoMsg('La date de paiement n\'est pas valide');
  }

  filterModel() {
    console.log('something change');
    this.models = this.origModels.filter(
      (models) => {
        return models.pseudo.includes(this.filterPseudo);
      }
    )
  }

  detailPay(modelId: number, modelPseudo: string) {
    const dialogRef = this.dialog.open(SuiviPayModelComponent, {
      width: '80vh',
      height: '80vh',
      data: {
        modelId,
        modelPseudo
      }
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

}
