import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { PaiementService } from '../../services/paiement/paiement.service';
import { AdminService } from '../../services/admin/admin.service';

@Component({
  selector: 'app-suivi-pay-model',
  templateUrl: './suivi-pay-model.component.html',
  styleUrls: ['./suivi-pay-model.component.scss']
})
export class SuiviPayModelComponent implements OnInit {
  modelId: number;
  modelPseudo: string = null;
  pay: any[];

  constructor(
    public dialogRef: MatDialogRef<SuiviPayModelComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private paiementService: PaiementService,
    public adminService: AdminService
  ) { }

  ngOnInit(): void {
    this.modelId = this.data.modelId;
    this.modelPseudo = this.data.modelPseudo;
    this.getSuiviPay();
  }

  getSuiviPay() {
    this.paiementService.getSuiviPay(this.modelId).subscribe(
      (data: any[]) => {
        console.log('Get suivi ', data);
        this.pay = data;
      }, (error) => {
        console.log(error);
      }
    )
  }

}
