import { Component, OnDestroy, OnInit } from '@angular/core';
import { HeaderService } from '../../../services/header/header.service';
import { ClientService } from '../../../services/client/client.service';
import { LocalStorageService } from 'ngx-localstorage';
import { CreditService } from '../../../services/credit/credit.service';
import { PopupService } from '../../../services/popup/popup.service';
import { NotificationService } from '../../../services/notification/notification.service';
import { Pack } from 'src/app/interfaces/pack-credit.interface';
import { PaiementService } from '../../../services/paiement/paiement.service';
import { isBoolean } from 'util';
import { CentralPayComponent } from '../../../modals/central-pay/central-pay.component';
import { MatDialog } from '@angular/material/dialog';
import * as creditVIP from './../../../services/credit/creditVIP.json';
import { SubscribeService } from '../../../services/subscribe/subscribe.service';

@Component({
  selector: 'app-buy-credit',
  templateUrl: './buy-credit.component.html',
  styleUrls: ['./buy-credit.component.scss']
})
export class BuyCreditComponent implements OnInit {
  solde = 0;
  coinImg = "assets/icons/coin-stack.svg";
  loading: boolean = false;
  currentPack: string = 'bienvenue';

  info = {
    id: 0,
    pseudo: '',
    credit: 0,
    creditId: null,
    promo: null
  }

  packBienvenue: Pack[] = this.creditService.packBienvenue;

  packBronze: Pack[] = this.creditService.packBronze;

  packArgent: Pack[] = this.creditService.packArgent;

  packOr: Pack[] = this.creditService.packOr;

  subscription = {
    subscribed: false,
    expired: false,
    subscribe: {},
    rest: 0
  }

  constructor(
    private clientService: ClientService,
    private store: LocalStorageService,
    private creditService: CreditService,
    private notificationService: NotificationService,
    private paiementService: PaiementService,
    private dialog: MatDialog,
    private subscribeService: SubscribeService
  ) {
   }

  ngOnInit(): void {
    this.getMyInfo();
  }

  // Get client infos
  async getMyInfo() {
    this.isSubscribed();
    const id = parseInt(this.store.get('identification//'));
    await this.clientService.getMyInfos().subscribe(
      (data) => {
        // console.log('My INFO ', data);
        this.info.id = data.id;
        this.info.pseudo = data.pseudo;
        this.info.credit = data.credit ? data.credit.credit : 0;
        this.info.creditId = data.credit ? data.credit.id : 0;
      }
    )
  }

  select(event) {
    // console.log(event.target.value);
    const value = event.target.value;
    this.currentPack = value;
  }

  buyPack(montant: number, credit: number, designation: string) {
    if (this.info.creditId === null) return false;

    if (designation === 'bienvenue') {
      this.paiementService.verifyPack().subscribe(
        (data: any) => {
          if (data.error) {
            this.notificationService.infoMsg(data.message);
          } else {
            this.openCentralPayModal(montant, credit, designation);
          }
        }, (error) => {
          console.log(error)
        }
      )
    } else {
      this.openCentralPayModal(montant, credit, designation);
    }
  }

  openCentralPayModal(montant: number, credit: number, designation: string) {
      this.loading = true;
      this.paiementService.openCentralPay(montant*100).subscribe(
      (data: any) => {
        this.loading = false;
        // console.log('Test ', data);
        if (data && data.breakdowns[0] && data.breakdowns[0].endpoint) {
          const url = data.breakdowns[0].endpoint;
          const paymentRequestId = data.paymentRequestId;
          // console.log('Endpoint ', url);
          const dialogRef = this.dialog.open(CentralPayComponent, {
            width: '90vh',
            data: {
              montant,
              credit,
              designation,
              creditId: this.info.creditId,
              id: this.info.id,
              url,
              paymentRequestId
            }
          });

          dialogRef.afterClosed().subscribe(result => {
            if (isBoolean(result)) {

            } else {
              if (result === 'good') {
                this.getMyInfo().then(
                  (data) => {
                    this.paiementService.buyPack(credit).subscribe(
                      (data) => {
                        this.notificationService.success(`Pack ${this.currentPack}`,
                        `Vous avez obtenu ${credit} crdits de plus.`);
                      }, (error) => {
                        console.log(error)
                      }
                    )
                });
              }
            }
          });
        }
      }, (error) => {
        this.loading = false;
        console.log(error);
      }
    );

  }

  getValueVIP(credit: number) {
    let point  = 0;
    creditVIP.points.forEach(
      (value) => {
        if (value.credit === credit) {
          if (this.subscription.subscribed && !this.subscription.expired) point = value.subscribe;
          else point = value.unsubscribe;
        }
      }
    );
    return point;
  }

  isSubscribed() {
    this.subscribeService.verify().subscribe(
      (data) => {
        // console.log(data);
        this.subscription = data;
      }
    )
  }

  paySubscription() {
    let futureDate = new Date();
    futureDate.setDate(futureDate.getDate() + 30);
    // console.log(futureDate);
    const price = 5;
    this.loading = true;
    this.paiementService.openCentralPay(price*100).subscribe(
      (data: any) => {
        this.loading = false;
        // console.log('Test ', data);
        if (data && data.breakdowns[0] && data.breakdowns[0].endpoint) {
          const url = data.breakdowns[0].endpoint;
          const paymentRequestId = data.paymentRequestId;
          
          const dialogRef = this.dialog.open(CentralPayComponent, {
            width: '90vh',
            data: {
              montant: price,
              credit: price,
              designation: 'abonnement',
              creditId: this.info.creditId,
              id: this.info.id,
              url,
              paymentRequestId
            }
          });

          dialogRef.afterClosed().subscribe(result => {
            if (isBoolean(result)) {

            } else {
              if (result === 'good') {
                this.subscribeService.new(price, futureDate).subscribe(
                  (data) => {
                    console.log(data);
                    this.isSubscribed();
                    this.notificationService.success('Abonnement', 'Flicitation, vous tes abonn');
                  }, (error) => {
                    console.log(error);
                  }
                );
              }
            }
          });
          

        
        }
      }, (error) => {
        this.loading = false;
        console.log(error);
      }
    );
  }

  dayBetweenDate() {
    let startDate: string = "2021-04-01";
    let date1: Date = new Date();
    let date2: Date = new Date(startDate);
    let timeMilli: number = date1.getTime() - date2.getTime();
    let daysBetweensDates: number = Math.ceil(timeMilli / (1000 * 60 * 60 * 24));
    return daysBetweensDates;
  }

}
