import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { PaiementService } from '../../../services/paiement/paiement.service';
import { RoomTipsService } from '../../../services/roomTips/room-tips.service';



@Component({
  selector: 'app-setting-tips',
  templateUrl: './setting-tips.component.html',
  styleUrls: ['./setting-tips.component.scss']
})
export class SettingTipsComponent implements OnInit {
  incomplete: boolean = false;
  invalid: boolean = false;
  loading: boolean = false;

  settingBronze = [
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null }
  ]

  settingArgent = [
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null }
  ]

  settingOr = [
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null },
    { tips: 0, description: null }
  ]


  constructor(
    public dialogRef: MatDialogRef<SettingTipsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private paiementService: PaiementService,
    private roomTipsService: RoomTipsService
  ) { }

  ngOnInit(): void {
    this.roomTipsService.getLastDescriptions().subscribe(
      (data: any) => {
        if (data.tips && data.descriptions) {
          const tips = data.tips?.split('~');
          const descriptions = data.descriptions?.split('~');
          this.autocompleteField(tips, descriptions);
        }

      }, (error) => {
        console.log(error);
      }
    )
  }

  autocompleteField(tips: any[], descriptions: any[]) {
    let countBronze = 0;
    let countArgent = 0;
    let countOr = 0;
    for(let i = 0; i < 5; i++) {
      this.settingBronze[countBronze].tips = parseInt(tips[i]);
      this.settingBronze[countBronze].description = descriptions[i];
      countBronze++;
    }
    for(let i = 5; i < 10; i++) {
      this.settingArgent[countArgent].tips = parseInt(tips[i]);
      this.settingArgent[countArgent].description = descriptions[i];
      countArgent++;
    }
    for(let i = 10; i < 15; i++) {
      this.settingOr[countOr].tips = parseInt(tips[i]);
      this.settingOr[countOr].description = descriptions[i];
      countOr++;
    }
  }

  close(): void {
    this.dialogRef.close(0);
  }

  async validate(tips: string, description: string): Promise<void> {
    this.dialogRef.close({
      success: true,
      tips,
      description
    });
  }

  someTipsBronze(isPrice: boolean) {
    let total = 0;
    this.settingBronze.map(
      (value) => {
        total += value.tips;
      }
    );
    if(isPrice) return this.paiementService.convertCreditToMoney(total);
    return total;
  }

  someTipsArgent(isPrice: boolean) {
    let total = 0;
    this.settingArgent.map(
      (value) => {
        total += value.tips;
      }
    );
    if(isPrice) return this.paiementService.convertCreditToMoney(total);
    return total;
  }

  someTipsOr(isPrice: boolean) {
    let total = 0;
    this.settingOr.map(
      (value) => {
        total += value.tips;
      }
    );
    if(isPrice) return this.paiementService.convertCreditToMoney(total);
    return total;
  }

  totalTips(isPrice: boolean) {
    let total = 0;
    this.settingBronze.map((value) => total += value.tips);
    this.settingArgent.map((value) => total += value.tips);
    this.settingOr.map((value) => total += value.tips);
    if(isPrice) return this.paiementService.convertCreditToMoney(total);
    return total;
  }

  saveSetting() {
    this.incomplete = false;
    this.invalid = false;
    let isEmpty = false;
    let isInvalid = false;
    let last = 0;
    let tipsFusion = '';
    let descriptionsFusion = '';

    // Verify if is Empty
    this.settingBronze.map((value) => { if(value.tips <= 0 || value.description === null || value.description === '') isEmpty = true;});
    this.settingArgent.map((value) => { if(value.tips <= 0 || value.description === null || value.description === '') isEmpty = true;});
    this.settingOr.map((value) => { if(value.tips <= 0 || value.description === null || value.description === '') isEmpty = true;});
    if (isEmpty) { this.incomplete = true; return null; }

    // Verify if value tips croissant
    this.settingBronze.map((value) => {
      if (value.tips < last) {isInvalid = true; console.log(value.tips, ' < ' , last)}
      last = value.tips;
    });

    this.settingArgent.map((value) => {
      if (value.tips < last) {isInvalid = true; console.log(value.tips, ' < ' , last)}
      last = value.tips;
    });

    this.settingOr.map((value) => {
      if (value.tips < last) {isInvalid = true; console.log(value.tips, ' < ' , last)}
      last = value.tips;
    });

    if (isInvalid) { this.invalid = true; return null; }

    this.loading = true;
    // fusion all tips and descrptions value
    this.settingBronze.map((value) => { tipsFusion += value.tips + '~'; descriptionsFusion += value.description + '~'; });
    this.settingArgent.map((value) => { tipsFusion += value.tips + '~'; descriptionsFusion += value.description + '~'; });
    this.settingOr.map((value) => { tipsFusion += value.tips + '~'; descriptionsFusion += value.description + '~'; });

    this.loading = false;
    this.validate(tipsFusion, descriptionsFusion);
  }


}
