import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Socket } from 'ngx-socket-io';
import { RoomPrivateService } from '../roomPrivate/room-private.service';
import { RoomTipsService } from '../roomTips/room-tips.service';
import { ChatSocket } from 'src/app/interfaces/chat.interface';

@Injectable({
  providedIn: 'root'
})
export class SocketService {

  messages: Observable<any>;
  constructor(
    private socket: Socket,
    private roomPrivateService: RoomPrivateService,
    private roomTipsService: RoomTipsService) {}

    soundIncome() {
      const audio = new Audio("./../../../assets/sound/incoming.wav" );
      audio.play();
    }

    soundOutcome() {
      const audio = new Audio("./../../../assets/sound/outcoming.mp3");
      audio.play();
    }

  sendCurrentSaloon(idRoom: any, live_selected: string) {
    const data = {
      idRoom,
      live_selected
    }
    this.socket.emit('currentSaloon', data);
  }

  askCurrentSaloon(idRoom: any, modelId: number) {
    const data = {
      idRoom,
      modelId
    }
    this.socket.emit('ask currentSaloon', data);
  }

  listen(eventName: string) {
    return new Observable((subscriber: any) => {
      this.socket.on(eventName, (data) => {
        subscriber.next(data);
      })
    })
  }

  async emit(eventName, data) {
    return await this.socket.emit(eventName, data);
  }

  sendMessage(data: ChatSocket) {
    this.socket.emit('message', data);
  }


  join(idRoom: number, modelId: number, pseudoClient?: string, idClient?: number) {
    const data = {
      room: idRoom + '',
      modelId: modelId,
      show: 'free',
      clientPseudo: pseudoClient,
      clientId: idClient
    }
    this.socket.emit('join', data)
  }

  leave(idRoom: number, role: string, pseudoClient?: string, idClient?: number) {
    const data = {
      room: idRoom + '',
      role: role,
      show: 'free',
      clientPseudo: pseudoClient,
      clientId: idClient
    }
    this.socket.emit('leave', data)
  }

  async joinPrivate(idRoom: number, modelId: number, clientId?: number, clientPseudo?: string, clientPeer?: string) {
    const data = {
      room: idRoom + 'P',
      modelId: modelId,
      show: 'private',
      clientId,
      clientPseudo,
      clientPeer
    }
    this.socket.emit('join', data)
  }

  async leavePrivate(idRoom: number, role: string, clientId: number) {
    const data = {
      room: idRoom + 'P',
      role: role,
      show: 'private',
      clientId
    }
    this.socket.emit('leave', data)
    return await this.roomPrivateService.updateActif(idRoom, false, 'private');
  }

  async joinVip(idRoom: number, modelId: number) {
    const data = {
      room: idRoom + 'V',
      modelId: modelId,
      show: 'vip'
    }
    this.socket.emit('join', data)
  }

  async leaveVip(idRoom: number, role: string) {
    const data = {
      room: idRoom + 'V',
      role: role,
      show: 'vip'
    }
    return await this.socket.emit('leave', data)
  }

  async joinTips(idRoom: number, modelId: number, clientId?: number, clientPseudo?: string, clientPeer?: string) {
    const data = {
      room: idRoom + 'T',
      modelId: modelId,
      show: 'tips',
      clientId,
      clientPseudo,
      clientPeer
    }
    this.socket.emit('join', data);
    return await this.roomTipsService.updateActif(idRoom, true, 'tips');
  }

  async leaveTips(idRoom: number, role: string, clientId: number) {
    const data = {
      room: idRoom + 'T',
      role: role,
      show: 'tips',
      clientId
    }
    this.socket.emit('leave', data)
    return await this.roomTipsService.updateActif(idRoom, false, 'tips', role);
  }

  async sendClientPeerId(data: {peerId: any, room: string}) {
    return await this.socket.emit('peerId', data)
  }

  async askModelPeerId(data: {peerId: any, room: string, clientId: number}) {
    return await this.socket.emit('ask peerId', data)
  }

  async ansModelPeerId(data: {peerId: any, room: string, clientId: number}) {
    console.log("Answer ", data);
    return await this.socket.emit('ans peerId', data)
  }

  async newPeerIdModel(data: {role: string, modelId: number, room: string, peerId: string}) {
    return await this.socket.emit('new model peerId', data)
  }

  async clientInviteModelToPrivate(roomId: number, clientId: number, clientPseudo: string, modelId: number) {
    const data = {
      roomId,
      clientId,
      clientPseudo,
      modelId
    }
    return await this.socket.emit('invite model to private', data)
  }

  async responseInvitationModelToPrivate(roomId: number, clientId: number, modelId: number, modelPseudo: string) {
    const data = {
      roomId,
      clientId,
      modelId,
      modelPseudo
    }
    return await this.socket.emit('response invitation to private', data)
  }

  async clientInviteModelToVIP(roomId: any, clientId: number, clientPseudo: string, modelId: number, special?: string) {
    const data = {
      roomId,
      clientId,
      clientPseudo,
      modelId,
      special
    }
    return await this.socket.emit('invite model to vip', data)
  }


  async responsePositiveInvitationModelToVIP(roomId: any, clientId: number, modelId: number, modelPseudo: string, special?: string) {
    const data = {
      roomId,
      clientId,
      modelId,
      modelPseudo,
      special
    }
    return await this.socket.emit('response positive invitation model to vip', data)
  }

  async responseNegativeInvitationModelToVIP(roomId: any, clientId: number, modelId: number, modelPseudo: string, special?: string) {
    const data = {
      roomId,
      clientId,
      modelId,
      modelPseudo,
      special
    }
    return await this.socket.emit('response negative invitation model to vip', data)
  }

  // Special audio & video
  async toggleAudio(roomId: any, clientId: number, peerId: any, modelId: number, isAudio: boolean) {
    const data = {
      roomId,
      clientId,
      peerId,
      modelId,
      isAudio
    }
    return await this.socket.emit('Toggle audio', data)
  }

  async toggleVideo(roomId: any, clientId: number, peerId: any, modelId: number, isVideo: boolean) {
    const data = {
      roomId,
      clientId,
      peerId,
      modelId,
      isVideo
    }
    return await this.socket.emit('Toggle video', data)
  }


  // Special stream
  async askModelStream(roomId: any, clientId: number, modelId: number) {
    const data = {
      roomId,
      clientId,
      modelId
    }
    return await this.socket.emit('Ask current model stream', data)
  }

  async answerModelStream(roomId: any, clientId: number, modelId: number, stream: MediaStream) {
    const data = {
      roomId,
      clientId,
      modelId,
      stream
    }
    console.log('Stream in socket service ', data.stream)
    return await this.socket.emit('Answer current model stream', data)
  }

  async banishClient(roomId: any, clientId: number) {
    const data = {
      roomId,
      clientId,
    }
    return await this.socket.emit('Banish client', data)
  }

  // Special Tips
  async updatePalier(roomId: any, mini: number, bronze: number, argent: number, or: number) {
    const data = {
      roomId,
      mini,
      bronze,
      argent,
      or
    }
    return await this.socket.emit('updatePalier', data)
  }

  // Special Live Choice Us
    async updateChoiceUs(roomId: any, title: string, description: string, tarif: number) {
      const data = {
        roomId,
        title,
        description,
        tarif
      }
      return await this.socket.emit('updateChoiceUs', data)
    }

  async newTips(roomId: any, symbole: string, credit: number, designation: string, vip: boolean = false) {
    const data = {
      roomId,
      symbole,
      credit,
      designation,
      vip
    }
    return await this.socket.emit('new tips', data);
  }

}
