import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmLogoutComponent } from 'src/app/modals/confirm-logout/confirm-logout/confirm-logout.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-leftbar-admin',
  templateUrl: './leftbar-admin.component.html',
  styleUrls: ['./leftbar-admin.component.scss']
})
export class LeftbarAdminComponent implements OnInit {
  icon = './../../../../../assets/icons/paper-clip.svg'
  logout = './../../../../../assets/icons/admin-icons/logout.png'
  dashboard = './../../../../../assets/icons/admin-icons/dashboard.png'
  toggledMenu: boolean = false;
  menu = [
    {
      title: 'client',
      under : [
        {
          label : 'Liste des clients',
          icon : './../../../../../assets/icons/admin-icons/list.png',
          route : '/les-clients'
        },
        {
          label : 'Clients bloqus',
          icon : './../../../../../assets/icons/admin-icons/user_block.png',
          route : '/les-clients-bloques'
        },
        // {
        //   label : 'Demandes',
        //   icon : './../../../../../assets/icons/admin-icons/ask2.png',
        //   route : '/les-demandes-clients'
        // },
        {
          label : 'Statistiques',
          icon : './../../../../../assets/icons/admin-icons/statistique.png',
          route : '/clients-statistiques'
        }
      ]
    },
    {
      title: 'modles',
      under : [
        {
          label : 'Liste des modles',
          icon : './../../../../../assets/icons/admin-icons/list.png',
          route : '/les-modeles'
        },
        {
          label : 'Modles bloqus',
          icon : './../../../../../assets/icons/admin-icons/user_block.png',
          route : '/les-modeles-bloques'
        },
        {
          label : 'Demandes',
          icon : './../../../../../assets/icons/admin-icons/ask2.png',
          route : '/les-demandes-modeles'
        },
        {
          label : 'Suivi',
          icon : './../../../../../assets/icons/admin-icons/payement.png',
          route : '/suivi-modeles'
        },
        {
          label: 'Commissions',
          icon : './../../../../../assets/icons/admin-icons/help-center.png',
          route: '/commissions-model'
        },
        {
          label : 'Statistiques',
          icon : './../../../../../assets/icons/admin-icons/statistique.png',
          route : '/modeles-statistiques'
        }
      ]
    },
    {
      title: 'administration',
      under : [
        {
          label : 'Paramtres',
          icon : './../../../../../assets/icons/admin-icons/settings.png',
          route : '/setting-admin'
        },
        {
          label : 'Message',
          icon : './../../../../../assets/icons/admin-icons/messenger.png',
          route : '/message-admin'
        },
        {
          label : 'Payements',
          icon : './../../../../../assets/icons/admin-icons/payement.png',
          route : '/list-paiement'
        }
      ]
    }
  ]


  constructor(
    private router: Router,
    private dialog: MatDialog
    ) { }

  ngOnInit(): void {
  }

  navigate(route) {
    this.router.navigate(['/admin' + route]);
  }

  out() {
    const dialogRef = this.dialog.open(ConfirmLogoutComponent, {
      width: '50vh',
      data: {action: 'admin'}
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

  toggleMenu() {
    this.toggledMenu = !this.toggledMenu;
    if (this.toggledMenu) $('.main_leftbar').show();
    else $('.main_leftbar').hide();
  }

}
