import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { TimerService } from '../../../../../../services/timer/timer.service';

@Component({
  selector: 'app-main-right-dashboard-admin',
  templateUrl: './main-right-dashboard-admin.component.html',
  styleUrls: ['./main-right-dashboard-admin.component.scss']
})
export class MainRightDashboardAdminComponent implements OnInit {
  years : number[] = []

  options: any;
  updating: boolean = false;

  currentSelect = 'client'

  clients = []

  clientsOrig = []

  modeles = []

  modelesOrig = []

  isIntegral: boolean = false
  currentYear: number = new Date().getFullYear()
  currentMonth: string = '01'

  labelsShow = [
    {
      title: '10 DERNIERS SHOW',
      label : [
        'Modle', 'Type', 'Dure',
        'Vues', 'Gains', 'Etat'
      ]
    },
    {
      title: 'TOP 10 MODELES',
      label : [
        'Modle', 'Show cre', 'Dure moyenne',
        'Gains', 'Rang'
      ]
    },
    {
      title: 'TOP 3 DES CLIENTS LES PLUS ACTIFS',
      label : [
        'Pseudo', 'Show regard', 'Dure moyenne',
        'Rang'
      ]
    }
  ]

  valueUser = []

  labelUser = this.labelsShow[2]

  constructor(
    private adminService: AdminService,
    private timerService: TimerService
  ) {
    this.options = {
      autoSize: true,
      data: this.getData(),
      series: [
        {
          xKey: 'date',
          yKey: 'count',
          yName: 'Nombre',
          stroke: '#FFF',
          marker: {
            size: 2,
            fill: '#FFF',
            stroke: '#FFF',
          },
        }
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          nice: false,
          tick: { count: 0,color:  "rgb(0, 0, 0)"},
          label: { format: 'hello', color:  "rgb(255, 255, 255)" },
        },
        {
          type: 'number',
          position: 'left',
          tick: { count: 1,color:  "rgb(0, 0, 0)"},
          label: { format: '#{.0f}', color:  "#fff"}
        }
      ],
      background: {
        fill: "#FFF",
        visible: false
      },
      tooltip: {
        enabled: false
      },
      navigator: {
        enabled: true
      },
      title: { text: '' },
      legend: { enabled: false },
    };
   }

  ngOnInit(): void {
    this.startUpdates();
    this.useDefaultGridStyle();
    this.initYears();
    this.getInscriptionsClient()
    this.getInscriptionsModel()
    this.getAverageClient()
  }

  initYears() {
    const initial = 2021;
    for(let i = 0; i < 100; i++ ) {
      this.years.push(initial+i)
    }
  }

  useDefaultGridStyle = () => {
    const options = cloneDeep(this.options);

    var gridStyle = [
      {
        stroke: 'white',
        lineDash: [2, 1],
      },
    ];
    options.axes[0].gridStyle = gridStyle;
    options.axes[1].gridStyle = gridStyle;

    this.options = options;
  };

  update = () => {
    const options = cloneDeep(this.options);

    options.data = this.getData();

    this.options = options;
  };

  startUpdates = () => {
    if (this.updating) {
      return;
    }
    this.updating = true;
    this.update();
    setInterval(this.update, 1000);
  };

  getData() {
    this.updating = false;
    if (this.currentSelect === 'client') return this.clients
    if (this.currentSelect === 'model') return this.modeles
    return this.clients;
  }

  changeSelection(text) {
    this.currentSelect = text
    this.update()
  }

  getInscriptionsModel() {
    this.adminService.getStatInscriptionModel().subscribe(
      (data: any) => {
        let tmp = data
        tmp.forEach(inscrit => {
          inscrit.date = this.adminService.formatDate(inscrit.date)
          inscrit.count = parseInt(inscrit.count)
        })
        this.modelesOrig = tmp
        this.modeles = tmp
      }, (error) => {
        console.log(error)
      }
    )
  }

  getInscriptionsClient() {
    this.adminService.getStatInscriptionClient().subscribe(
      (data: any) => {
        // console.log(data)
        let tmp = data
        tmp.forEach(inscrit => {
          inscrit.date = this.adminService.formatDate(inscrit.date)
          inscrit.count = parseInt(inscrit.count)
        })
        this.clientsOrig = tmp
        this.clients = tmp
        // console.log(this.clients)
      }, (error) => {
        console.log(error)
      }
    )
  }

  selectMonth(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentMonth = value
    const search = this.currentMonth+'/'+this.currentYear
    if (this.currentSelect === 'client') {
      this.clients =  this.clientsOrig.filter(element => {
        const year = element.date.substring(3, 10)
        if (year === search) return true
        return false
      })
    } else if (this.currentSelect === 'model') {
      this.modeles =  this.modelesOrig.filter(element => {
        const year = element.date.substring(3, 10)
        if (year === search) return true
        return false
      })
    }

  }

  selectYear(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentYear = value
    if (this.currentSelect === 'client') {
      this.clients =  this.clientsOrig.filter(element => {
        const year = element.date.substring(6, 10)
        if (year === this.currentYear) return true
        return false
      })
    } else if (this.currentSelect === 'model') {
      this.modeles =  this.modelesOrig.filter(element => {
        const year = element.date.substring(6, 10)
        if (year === this.currentYear) return true
        return false
      })
    }

  }

  integral() {
    this.isIntegral = !this.isIntegral
    if (this.isIntegral) {
      if (this.currentSelect === 'client') {
        this.clients = this.clientsOrig
      } else if (this.currentSelect === 'model') {
        this.modeles = this.modelesOrig
      }

    } else {
        const search = this.currentMonth+'/'+this.currentYear
        if (this.currentSelect === 'client') {
          this.clients =  this.clientsOrig.filter(element => {
            const year = element.date.substring(3, 10)
            if (year === search) return true
            return false
          })
        } else if (this.currentSelect === 'model') {
          this.modeles =  this.modeles.filter(element => {
            const year = element.date.substring(3, 10)
            if (year === search) return true
            return false
          })
        }
    }
  }

  getAverageClient() {
    this.adminService.getAverageClient().subscribe(
      (data: any[]) => {
        this.valueUser = [];
        let tmp: any[] = data;
        tmp.forEach(val => {
          val.duree = this.timerService.convertToTime(val.duree);
          // console.log(val.duree)
        })
        let countLoop = 0;
        // console.log(' Get average client ', tmp);
        for (let i = tmp.length; i > tmp.length - 3; i--) {
          const index = i - 1;
          if (!tmp[index]) continue;
          tmp[index].rang = countLoop + 1;
          this.valueUser.push(tmp[index]);
          countLoop++;
        }
      }, (error) => {
        console.log(error)
      }
    )
  }
}


