import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef } from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Chat, ChatSocket, ClientCam } from 'src/app/interfaces/chat.interface';
import { ChatService } from 'src/app/services/chat/chat.service';
import { ModelService } from 'src/app/services/model/model.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { RoomPrivateService } from '../../../services/roomPrivate/room-private.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { CreditService } from '../../../services/credit/credit.service';
import { TimerService } from '../../../services/timer/timer.service';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import * as $ from 'jquery';
import * as AOS from 'aos';

// For Peer
import './../shim';
import Peer from "peerjs"
import { Subscription, Observable } from 'rxjs';
import { RoomTipsService } from '../../../services/roomTips/room-tips.service';

declare var initSettingVideoModel: any;


interface Stream {
  clientId: number,
  stream: MediaStream,
  peerId: any,
  isAudio: boolean,
  isVideo: boolean
}


@Component({
  selector: 'app-live-tips-clients-model',
  templateUrl: './live-tips-clients-model.component.html',
  styleUrls: ['./live-tips-clients-model.component.scss', './../../live/live.scss']
})
export class LiveTipsClientsModelComponent implements OnInit {
  context: string = "LIVE TIPS";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/live-detail.png";
  close = "assets/icons/close.svg";

  toggleDot: boolean = false;
  isToggledTool: boolean = false;

  info = {
    bg : "assets/logos/dark-light.jpg",
    message : '',
    idRoom: 0,
    pseudo: '',
    chatColor:'#FB77FC',
    modelId: 0,
    actif: 0,
    creditModel: 0,
    idCreditModel: 0,
    gain: 0,
    tips: [],
    descriptions: []
  }

  timer = {
    id: 0,
    hour: 0,
    minute: 0,
    second: 0,
    fail: false,
    reinit: false,
    timer: null
  };

  messages: Chat[] = [];
  leaved: boolean = false;

  clientId: number = null;

  clients: ClientCam[] = [];

  clientStream: Stream[] = [];

  joinSub = new Subscription();
  leaveSub = new Subscription();
  messageSub = new Subscription();
  peerSub = new Subscription();
  askModelStreamSub = new Subscription();
  toggleAudioSub = new Subscription();
  toggleVideoSub = new Subscription();
  newTipsSub = new Subscription();

  stat = {
    id_show: 0,
    type_show : null,
    tarif_show: 0,
    time_show: 0,
    time_passed : this.timer.hour + ':' + this.timer.minute + ':' + this.timer.second,
    hour_passed : null,
    minute_passed : null,
    second_passed : null,
    actualCredit: this.info.creditModel,
    winCredit: 0,
    totalCredit: this.info.creditModel
  }

  statVIP = {
    tarif_show: 0,
    time_show: 0
  }

  loading: boolean = false;

  showVideo: boolean = true;
  showAudio: boolean = true;

  showEmojiPicker = false;
  sets = [
    'native',
    'google',
    'twitter',
    'facebook',
    'emojione',
    'apple',
    'messenger'
  ]
  set = 'google';

  roomId = null;

  sound_notification: boolean;
  sound_message: boolean;


  // WEB RTC VIDEO
  @ViewChild('video_live_model', {static: true}) video: ElementRef<HTMLVideoElement>;
  private peer: Peer;
  peerIdShare: string;
  peerId: string;
  private lazyStream: MediaStream;
  currentPeer: any;
  private peerList: Array<any> = [];

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private store: LocalStorageService,
    private modelService: ModelService,
    private chatService: ChatService,
    private socketService: SocketService,
    private profilService: ProfilService,
    private popupService: PopupService,
    private creditService: CreditService,
    private timerService: TimerService,
    private profileService: ProfilService,
    private roomTipsService: RoomTipsService) {
      this.peer = new Peer();
  }

  ngOnInit(): void {
    AOS.init();
    this.verifySound();
    this.getInfo();
    this.initLiveVideo();
    $(document).ready(
      function() {
        initSettingVideoModel();
      }
    );
  }

  toggleTool(value) {
    this.isToggledTool = value;
  }

  verifySound() {
    this.profileService.getInfo().subscribe(
      (data: any) => {
        // console.log(data.setting);
        this.sound_notification = data.setting.sound_notification === 1 ? true : false;
        this.sound_message = data.setting.sound_message === 1 ? true : false;
      }
    )
  }

  ngOnDestroy(): void {
    clearInterval(this.timer.timer);
    this.onStop();
    this.joinSub.unsubscribe();
    this.leaveSub.unsubscribe();
    this.messageSub.unsubscribe();
    this.peerSub.unsubscribe();
    this.askModelStreamSub.unsubscribe();
    this.toggleAudioSub.unsubscribe();
    this.toggleVideoSub.unsubscribe();
    this.newTipsSub.unsubscribe();
  }

  getInfo() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log('Get info ', data);
        if (data.profile.status === 'En vip') {
          this.onStop();
          this.router.navigate(['/modele/live-vip']);
        }
        if (data.profile.status === 'En ligne') {
          this.onStop();
          this.router.navigate(['/modele/chat']);
        }
        if (data.profile.status === 'En live choice') {
          this.onStop();
          this.router.navigate(['/modele/live-choice-us']);
        }
        this.info.bg = data.path_soft;
        this.info.modelId = data.id;
        this.info.pseudo = data.pseudo;
        this.getInfoRoom();
      }
    )
  }

  getInfoRoom() {
    this.roomTipsService.getRoomModel(this.info.modelId).subscribe(
      async (data) => {
        // console.log('Model room ', data);
        if (data.status !== 'open') {
          this.popupService.info(null, 'LIVE FERME', 'Ce room est ferm');
          return null;
        }
        this.info.idRoom = data.id;
        this.roomId = data.id+'T';
        this.info.gain = data.gain;
        this.info.tips = data.tips.split("~").map(Number);
        this.info.descriptions = data.descriptions.split("~");
        this.getCredit();
        this.initSocket();
        this.getActifRoom();
      }
    )
  }

  async getActifRoom() {
    this.roomTipsService.getActifs(this.info.idRoom).subscribe(
      (data: any[]) => {
        // console.log('Actifs ', data);
        this.info.actif = data ? data.length : 0;
      }
    )
  }

  async getStatRoom(idRoom: number) {
    if (!idRoom || idRoom === 0) return null;
    this.roomTipsService.getStatRoom(this.info.idRoom).subscribe(
      (data: {actif: number, gain: number}) => {
        if (!data) return null;
        this.info.gain = data.gain;
      }
    )
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data) => {
        this.info.creditModel = data ? data.credit : 0;
        this.info.idCreditModel = data ? data.id : 0;
      }
    );

    // Launch the timer
    this.initTimer();
  }

  initSocket() {

    this.joinSub = this.socketService.listen(`joined ${this.info.idRoom}T`).subscribe(
      (data: {actif: number, clientId: number, clientPseudo: string}) => {
        // console.log('joined ', data);
        this.getActifRoom();
        if (this.sound_notification) {
          this.socketService.soundIncome();
        }
    }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.info.idRoom}T`).subscribe(
      (data: {clientId: number, clientPseudo: string}) => {
        this.getActifRoom();
        if (this.sound_notification) {
          this.socketService.soundOutcome();
        }
        // console.log('leaved ', data);
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.info.idRoom}T`).subscribe(
      (data: Chat) => {
        // this.socketService.soundOutcome();
        this.messages.push(data);
        // this.getMessages();
      }
    );

    this.newTipsSub = this.socketService.listen(`new tips ${this.info.idRoom}T`).subscribe(
      (data: {symbole: string, credit: number, designation: string, vip: boolean}) => {
        // console.log('new tips ', data);
        if (data.credit) {
          this.info.creditModel += data.credit;
        }
        this.getStatRoom(this.info.idRoom);
        this.socketService.soundIncome();
    });

    this.peerSub = this.socketService.listen(`ask peerId ${this.info.idRoom}T`).subscribe(
      async (data: {room: string, clientId: number, clientPseudo: string}) => {
        console.log('Ask peer Id ', data);
        if (!this.peerId) await this.getPeerId();
        this.socketService.ansModelPeerId({
          peerId: this.peerId, room: this.info.idRoom+'T', clientId: data.clientId
        });
      }
    );


    this.toggleAudioSub = this.socketService.listen(`Toggle audio ${this.info.idRoom}T`).subscribe(
      (data: {roomId: any, clientId: number, peerId: any, modelId: number, isAudio: boolean}) => {
          if (data.clientId) {
            console.log('Toggle audio from ', data.clientId);
            this.toggleAudioClientStream(data.peerId, data.isAudio)
          }
      }
    );

    this.toggleVideoSub = this.socketService.listen(`Toggle video ${this.info.idRoom}T`).subscribe(
      (data: {roomId: any, clientId: number, peerId: any, modelId: number, isVideo: boolean}) => {
          if (data.clientId) {
            this.toggleVideoClientStream(data.peerId, data.isVideo)
          }
      }
    );

    this.getMessages();
  }


  getMessages() {
    this.chatService.getMessage(this.info.idRoom, 'tips').subscribe(
      (data) => {
        // console.log('Messages ', data);
        this.messages = data;
      }
    )
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    // console.log(this.toggleDot);
  }

  sendMessage() {
    if (!this.info.message) return null;
    const data: Chat = {
      idRoom: this.info.idRoom,
      message: this.info.message,
      type_source: 'model',
      id_source: this.info.modelId,
      type_chat: 'tips',
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo
    }
    const msg: ChatSocket = {
      room: this.info.idRoom + 'T',
      role: 'model',
      id: this.info.modelId,
      message: this.info.message,
      idRoom: this.info.idRoom,
      type_source: 'model',
      id_source: this.info.modelId,
      type_chat: 'tips',
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo
    }
    this.socketService.sendMessage(msg);
    this.chatService.postMessage(data).subscribe(
      (data) => {
        // this.getMessages();
        this.info.message = null;
      }
    )
  }


  async liveOut() {
    this.loading = true;
    // const room = this.store.get('privateRoomId');
    this.onStop();
    this.leaved = true;
    await (await this.leaveTimer()).subscribe(
      (data) => {
        this.timer.reinit = true;
        this.clearTimer(); // set to zero
        this.socketService.leaveTips(this.info.idRoom, 'model', this.info.modelId);
        // this.roomPrivateService.leaveRoom();
        this.profilService.updateStatus(this.info.modelId, 'En ligne').subscribe(
          (data) => {
            this.loading = false;
            window.location.href = '/modele/chat';
            // this.router.navigate(['/modele/chat']);
            // setTimeout(
            //   () => {
            //     window.location.reload();
            //   }, 200
            // );
          }
        )
      }, (error) => {
        this.loading = false;
      }
    );
  }

  // Timer
  async initTimer() {
      this.timerService.beginTimerModel(this.info.modelId, TypeTimer.TIPS).subscribe(
        async (data) => {
          // console.log('Timer ', data);
          const created: Date = data.createdAt;
          const updated: Date = data.updatedAt;
          this.timer.id = data.id;
          // console.log(created.toString(), " to ", updated.toString());
          const {hour, minute, second} = this.timerService.convertTime(created.toString(), updated.toString());
          this.timer.hour = hour;
          this.timer.minute = minute;
          this.timer.second = second;
          // console.log(this.timer.hour, ':', this.timer.minute, ':', this.timer.second);

        }
      )
  }

  beginCountCredit(nbSecond: number) {
    const delay = nbSecond * 1000;
    this.timer.timer = setInterval(
      () => {
        if (this.info.actif > 0) {
          this.creditService.getCredit().subscribe(
            (data) => {
              this.info.creditModel = data ? data.credit : 0;
            }
          );
        }
      }, delay
    )
  }

  // Timer set to 0
  clearTimer() {
    this.timer.reinit = true;
  }

  async leaveTimer(): Promise<Observable<any>> {
    const data = {
      id: this.timer.id,
      type: TypeTimer.TIPS,
      leaved: true,
      push: new Date().toString()
    }

    return await this.timerService.updateTimer(data);
  }

    // WEB RTC - LIVE VIDEO
    initLiveVideo() {
      this.onStart();
      this.getPeerId();
    }

    onStart(){
      if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
        navigator.mediaDevices.getUserMedia({video: true, audio: true}).then((ms: MediaStream) => {
          this.lazyStream = ms;
          this.video.nativeElement.srcObject = this.lazyStream;
          $('#video_live_model').prop('volume', 0)
        });
      }
    }

    onStop() {
      if (this.video && this.video.nativeElement.srcObject) {
        this.video.nativeElement.pause();
        if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
          (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
        }
        this.video.nativeElement.srcObject = null;
      }
      if (this.lazyStream) {
        this.lazyStream.getTracks().forEach(
          (track) => {
            track.stop();
          }
        );
      }
      this.lazyStream = null;
    }

  private getPeerId = () => {
    this.peer.on('open', (id) => {
      console.log("Peer Id ", id);
      this.peerId = id;
      const data = {
        role: 'model',
        modelId: this.info.modelId,
        room : this.info.idRoom+'T',
        peerId: this.peerId
      }
      this.socketService.newPeerIdModel(data);
    });

    this.peer.on('call', (call) => {
      console.log("Someone call model");
      call.answer(this.lazyStream);
      console.log("After answer stream");
      call.on('stream', (remoteStream) => {
        console.log("On receive stream in model");
        if (!this.peerList.includes(call.peer)) {
          this.streamRemoteVideo(remoteStream, call.peer);
          this.currentPeer = call.peerConnection;
          this.peerList.push(call.peer);
        }
        console.log("Peer list ", this.peerList);
      });
    });
  }

  connectWithPeer(): void {
    this.callPeer(this.peerIdShare);
  }

  private callPeer(id: string): void {
    const call = this.peer.call(id, this.lazyStream);
    call.on('stream', (remoteStream) => {
      if (!this.peerList.includes(call.peer)) {
        this.streamRemoteVideo(remoteStream, call.peer);
        this.currentPeer = call.peerConnection;
        this.peerList.push(call.peer);
      }
    });
  }

  private callClient(clientId: number, peerId: any): void {
    const call = this.peer.call(peerId, this.lazyStream);
    call.on('stream', (remoteStream) => {
      if (!this.peerList.includes(call.peer)) {
        this.streamRemoteVideo(remoteStream, call.peer, clientId);
        this.currentPeer = call.peerConnection;
        this.peerList.push(call.peer);
      }
    });
  }

  removeStream(clientId: number) {
    // console.log('Remove stream ', clientId)
    const index = this.clientStream.findIndex(element => element.clientId === clientId)
    // console.log(index)
    // console.log(this.clientStream)
    this.clientStream.splice(index, 1)
  }

  private streamRemoteVideo(stream: any, peerId: any, clientId?: number): void {
    console.log('Add remote stream client ', stream);
    // const video = document.createElement('video');
    // video.classList.add('video');
    // video.srcObject = stream;
    // video.play();

    // document.getElementById('remote-video').append(video);
    // this.clients[0].client.stream = stream;
    let id = null;
    if (!clientId) {
      id = this.clientStream.length;
    } else {
      id = clientId;
    }

    if (this.clientStream.length < 5) {
      this.clientStream.push({
        clientId: id,
        stream: stream,
        peerId,
        isAudio: true,
        isVideo: true
      });
    }

  }

  toggleAudioClientStream(peerId: any, isAudio: boolean) {
    this.clientStream.forEach(
      (stream) => {
        if (stream.peerId === peerId) {
          stream.isAudio = isAudio
        }
      }
    )
  }

  toggleVideoClientStream(peerId: any, isVideo: boolean) {
    this.clientStream.forEach(
      (stream) => {
        if (stream.peerId === peerId) {
          stream.isVideo = isVideo
        }
      }
    )
  }

  toggleVideo() {
    this.showVideo = !this.showVideo;
    if (this.showVideo) {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = true;
      });
    } else {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = false;
      });
    }
    // this.onStop();
    // this.onStart();
    const room = this.info.idRoom+'T';
    this.socketService.toggleVideo(room, null, this.peerId, this.info.modelId, this.showVideo);
  }

  toggleAudio() {
    this.showAudio = !this.showAudio;
    // this.onStop();
    // this.onStart();
    if (!this.showAudio) {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    } else {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    }
    const room = this.info.idRoom+'T';
    this.socketService.toggleAudio(room, null, this.peerId, this.info.modelId, this.showAudio);
  }

    // -------- EMOJI -----------------

    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    }

    addEmoji(event) {
      const {info} = this;
      // console.log(this.info.message);
      // console.log(`${event.emoji.native}`);
      let text = '';
      if (this.info.message) {
        text = `${info.message}${event.emoji.native}`;
      } else {
        text = `${event.emoji.native}`;
      }
      this.info.message = text;
      // console.log(this.info.message)
      // this.showEmojiPicker = false;
    }

    focus() {
      this.showEmojiPicker = false;
    }


}
