import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ClientService } from '../../../services/client/client.service';
import { PopupService } from '../../../services/popup/popup.service';
import { NotificationService } from '../../../services/notification/notification.service';

interface Message {
  email: string,
  object: string,
  message: string
}

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  loginForm : FormGroup;
  submitted :boolean =  false;
  loading: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private clientService: ClientService,
    private popupService: PopupService,
    private notificationService: NotificationService
  ) {
    this.loginForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      object: ['', Validators.required],
      message: ['', Validators.required],
    })
  }

  ngOnInit(): void {
  }

  get form() { return this.loginForm.controls }

  async send(event) {
    this.submitted = true;
    event.preventDefault();
    if (this.loginForm.invalid) {return false;}
    this.loading = true;
    let message: Message = this.loginForm.value;
    console.log(message);
    this.clientService.sendMessage(message).subscribe(
      (data: any) => {
        this.submitted = false;
        console.log(data);
        this.popupService.info(null, 'Accus de rception', 'Votre message a t bien envoy !');
        this.loginForm.reset();
        this.loading = false;
      }, (error) => {
        console.log(error);
        this.notificationService.errorMsg('Une erreur s\'est produite');
        this.loading = false;
      }
    )
  }
}
