        // -------- EMOJI -----------------

        toggleEmojiPicker() {
          this.showEmojiPicker = !this.showEmojiPicker;
        }

        addEmoji(event) {
          const {message} = this;
          let text = '';
          if (message) {
            text = `${message}${event.emoji.native}`;
          } else {
            text = `${event.emoji.native}`;
          }
          this.message = text;
        }

        focus() {
          this.showEmojiPicker = false;
        }

              // Tips

      selectTips(value: TIP) {
        // console.log('Tips ', value);
        this.loading = true;
        if (value.credit > this.clientCredit) {
          this.notificationService.infoMsg('Votre crdit est insuffisant');
          this.loading = false;
          return null;
        }
        this.creditService.buyGift(this.clientId, this.modelId, value.credit).
        subscribe(
          (data: any) => {
            // console.log(data);
            this.clientCredit = data.creditClient;
            this.sendTips(value.symbole, value.credit, value.designation, value.vip);
          }, (error) => {
            console.log(error);
          }
        )

      }

      toggleTips() {
        this.showTips = !this.showTips;
      }

      sendTips(symbole: string, credit: number, designation, vip: boolean = false) {
        const data: Chat = {
          idRoom: this.idRoom,
          message: vip ? 'vip' : 'This is a gift',
          type_source: 'client',
          id_source: this.clientId,
          code_couleur: this.chatColor,
          type_chat: 'tips',
          giftSymbol: symbole,
          giftDesign: designation,
          giftSource: this.clientPseudo,
          pseudo: this.clientPseudo
        }
        const msg: ChatSocket = {
          room: this.idRoom + 'T',
          role: 'client',
          id: this.clientId,
          message: vip ? 'vip' : 'This is a gift',
          pseudo: this.clientPseudo,
          idRoom: this.idRoom,
          type_source: 'client',
          id_source: this.clientId,
          code_couleur: this.chatColor,
          type_chat: 'tips',
          giftSymbol: symbole,
          giftDesign: designation,
          giftSource: this.clientPseudo
        }
        this.socketService.sendMessage(msg);
        this.chatService.postMessage(data).subscribe(
          (data) => {
            // this.getMessages();
            this.loading = false;
            this.showTips = false;
          }
        )
      }

      buyTips(value: TIP) {
        this.loading = true;
        if (value.credit > this.clientCredit) {
          this.notificationService.infoMsg('Votre crdit est insuffisant');
          this.loading = false;
          return null;
        }
        this.creditService.buyGift(this.clientId, this.modelId, value.credit).
        subscribe(
        (data: any) => {
            this.clientCredit = data.creditClient;
            this.roomTipsService.updateGain(this.idRoom, value.credit).subscribe(
              (data) => {
                // notify send and animation
                this.socketService.newTips(this.idRoom+'T', value.symbole, value.credit, value.designation, value.vip);
                this.sendTips(value.symbole, value.credit, value.designation, value.vip);
                this.notificationService.successMsg(`Votre ${value.symbole} est envoy(e)`);
              }
            )
        }, (error) => {
            console.log(error);
        }
      )
    }

