import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { ThumbnailsPosition } from 'ng-gallery';

@Component({
  selector: 'app-statistique-models-admin',
  templateUrl: './statistique-models-admin.component.html',
  styleUrls: ['./statistique-models-admin.component.scss']
})
export class StatistiqueModelsAdminComponent implements OnInit {
  years : number[] = []

  actif: number = 0

  options: any;
  updating: boolean = false;
  models = []
  original = []
  originModels = []
  currentYear: number = new Date().getFullYear()
  currentMonth: string = '01'

  isIntegral: boolean = false


  constructor(
    private adminService: AdminService
  ) {
    this.options = {
      autoSize: true,
      data: this.getData(),
      series: [
        {
          xKey: 'date',
          yKey: 'inscription',
          yName: 'Inscription',
          stroke: '#FB77FC',
          marker: {
            size: 2,
            fill: '#FB77FC',
            stroke: '#FB77FC',
          },
        },
        {
          xKey: 'date',
          yKey: 'suppression',
          yName: 'Suppression',
          stroke: '#4E009F',
          marker: {
            size: 2,
            fill: '#4E009F',
            stroke: '#4E009F',
            color: '#fff'
          },
        }
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          nice: false,
          // tick: { count: agCharts.time.second.every(3), color:  "rgb(0, 0, 0)"},
          tick: { count: 0,color:  "rgb(0, 0, 0)"},
          label: { format: '%m/%d/%Y', color:  "rgb(0, 0, 0)" },
        },
        {
          type: 'number',
          position: 'left',
          tick: { count: 1,color:  "rgb(0, 0, 0)"},
          label: { format: '#{.0f}', color:  "#000"}
        }
      ],
      background: {
        fill: "#ffffff5f",
        visible: true
      },
      tooltip: {
        enabled: true
      },
      navigator: {
        enabled: true
      },
      title: { text: '' },
      legend: {
        position: 'bottom',
        enabled: true,
        item: {
          label: {
            color: 'black'
          }
        }
      }
    };
  }

  ngOnInit(): void {
    this.initYears();
    this.startUpdates();
    this.useDefaultGridStyle();
    this.countModelActif()
    this.getInscriptions()
  }

  countModelActif() {
    this.adminService.countModelActif().subscribe(
      (data: number) => {
        this.actif = data ? data : 0
      }, (error) => {
        console.log(error)
      }
    )
  }

  initYears() {
    const initial = 2021;
    for(let i = 0; i < 50; i++ ) {
      this.years.push(initial+i)
    }
  }

  useDefaultGridStyle = () => {
    const options = cloneDeep(this.options);

    var gridStyle = [
      {
        stroke: 'white',
        lineDash: [2, 1],
      },
    ];
    options.axes[0].gridStyle = gridStyle;
    options.axes[1].gridStyle = gridStyle;

    this.options = options;
  };

  update = () => {
    const options = cloneDeep(this.options);

    options.data = this.getData();

    this.options = options;
  };

  startUpdates = () => {
    if (this.updating) {
      return;
    }
    this.updating = true;
    this.update();
    setInterval(this.update, 1000);
  };

  getData() {
    this.updating = false;
    return this.models;
  }

  getInscriptions() {
    this.originModels = []
    this.adminService.getStatInscriptionModel().subscribe(
      (data: any) => {
        let tmp = data
        tmp.forEach(inscrit => {
          inscrit.date = this.adminService.formatDate(inscrit.date)
          inscrit.count = parseInt(inscrit.count)
          inscrit.inscription = parseInt(inscrit.count)
          inscrit.suppression = 0
        })
        this.originModels = tmp
        this.getSuppression()
      }, (error) => {
        console.log(error)
      }
    )
  }

  getSuppression() {
    this.adminService.getStatSuppressionModel().subscribe(
      (data: any) => {
        let tmp = data
        tmp.forEach(inscrit => {
          inscrit.date = this.adminService.formatDate(inscrit.date)
          inscrit.count = parseInt(inscrit.count)
          inscrit.inscription = 0
          inscrit.suppression = parseInt(inscrit.count)
          this.originModels.push(inscrit)
        })
        // console.log(this.originModels)
        this.resume()
      }, (error) => {
        console.log(error)
      }
    )
  }

  selectMonth(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentMonth = value
    const search = this.currentMonth+'/'+this.currentYear
    this.models =  this.original.filter(element => {
      const year = element.date.substring(3, 10)
      if (year === search) return true
      return false
    })
  }

  selectYear(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentYear = value
    this.models =  this.original.filter(element => {
      const year = element.date.substring(6, 10)
      if (year === this.currentYear) return true
      return false
    })
  }

  integral() {
    this.isIntegral = !this.isIntegral
    if (this.isIntegral) {
        this.models = this.original
    } else {
        const search = this.currentMonth+'/'+this.currentYear
        this.models =  this.original.filter(element => {
          const year = element.date.substring(3, 10)
          if (year === search) return true
          return false
      })
    }
  }

  resume() {
    let find = false
    this.models = []
    this.originModels.forEach(
      element => {
        find = false
        for (let i = 0; i < this.models.length; i++) {
          if (this.models[i].date === element.date) {
            this.models[i].inscription += element.inscription
            this.models[i].suppression += element.suppression
            find = true
          }
        }
        if (!find)this.models.push(element)
      }
    )
    this.original = this.models
  }
}
