import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ProfilService } from '../../services/profil/profil.service';
import { AlbumService } from '../../services/album/album.service';
import { Router } from '@angular/router';
import { PopupService } from 'src/app/services/popup/popup.service';
import { RoomTipsService } from 'src/app/services/roomTips/room-tips.service';
import { ClientService } from '../../services/client/client.service';
import * as AOS from 'aos';

@Component({
  selector: 'app-profil-model-detail',
  templateUrl: './profil-model-detail.component.html',
  styleUrls: ['./profil-model-detail.component.scss']
})
export class ProfilModelDetailComponent implements OnInit {
  modelId: number;
  modelPseudo: string;
  modelProfil: string;
  modelAge: any;

  model =  {
    pseudo: '',
    path_soft: null,
    sex_orientation: '',
    like: '',
    dislike: '',
    status: '',
    social_network: '',
    description: '',
    date_last_connection: ''
  }

  imageData: any[];

  clientId: number;
  clientPseudo: string;
  clientCredit: number;
  clientCreditId: number;

  constructor(
    public dialogRef: MatDialogRef<ProfilModelDetailComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private profilService: ProfilService,
    private albumService: AlbumService,
    private router: Router,
    private popupService: PopupService,
    private roomTipsService: RoomTipsService,
    private clientService: ClientService
  ) {}

  ngOnInit(): void {
    AOS.init();
    this.modelId = this.data.modelId;
    this.modelPseudo = this.data.modelPseudo;
    this.modelProfil = this.data.modelProfil;
    const date = new Date().getFullYear();
    this.modelAge = date - this.data.modelAge;
    this.getProfilModel();
    this.seeModel();
    this.getCreditClient();
  }

  close(): void {
    this.dialogRef.close(0);
  }

  getProfilModel() {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      (data: any) => {
        // console.log(data);
        this.model = data;
      }
    )
  }

  seeModel() {
    this.imageData = [];
    const data = {
      modelId : this.modelId,
      type_album: 'free'
    }
    this.albumService.getModelAlbums(data).subscribe(
      (data: any) => {
        // console.log(data);
        this.imageData = data;
      }
    )
  }

  launch() {
    if (this.model.status === 'En live') {
      this.navigateTo('/client/live/private', this.modelId);
      this.close();
    } else if (this.model.status === 'En tips') {
      this.liveTips();
    } else if (this.model.status === 'En live choice') {
      this.popupService.info(null, 'LIVE OCCUPE', `${this.modelPseudo} est en live CHOICE US en ce moment`);
    } else if (this.model.status === 'En vip') {
      this.popupService.info(null, 'LIVE OCCUPE', `${this.modelPseudo} est en live VIP en ce moment`);
    } else if (this.model.status === 'En ligne') {
      this.navigateTo('/client/live/free', this.modelId);
      this.close();
    } else {
      this.popupService.info(null, 'HORS LIGNE', `${this.modelPseudo} est hors ligne pour le moment`);
    }
  }

  navigateTo(url: string, modelId: number) {
    this.router.navigate([`${url}`, {model: modelId}]);
  }

  liveTips() {
    if (this.clientCredit <= 10) {
      this.popupService.info(null,'LIVE TIPS',
      `Veuillez acheter du crdit (10 crdits minimum).`);
      return false;
    }

    this.roomTipsService.debiterRoom(this.clientCreditId, 1).subscribe(
      (data) => {
        this.navigateTo('/client/live/tips-clients', this.modelId);
        this.close();
      }, (error) => {
        console.log(error);
      }
    );
  }

  getCreditClient() {
    this.clientService.getMyInfos().subscribe( // My info
      (data) => {
        // console.log("My info ", data)
        this.clientId = data.id;
        this.clientPseudo = data.pseudo;
        this.clientCredit = data.credit ? data.credit.credit : 0; // Client Credit
        this.clientCreditId = data.credit ? data.credit.id : 0; // Client Credit Id
      }
    )
  }

  goToAlbums() {
    this.close();
    this.navigateTo('/client/albums-model', this.modelId);
  }
}
