import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { NotificationService } from '../../../../../../services/notification/notification.service';

@Component({
  selector: 'app-message-admin',
  templateUrl: './message-admin.component.html',
  styleUrls: ['./message-admin.component.scss']
})
export class MessageAdminComponent implements OnInit {
  error: string = null
  messageForm : FormGroup;

  constructor(
    private adminService: AdminService,
    private notificationService: NotificationService,
    private formBuilder: FormBuilder
  ) {
    this.messageForm = this.formBuilder.group({
      email: ['', Validators.required],
      objet: ['', Validators.required],
      message: ['', Validators.required]
    })
  }

  ngOnInit(): void {
  }

  sendMail() {
    this.error = null
    if (this.messageForm.invalid) {
      this.error = 'Veuillez complter les champs'
      return false;
    }
    let message = this.messageForm.value;

      this.adminService.sendMail(message.email, message.objet, message.message).subscribe(
        (data: any) => {
          if (data.success) {
            this.notificationService.info('Message', `Envoy  ${message.email}`)
          }
        }, (error) => {
          console.log(error)
        }
      )
  }

}
