import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import * as $ from 'jquery';
import { CreditService } from '../../services/credit/credit.service';
import { PaiementService, CentralPayDto } from '../../services/paiement/paiement.service';
import { NotificationService } from '../../services/notification/notification.service';
import { SocketService } from 'src/app/services/socket/socket.service';

interface Card {
  firstName: string,
  lastName: string,
  email: string,
  card: string,
  mm: string,
  yyyy: string,
  cvv: string
}

@Component({
  selector: 'app-central-pay',
  templateUrl: './central-pay.component.html',
  styleUrls: ['./central-pay.component.scss']
})
export class CentralPayComponent implements OnInit {
  montant: number = null;
  credit: number = null;
  designation: string = null;
  creditId: number = null;
  id: number = null;
  payForm : FormGroup;
  submitted: boolean = false;
  loading: boolean = false;
  endpoint: string;
  paymentRequestId: string;


  currentCard: string = null;
  oldCount: number = 0;

  constructor(
    public dialogRef: MatDialogRef<CentralPayComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private formBuilder: FormBuilder,
    private creditService: CreditService,
    private paiementService: PaiementService,
    private notificationService: NotificationService,
    private socketService: SocketService) {
      this.payForm = this.formBuilder.group({
        firstName: ['', Validators.required],
        lastName: ['', Validators.required],
        email: ['',[Validators.required, Validators.email]],
        card: ['', [Validators.required, Validators.pattern(/^(?=\W*\w).{14,20}$/)]],
        mm: ['', [Validators.required, Validators.pattern(/^(?=\D*\d).{1,2}$/)]],
        yyyy: ['', [Validators.required, Validators.pattern(/^(?=\D*\d).{4}$/)]],
        cvv: ['',[Validators.required, Validators.pattern(/^(?=\D*\d).{3,4}$/)]]
      })
    }


  ngOnInit(): void {
    this.montant = this.data.montant;
    this.credit = this.data.credit;
    this.designation = this.data.designation;
    this.creditId = this.data.creditId;
    this.id = this.data.id;
    this.endpoint = this.data.url ? this.data.url : '#';
    this.paymentRequestId = this.data.paymentRequestId;
    console.log('PayementRequestId ', this.paymentRequestId);
    this.socketService.listen(`centralPay ${this.paymentRequestId}`).subscribe(
      (data: any) => {
        console.log('From Central Pay ', data);
        if (data && data.object) {
          if (data.object.transactionStatus === "SUCCESS") {
            this.pay();
          } else {
            this.notificationService.failure('Paiement', `${data.object.bankMessage} - ${data.object.transactionStatus}`);
          }
        }
      }
    );
  }

  close(): void {
    this.dialogRef.close(0);
  }

  async validate(): Promise<void> {
    this.dialogRef.close('good')
  }

  get form() { return this.payForm.controls }


  pressCard(event: any){
    var charCode = (event.which) ? event.which : event.keyCode

    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      var lastIndex = this.currentCard.length;
      this.currentCard = this.currentCard.substring(0, lastIndex-1);
      return false;
    }
    const word = event.target.value;
    const long = this.currentCard ? this.currentCard.length : 0;
    if (this.oldCount < long) {
      this.oldCount = long;
      if (long === 4 || long === 9 || long === 14) {
        this.currentCard += ' ';
      }
    } else {
      this.oldCount = long;
    }

    // if (this.currentCard.length > 3) this.currentCard = '99999';
 }


 async pay() {
  this.submitted = true;
  // event.preventDefault();
  // if (this.payForm.invalid) {
  //   return false;
  // }
  // this.loading = true;

  // let details: Card = this.payForm.value;

  // let dataToCentralPay: CentralPayDto = {
  //   firstname: details.firstName,
  //   lastname: details.lastName,
  //   number: details.card.replace(/\s/g,''),
  //   holderEmail: details.email,
  //   expirationMonth: details.mm,
  //   expirationYear: details.yyyy,
  //   cvc: details.cvv
  // }

  // console.log(dataToCentralPay);

  // this.paiementService.goToCentralPay(dataToCentralPay).subscribe(
  //   (data) => {
  //     console.log('Go to central Pay ', data);
  //   }, (error) => {
  //     console.log('Error go to central Pay ', error);
  //   }
  // )

  // console.log(details)
  this.paiementService.saveDetailsBuyPack(this.designation, this.credit, this.montant,
    'none', 'none', 'none', 'none',
    'none', 'none', 'none')
    .subscribe(
      (data: any) => {
        // console.log(data);
        if (data.error) {
          this.notificationService.infoMsg(data.message);
          this.loading = false;
        } else {
          if (this.designation === 'abonnement') {
            this.validate();
            this.loading = false;
          } else {
            this.creditService.buyCreditClient(this.montant, this.credit, this.creditId, this.id).subscribe(
              async (data) => {
                if (!data) {
                  this.notificationService.failure(`Pack ${this.designation}`,
                  `Votre transaction a echou.`);
                } else {
                  this.validate();
                }
                this.loading = false;
              }
            );
          }
        }
      }, (error) => {
        console.log(error);
        this.loading = false;
      }
    )
}


}
