import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin/admin.service';
import { NotificationService } from 'src/app/services/notification/notification.service';


@Component({
  selector: 'app-bloquer-clients-admin',
  templateUrl: './bloquer-clients-admin.component.html',
  styleUrls: ['./bloquer-clients-admin.component.scss']
})
export class BloquerClientsAdminComponent implements OnInit {
  icon = './../../../../../../assets/icons/paper-clip.svg'

  search = './../../../../../../assets/icons/admin-icons/search.png'
  four = './../../../../../../assets/icons/admin-icons/menu_four.png'
  other = './../../../../../../assets/icons/admin-icons/menu.png'

  deactivate = './../../../../../../assets/icons/admin-icons/deactivate.png'
  activate = './../../../../../../assets/icons/admin-icons/activate.png'
  delete = './../../../../../../assets/icons/admin-icons/delete.png'
  bloqued = './../../../../../../assets/icons/admin-icons/bloquer.png'
  debloquer = './../../../../../../assets/icons/admin-icons/debloquer.png'

  clients = []

  count: number  = 0
  range: number = 10
  filter: string = ''
  selected: string = 'tous'

  page: number  = 0
  topPage: number = 0
  thirdlyPage: number = 3
  list_pagination: number[] = []

  constructor(
    public adminService: AdminService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    this.countClient()
    this.listClients()
  }

  initPagination() {
    this.list_pagination = []
    let res = this.count / this.range
    res = Math.floor(res)
    this.topPage = res
    console.log('TopPage ',this.topPage)
    for (let i = 0; i <= res; i++) {
      this.list_pagination.push(i)
    }
  }

  countClient() {
    this.adminService.countClientsBlocked().subscribe(
      (data) => {
        this.count = data ? data : 0
        this.initPagination()
      }
    )
  }


  listClients() {
    this.adminService.getClientsBlocked(this.range, this.page, this.filter).subscribe(
      (data) => {
        console.log(data)
        this.clients = data
        this.clients.forEach( (client) => {
          client.createdAt = this.adminService.formatDate(client.createdAt)
          client.updatedAt = this.adminService.formatDate(client.updatedAt)
        })
        this.count = this.clients.length
        this.initPagination()
      }, (error) => {
        console.log(error)
      }
    )
  }

  input_search(event) {
    this.listClients()
  }

  selectPage(page: number) {
    this.page = page
    this.listClients()
  }

  nextPage() {
    if (this.page + 1 >= this.topPage) return false
    this.page += 1
    this.thirdlyPage += 3
    this.listClients()
  }

  prevPage() {
    if (this.page - 1 < 0) return false
    this.page -= 1
    this.thirdlyPage -= 3
    if (this.thirdlyPage < 0) this.thirdlyPage = 0
    this.listClients()
  }

  tout() {
    this.page = 0
    this.range = this.count
    this.listClients()
  }

  deleteClient(idClient: number) {
    this.adminService.deleteClient(idClient).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error)
      }
    )
  }

  deblockedClient(idClient: number) {
    const data = {
      idClient,
      reverse: true
    }
    this.adminService.blockClient(data).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error);
      }
    )
  }
}
