import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef } from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Chat, ChatSocket } from 'src/app/interfaces/chat.interface';
import { ChatService } from 'src/app/services/chat/chat.service';
import { ModelService } from 'src/app/services/model/model.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { RoomPrivateService } from '../../../services/roomPrivate/room-private.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { TimerService } from '../../../services/timer/timer.service';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import { CreditService } from '../../../services/credit/credit.service';
import * as $ from 'jquery'

// Peer
import './../shim';
import Peer from "peerjs"
import { Observable, Subscription } from 'rxjs';
import { BanishService } from '../../../services/banish/banish.service';

declare var initSettingVideoModel: any;

@Component({
  selector: 'app-live-free-saloon-model',
  templateUrl: './live-free-saloon-model.component.html',
  styleUrls: ['./live-free-saloon-model.component.scss', './../../live/live.scss']
})
export class LiveFreeSaloonModelComponent implements OnInit {
  context: string = "LIVE CHOICE US";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/live-detail.png";
  close = "assets/icons/close.svg";

  toggleDot: boolean = false;



  client = {
    img: "assets/images/user_chat.png",
    pseudo: '',
    id: 0
  }

  info = {
    bg : "assets/logos/dark-light.jpg",
    message : '',
    idRoom: 0,
    pseudo: '',
    chatColor:'#FB77FC',
    modelId: 0,
    actif: 0,
    creditModel: 0,
    idCreditModel: 0,
    winCredit: 0
  }

  timer = {
    id: 0,
    hour: 0,
    minute: 0,
    second: 0,
    fail: false,
    reinit: false,
    timer: null
  };

  choiceUs = {
    title: '',
    description: '',
    tarif: 0
  }

  stat = {
    id_show: 0,
    filledChoiceUs: false,
    type_show : null,
    tarif_show: 0,
    time_show: 0,
    client: this.client.pseudo,
    time_passed : this.timer.hour + ':' + this.timer.minute + ':' + this.timer.second,
    hour_passed : null,
    minute_passed : null,
    second_passed : null,
    actualCredit: this.info.creditModel,
    winCredit: this.info.winCredit,
    totalCredit: this.info.creditModel +  this.info.winCredit
  }

  messages: Chat[] = [];
  leaved: boolean = false;

  joinSub = new Subscription();
  leaveSub = new Subscription();
  messageSub = new Subscription();
  peerSub = new Subscription();
  toggleAudioSub = new Subscription();
  toggleVideoSub = new Subscription();
  updateChoiceUsSub = new Subscription();

  loading: boolean = false;

  showEmojiPicker = false;
  sets = [
    'native',
    'google',
    'twitter',
    'facebook',
    'emojione',
    'apple',
    'messenger'
  ]
  set = 'google';

  showVideo: boolean = true;
  showAudio: boolean = true;

  roomId = null;

  showInfoRoom: boolean = true;

  sound_notification: boolean;
  sound_message: boolean;
  // WEB RTC VIDEO
  @ViewChild('video_live_model', {static: true}) video: ElementRef<HTMLVideoElement>;
  @ViewChild('video_live_client', {static: true}) remote_video: ElementRef<HTMLVideoElement>;

  private peer: Peer;
  peerIdShare: string;
  peerId: string;
  private lazyStream: any;
  currentPeer: any;
  private peerList: Array<any> = [];

  hasGotStreamClient: boolean = false;

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private store: LocalStorageService,
    private modelService: ModelService,
    private route: ActivatedRoute,
    private chatService: ChatService,
    private roomVipService: RoomVipService,
    private socketService: SocketService,
    private profilService: ProfilService,
    private timerService: TimerService,
    private creditService: CreditService,
    private banishService: BanishService,
    private profileService: ProfilService) {
      this.peer = new Peer();
      // const id = this.route.snapshot.paramMap.get('clientId');
      // this.client.id = parseInt(id);
  }

  ngOnInit(): void {
    this.verifySound();
    $(document).ready(
      function() {
        initSettingVideoModel();
      }
    );
    this.video.nativeElement.autoplay = true;
    this.video.nativeElement.playsInline = true;
    this.video.nativeElement.muted = true;
    this.remote_video.nativeElement.autoplay = true;
    this.remote_video.nativeElement.playsInline = true;
    this.getInfo();
    this.initLiveVideo();
    this.VerifyHasGotStreamClient();
  }

  VerifyHasGotStreamClient() {
    setTimeout(() => {
      console.log('verify stream client after 10 sec');
      if (!this.hasGotStreamClient) {
        if (this.peerId) {
          const data = {
            role: 'model',
            modelId: this.info.modelId,
            room : this.info.idRoom+'V',
            peerId: this.peerId
          }
          this.socketService.newPeerIdModel(data);
        } else {
          this.peer.on('open', (id) => {
            this.peerId = id;
            const data = {
              role: 'model',
              modelId: this.info.modelId,
              room : this.info.idRoom+'V',
              peerId: this.peerId
            }
            this.socketService.newPeerIdModel(data);
          });
        }
      }
    }, 10000);
  }

  toggleShowInfoRoom() {
    this.showInfoRoom = !this.showInfoRoom;
  }

  verifySound() {
    this.profileService.getInfo().subscribe(
      (data: any) => {
        // console.log(data.setting);
        this.sound_notification = data.setting.sound_notification === 1 ? true : false;
        this.sound_message = data.setting.sound_message === 1 ? true : false;
      }
    )
  }

  ngOnDestroy() {
    this.onStop();
    clearInterval(this.timer.timer)
    this.joinSub.unsubscribe();
    this.leaveSub.unsubscribe();
    this.messageSub.unsubscribe();
    this.peerSub.unsubscribe();
    this.toggleAudioSub.unsubscribe();
    this.toggleVideoSub.unsubscribe();
  }

  // Get info Model
  getInfo() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log('Get info ', data);
        if (data.profile.status === 'En live') {
          this.onStop();
          this.router.navigate(['/modele/live-private']);
        } else if (data.profile.status === 'En ligne') {
          this.onStop();
          this.router.navigate(['/modele/chat']);
        }
        this.info.bg = data.path_soft;
        this.info.modelId = data.id;
        this.info.pseudo = data.pseudo;
        this.getInfoRoom();
        this.getCredit();
      }
    )
  }


  getInfoRoom() {
    this.roomVipService.getRoomModel(this.info.modelId).subscribe(
      (data) => {
        // console.log('Model room ', data);
        this.info.idRoom = data.idRoom;
        this.roomId = data.idRoom +'V';
        this.info.actif = data.actif < 0 ? 0 : data.actif;
        this.info.actif = data.actif > 2 ? 2 : data.actif;
        this.client.id = data.clientId;

        this.stat.tarif_show = data.free;
        this.stat.filledChoiceUs = data.free !== null ? true : false;
        this.choiceUs.title = data.title;
        this.choiceUs.description = data.description;
        this.choiceUs.tarif = data.free;

        this.getMessages();
        this.initSocket();
        this.getInfoClient();
      }
    )
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data) => {
        // console.log("Credit ", data);
        this.info.creditModel = data ? data.credit : 0;
        this.info.idCreditModel = data ? data.id : 0;
        this.stat.totalCredit = this.info.creditModel;
        this.getGain();
      }
    );

    // Launch the timer
    this.initTimer();
    this.getCostShow({title: '', description: '', tarif: 0});
  }

  initSocket() {
    if (!this.roomVipService.joinedRoom(this.info.idRoom)) {
      this.roomVipService.joinRoom(this.info.idRoom);
      this.socketService.joinVip(this.info.idRoom, this.info.modelId);
    }

    this.joinSub = this.socketService.listen(`joined ${this.info.idRoom}V`).subscribe(
      (data: any) => {
        // console.log('joined ', data);
        // this.info.actif = data < 1 ? 0 : data;
        // this.info.actif = data > 2 ? 2 : data;
        this.info.actif = 2;
        if (this.sound_notification) {
          this.socketService.soundIncome();
        }
        this.leaved = false;
      }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.info.idRoom}V`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        // this.info.actif = data < 1 ? 0 : data;
        // this.info.actif = data > 2 ? 2 : data;
        this.info.actif = 1;
        if (this.sound_notification) {
          this.socketService.soundOutcome();
        }
        this.leaved = true;
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.info.idRoom}V`).subscribe(
      (data: Chat) => {
        // console.log('Message from client ', data);
        this.messages.push(data);
        // this.getMessages();
      }
    );

    this.peerSub = this.socketService.listen(`peerId ${this.info.idRoom}V`).subscribe(
      (data: {peerId: any, idRoom: string}) => {
        console.log('Received Peer Id ', data);
        this.peerIdShare = data.peerId;
        this.connectWithPeer();
      }
    );

    this.toggleAudioSub = this.socketService.listen(`Toggle audio ${this.info.idRoom}V`).subscribe(
      (data: {roomId: any, clientId: number, peerId: any, modelId: number, isAudio: boolean}) => {
          if (data.clientId) {
            this.toggleAudioClientStream(data.peerId, data.isAudio)
          }
      }
    );

    this.toggleVideoSub = this.socketService.listen(`Toggle video ${this.info.idRoom}V`).subscribe(
      (data: {roomId: any, clientId: number, peerId: any, modelId: number, isVideo: boolean}) => {
          if (data.clientId) {
            this.toggleVideoClientStream(data.peerId, data.isVideo)
          }
      }
    );

    // Live Choice us change
    this.updateChoiceUsSub = this.socketService.listen(`updateChoiceUs ${this.info.idRoom}V`).subscribe(
          (data: {title: string, description: string, tarif: number}) => {
    this.choiceUs.title = data.title;
    this.choiceUs.description = data.description;
    this.choiceUs.tarif = data.tarif;
    this.getCostShow(this.choiceUs);
    this.stat.filledChoiceUs = true;
    });
  }

  toggleAudioClientStream(peerId: any, isAudio: boolean) {
    if (isAudio) {
      $('#video_live_client').prop('volume', 1)
    } else {
      $('#video_live_client').prop('volume', 0)
    }
  }

  toggleVideoClientStream(peerId: any, isVideo: boolean) {
    var filterVal = 'blur(0px)';
    var filterFull = 'blur(200px)';
    if (isVideo) {
      $('#video_live_client')
      .css('filter',filterVal)
      .css('webkitFilter',filterVal)
      .css('mozFilter',filterVal)
      .css('oFilter',filterVal)
      .css('msFilter',filterVal);
    } else {
      $('#video_live_client')
      .css('filter',filterFull)
      .css('webkitFilter',filterFull)
      .css('mozFilter',filterFull)
      .css('oFilter',filterFull)
      .css('msFilter',filterFull);
    }
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    // console.log(this.toggleDot);
  }

  sendMessage() {
    if (!this.info.message) return null;
    const data: Chat = {
      idRoom: this.info.idRoom,
      message: this.info.message,
      type_source: 'model',
      id_source: this.info.modelId,
      type_chat: 'vip',
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo
    }
    const msg: ChatSocket = {
      room: this.info.idRoom + 'V',
      role: 'model',
      id: this.info.modelId,
      message: this.info.message,
      idRoom: this.info.idRoom,
      type_source: 'model',
      id_source: this.info.modelId,
      type_chat: 'vip',
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo
    }
    this.socketService.sendMessage(msg);
    this.chatService.postMessage(data).subscribe(
      (data) => {
        // this.getMessages();
        this.info.message = null;
      }
    )
  }

  getMessages() {
    this.chatService.getMessage(this.info.idRoom, 'vip').subscribe(
      (data) => {
        // console.log('Messages ', data);
        this.messages = data;
      }
    )
  }

  getInfoClient() {
    this.roomVipService.getInfo(this.client.id).subscribe( // info client
      (data) => {
        // console.log("Info client ", data);
        this.client.pseudo = data.pseudo;
        this.stat.client = data.pseudo;
      }
    )
  }

  async liveOut() {
    this.loading = true;
    this.onStop();
    await (await this.leaveTimer()).subscribe(
      async (data) => {
        this.timer.reinit = true;
        this.clearTimer(); // set to zero
        return await this.socketService.leaveVip(this.info.idRoom, 'model').then(
          async (data) => {
          this.roomVipService.leaveRoom();
          await this.profilService.updateStatus(this.info.modelId, 'En ligne').subscribe(
          (data) => {
            this.loading = false;
            window.location.href = '/modele/chat';
            // this.router.navigate(['/modele/chat']);
            }
          )}
        );
      }, (error) => {
        this.loading = false;
      }
    );
  }

      // Timer
  async initTimer() {
        this.timerService.beginTimerModel(this.info.modelId, TypeTimer.VIP).subscribe(
          async (data) => {
            // console.log('Timer ', data);
            const created: Date = data.createdAt;
            const updated: Date = data.updatedAt;
            this.timer.id = data.id;
            // console.log(created.toString(), " to ", updated.toString());
            const {hour, minute, second} = this.timerService.convertTime(created.toString(), updated.toString());
            this.timer.hour = hour;
            this.timer.minute = minute;
            this.timer.second = second;
            this.stat.time_passed = this.timer.hour + ' : ' + this.timer.minute + ' : ' +this.timer.second;
            this.stat.hour_passed = this.timer.hour;
            this.stat.minute_passed = this.timer.minute;
            this.stat.second_passed = this.timer.second;
            // console.log(this.timer.hour, ':', this.timer.minute, ':', this.timer.second);
          }
    )
  }

  async getCostShow(data: {title: string, description: string, tarif: number}) {
      // console.log("Show Cost ", data);
      this.stat.tarif_show = data.tarif;
      this.stat.time_show = 60;
      this.stat.type_show = 'CHOICE US';
      this.stat.id_show = 0;
      this.beginTimer();
  }

  beginTimer() {
    // console.log("timer second ", this.show.second);
    const delay = this.stat.time_show * 1000;
    // console.log(delay, " ms ");
   this.timer.timer = setInterval(
      () => {
        if (this.info.actif > 1) this.getGain();
      }, delay
    )
  }

  getGain() {
    this.roomVipService.getRoomModel(this.info.modelId).subscribe(
      (data) => {
        // console.log("Gain ", data);
        this.stat.winCredit = data.gain;
        this.stat.actualCredit = this.stat.totalCredit - this.stat.winCredit;
        this.info.winCredit = data.gain;
    })
  }

  // Timer set to 0
  clearTimer() {
    this.timer.reinit = true;
  }

  async leaveTimer(): Promise<Observable<any>> {
    const data = {
      id: this.timer.id,
      type: TypeTimer.VIP,
      leaved: true,
      push: new Date().toString()
    }

    return await this.timerService.updateTimer(data);
  }


  // WEB RTC - LIVE VIDEO
  initLiveVideo() {
    // console.log("Start");
    this.onStart();
    this.getPeerId();
  }

  onStart(){
    if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
      navigator.mediaDevices.getUserMedia({video: true, audio: true}).then((ms: MediaStream) => {
        this.lazyStream = ms;
        console.log('My stream ', ms);
        this.video.nativeElement.srcObject = this.lazyStream;
        // this.remote_video.nativeElement.srcObject = ms;
        // const _video = this.video.nativeElement;
        // _video.srcObject = ms;
        // _video.play();
        $('#video_live_model').prop('volume', 0);
      });
    }
  }

  onStop() {
    if (this.video && this.video.nativeElement.srcObject) {
      this.video.nativeElement.pause();
      if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
        (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
      }
      this.video.nativeElement.srcObject = null;
    }
    if (this.lazyStream) {
      this.lazyStream.getTracks().forEach(
        (track) => {
          track.stop();
        }
      );
    }
    this.lazyStream = null;
  }

  private getPeerId = () => {
    // console.log("Get Peer");
    this.peer.on('open', (id) => {
      console.log("Peer Id ", id);
      this.peerId = id;
      const data = {
        role: 'model',
        modelId: this.info.modelId,
        room : this.info.idRoom+'V',
        peerId: this.peerId
      }
      this.socketService.newPeerIdModel(data);
    });

    this.peer.on('call', (call) => {
      console.log('On Call');
      call.answer(this.lazyStream);
      console.log('Answer stream');
      call.on('stream', (remoteStream) => {
        console.log('Receive stream');
        this.hasGotStreamClient = true;
        this.streamRemoteVideo(remoteStream);
        this.currentPeer = call.peerConnection;
        this.peerList.push(call.peer);
      });

      // this.onStop();
      // navigator.mediaDevices.getUserMedia({
      //   video: true,
      //   audio: true
      // }).then((stream) => {
      //   this.lazyStream = stream;
      //   this.video.nativeElement.srcObject = stream;
      //   // const _video = this.video.nativeElement;
      //   // _video.srcObject = stream;
      //   // _video.play();
      //   call.answer(stream);
      //   // console.log('Answer stream');
      //   call.on('stream', (remoteStream) => {
      //     // console.log('Receive stream');
      //     this.streamRemoteVideo(remoteStream);
      //     if (!this.peerList.includes(call.peer)) {
      //       this.currentPeer = call.peerConnection;
      //       this.peerList.push(call.peer);
      //     }
      //   });
      // }).catch(err => {
      //   console.log(err + 'Unable to get media');
      // });
    });
  }

  connectWithPeer(): void {
    this.callPeer(this.peerIdShare);
  }

  private callPeer(id: string): void {
    console.log('Call peer');
    const call = this.peer.call(id, this.lazyStream);
    call.on('stream', (remoteStream) => {
      console.log('Receive stream');
      if (!this.peerList.includes(call.peer)) {
        this.streamRemoteVideo(remoteStream);
        this.currentPeer = call.peerConnection;
        this.peerList.push(call.peer);
      }
    });

    // this.onStop();
    // navigator.mediaDevices.getUserMedia({
    //   video: true,
    //   audio: true
    // }).then((stream) => {
    //   this.lazyStream = stream;
    //   this.video.nativeElement.srcObject = stream;
    //   const call = this.peer.call(id, stream);
    //   call.on('stream', (remoteStream) => {
    //     // console.log('Receive stream');
    //     this.streamRemoteVideo(remoteStream);
    //     if (!this.peerList.includes(call.peer)) {
    //       this.currentPeer = call.peerConnection;
    //       this.peerList.push(call.peer);
    //     }
    //   });
    // }).catch(err => {
    //   console.log(err + 'Unable to connect');
    // });
  }

  private streamRemoteVideo(stream: MediaStream): void {
    console.log('Stream Remote video ', stream)
    // this.remote_video.nativeElement.srcObject = stream;
    const _video = this.remote_video.nativeElement;
    _video.srcObject = stream;
    setTimeout(() => {
      _video.play();
    }, 3000)

    // const video = document.createElement('video');
    // video.classList.add('video');
    // video.srcObject = stream;
    // video.play();
    // document.getElementById('remote-video').append(video);
  }

  toggleVideo() {
    this.showVideo = !this.showVideo;
    if (this.showVideo) {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    } else {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    }
    // if (this.showVideo) {
    //   $("#video_live_model").removeClass( "hideStream" ).addClass("showStream");
    // } else {
    //   $("#video_live_model").removeClass("showStream").addClass("hideStream");
    // }
    // this.onStop();
    // this.onStart();
    const room = this.info.idRoom+'V'
    this.socketService.toggleVideo(room, null, this.peerId, this.info.modelId, this.showVideo)
  }

  toggleAudio() {
    this.showAudio = !this.showAudio;

    if (!this.showAudio) {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    } else {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    }

    // this.onStop();
    // this.onStart();
    const room = this.info.idRoom+'V'
    this.socketService.toggleAudio(room, null, this.peerId, this.info.modelId, this.showAudio)
  }

  banish() {
    if (this.client.id === 0 || !this.client.id || !this.roomId) return null
    this.banishService.banishClient(this.info.modelId, this.client.id).subscribe(
      (data) => {
        this.socketService.banishClient(this.roomId, this.client.id)
        // console.log(data)
      }
    )
  }

    // -------- EMOJI -----------------

    toggleEmojiPicker() {
      this.showEmojiPicker = !this.showEmojiPicker;
    }

    addEmoji(event) {
      const {info} = this;
      // console.log(this.info.message);
      // console.log(`${event.emoji.native}`);
      let text = '';
      if (this.info.message) {
        text = `${info.message}${event.emoji.native}`;
      } else {
        text = `${event.emoji.native}`;
      }
      this.info.message = text;
      // console.log(this.info.message)
      // this.showEmojiPicker = false;
    }

    focus() {
      this.showEmojiPicker = false;
    }


}
