import { Component, OnInit } from '@angular/core';
import { PaiementService } from 'src/app/services/paiement/paiement.service';
import { CreditService } from '../../../../services/credit/credit.service';
import { MatDialog } from '@angular/material/dialog';
import { SuiviPayModelComponent } from 'src/app/modals/suivi-pay-model/suivi-pay-model.component';
import { ModelService } from '../../../../services/model/model.service';

@Component({
  selector: 'app-solde',
  templateUrl: './solde.component.html',
  styleUrls: ['./solde.component.scss']
})
export class SoldeComponent implements OnInit {
  coinImg = "assets/icons/coin-stack.svg";
  solde = 500;
  modelId: number;
  modelPseudo: string;

  constructor(
    private creditService: CreditService,
    public paiementService: PaiementService,
    private dialog: MatDialog,
    private modelService: ModelService
  ) { }

  ngOnInit(): void {
    this.getCredit();
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data) => {
        // console.log(data);
        this.solde = data.credit;
        this.getInfoModel();
      }
    )
  }

  getInfoModel() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log(data);
        this.modelId = data.id;
        this.modelPseudo = data.pseudo;
      }
    )
  }

  detailPay() {
    const dialogRef = this.dialog.open(SuiviPayModelComponent, {
      width: '80vh',
      height: '80vh',
      data: {
        modelId : this.modelId,
        modelPseudo : this.modelPseudo
      }
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

}
