import { Component, OnInit } from '@angular/core';
import { CommissionService } from '../../../../../../services/commission/commission.service';
import { AdminService } from '../../../../../../services/admin/admin.service';

@Component({
  selector: 'app-commissions-model-admin',
  templateUrl: './commissions-model-admin.component.html',
  styleUrls: ['./commissions-model-admin.component.scss']
})
export class CommissionsModelAdminComponent implements OnInit {
  commissions = [];

  constructor(
    private commissionService: CommissionService,
    public adminService: AdminService
  ) { }

  ngOnInit(): void {
    this.getAllCommissions();
  }

  getAllCommissions() {
    this.commissionService.getAllCommissions().subscribe(
      (data) => {
        console.log(data)
        this.commissions = data;
      }
    )
  }

}
