import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin/admin.service';
import { NotificationService } from 'src/app/services/notification/notification.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-paiement-admin',
  templateUrl: './list-paiement-admin.component.html',
  styleUrls: ['./list-paiement-admin.component.scss']
})
export class ListPaiementAdminComponent implements OnInit {
  icon = './../../../../../../../assets/icons/paper-clip.svg'

  search = './../../../../../../../assets/icons/admin-icons/search.png'
  four = './../../../../../../../assets/icons/admin-icons/menu_four.png'
  other = './../../../../../../../assets/icons/admin-icons/menu.png'

  deactivate = './../../../../../../../assets/icons/admin-icons/deactivate.png'
  activate = './../../../../../../../assets/icons/admin-icons/activate.png'
  delete = './../../../../../../../assets/icons/admin-icons/delete.png'
  bloqued = './../../../../../../../assets/icons/admin-icons/bloquer.png'
  debloquer = './../../../../../../../assets/icons/admin-icons/debloquer.png'

  paiements = []

  count: number  = 0
  range: number = 10
  filter: string = ''
  selected: string = 'tous'

  page: number  = 0
  topPage: number = 0
  thirdlyPage: number = 3
  list_pagination: number[] = []
  constructor(
    public adminService: AdminService,
    private notificationService: NotificationService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.countPay()
    this.listPay()
  }

  initPagination() {
    this.list_pagination = []
    let res = this.count / this.range
    res = Math.floor(res)
    this.topPage = res
    for (let i = 0; i <= res; i++) {
      this.list_pagination.push(i)
    }
  }

  countPay() {
    this.adminService.countPay().subscribe(
      (data: any) => {
        this.count = data ? data : 0
        this.initPagination()
      }
    )
  }

  listPay() {
    this.adminService.getListPaiement(this.selected, this.range, this.page, this.filter).subscribe(
      (data: any) => {
        // console.log('Paiement ', data)
        this.paiements = data
        this.paiements.forEach( (client) => {
          client.createdAt = this.adminService.formatDate(client.createdAt)
          client.montant = client.montant ? client.montant + ' ' : ''
        })
        this.count = this.paiements.length
        this.initPagination()
      }, (error) => {
        console.log(error)
      }
    )
  }

  select(event) {
    const value = event.target.value;
    this.selected = value
    this.listPay()
  }

  input_search(event) {
    this.listPay()
  }

  selectPage(page: number) {
    this.page = page
    this.listPay()
  }

  nextPage() {
    this.page += 1
    this.thirdlyPage += 3
    this.listPay()
  }

  prevPage() {
    this.page -= 1
    this.thirdlyPage -= 3
    if (this.thirdlyPage < 0) this.thirdlyPage = 0
    this.listPay()
  }

  tout() {
    this.page = 0
    this.range = this.count
    this.listPay()
  }

  deletePay(idPay: number) {
    this.adminService.deletePaiement(idPay).subscribe(
      (data: any) => {
        if (data.success) this.listPay()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error)
      }
    )
  }

  newPay() {
    this.router.navigate(['/admin/paiement'])
  }

}
