@Injectable({
  providedIn: 'root'
})
export class PaiementService {
  endpoint = environment.endpoint;

  constructor(private http: HttpClient, private authService: AuthService) { }

  buyPack(credit: number) {
    const data = {
      credit
    }
    return this.http.post(`${this.endpoint}/paiement/buy-pack`, data, httpOptions)
  }

  saveDetailsBuyPack(designation: string, credit: number, montant: number, card : string,
    mm : string, yyyy : string, cvv : string, firstName : string, lastName : string,
    email : string) {
    const data = {
      designation,
      credit,
      montant,
      card,
      mm,
      yyyy,
      cvv, firstName,
      lastName,
      email
    }
    return this.http.post(`${this.endpoint}/buy-pack`, data, httpOptions)
  }

  hasPaid() {
    return this.http.post(`${this.endpoint}/buy-pack/hasPaid`, {}, httpOptions)
  }

  verifyPack() {
    return this.http.post(`${this.endpoint}/buy-pack/verifyPack`, {}, httpOptions)
  }

  convertCreditToMoney(credit: number) {
    const money = credit * 0.147;
    return money.toFixed(3);
  }

  getSuiviPay(modelId: number) {
    return this.http.get(`${this.endpoint}/paiement/getSuiviPay/${modelId}`, httpOptions)
  }

  // Central Pay

  openCentralPay(price: number) {
    const data = {
      amount: price
    }
    return this.http.post(`${this.endpoint}/paiement/open-centralPay`, data, httpOptions);
  }

  goToCentralPay(data: CentralPayDto) {
    return this.http.post(`${this.endpoint}/paiement/goToCentralPay`, data, httpOptions);
  }
}
