import { Component, Input, OnInit } from '@angular/core';
import { RoomVipService } from '../../../../services/roomVip/room-vip.service';
import { NotificationService } from '../../../../services/notification/notification.service';
import { SocketService } from '../../../../services/socket/socket.service';

@Component({
  selector: 'app-live-vip-stat',
  templateUrl: './live-vip-stat.component.html',
  styleUrls: ['./live-vip-stat.component.scss']
})
export class LiveVipStatComponent implements OnInit {
  @Input()
  type_show: any;

  @Input()
  tarif_show: any;

  @Input()
  time_show: any;

  @Input()
  client: any;

  @Input()
  time_passed: any;

  @Input()
  hour_passed: any;

  @Input()
  minute_passed: any;

  @Input()
  second_passed: any;

  @Input()
  actualCredit: any;

  @Input()
  winCredit: any;

  @Input()
  totalCredit: any;

  @Input()
  liveFree: number;

  @Input()
  roomId: number;



  @Input()
  filled: boolean;

  @Input()
  filledChoiceUs: boolean;

  @Input()
  title: string;

  @Input()
  description: string;

  @Input()
  tarif: number;

  stats1 = [
    {label: "Type du show", value: "VIP"},
    {label: "Tarif du show", value: "15 Crdits / mn"},
    {label: "Client", value: "Motome"}
  ];

  stats2 = [
    {label: "Solde de crdits", value: "0 Crdits"},
    {label: "Dure passe", value: "00:00"},
    {label: "Crdits encaisss", value: "0 Credits"},
    {label: "Solde total", value: "0 Credits"},
  ];

  choiceUsDetail = {
    title: '',
    description: '',
    tarif: 0
  }

  choiceUs = [
    {
      title: 'Discussion',
      description: 'Lors de ce show on se fera plaisir tout les deux \
      de discuter librement tout ce que vous voulez'
    },
    {
      title: 'Voyeur',
      description: "Pendant ce show, faisons nous plaisir jusqu' la jouissance. \
      Tu pourras voir tout ce que vous voulez sur moi"
    },
    {
      title: 'Masturbation',
      description: "Pendant ce show, faisons nous plaisir jusqu' la jouissance. \
      Tu pourras voir tout ce que vous voulez sur moi"
    }, {
      title: 'Ignore cam',
      description: ' Faites vous plaisir !'
    },
    {
      title: 'Gode',
      description: 'Faites vous plaisir !'
    },
    {
      title: 'Anal',
      description: 'Faites vous plaisir !'
    }
  ];


  constructor(
    private roomVipService: RoomVipService,
    private notificationService: NotificationService,
    private socketService: SocketService
  ) { }

  ngOnInit(): void {
    this.startMinuteur();
  }

  startMinuteur() {
    setInterval(() => {
          this.second_passed++;
          if (this.second_passed > 59) {
            this.second_passed = 0;
            this.minute_passed++;
            if (this.minute_passed > 59) {
              this.minute_passed = 0;
              this.hour_passed++;
            }
          }
    }, 1000);
  }


  getPaliers() {
    this.roomVipService.getRoomInfo(this.roomId).subscribe(
      (data) => {
        console.log(data);
      }
    )
  }

  changeChoiceUs(event) {
    let found = false;
    this.choiceUs.forEach(element => {
      if (element.title === event.target.value) {
        this.choiceUsDetail.title = element.title;
        this.choiceUsDetail.description = element.description;
        this.description = element.description;
        found = true;
      }
    })
    if (!found) {
      this.choiceUsDetail.description = '';
      this.description = '';
    }
  }

  changeChoiceUsTarif(event) {
    this.choiceUsDetail.tarif = event.target.value;
  }

  saveChoiceUs() {
    if (this.choiceUsDetail.description === '' || this.choiceUsDetail.title === '' || this.choiceUsDetail.tarif === 0) {
      this.notificationService.infoMsg('Veuillez prciser le detail de votre room');
      return null;
    } else {
      this.roomVipService.updateChoiceUs(this.roomId, this.choiceUsDetail.title, this.choiceUsDetail.description,
        this.choiceUsDetail.tarif).subscribe(
          (data) => {
            console.log(data);
            this.socketService.updateChoiceUs(this.roomId+'V', this.choiceUsDetail.title, this.choiceUsDetail.description,
            this.choiceUsDetail.tarif);
            this.notificationService.infoMsg(`LIVE CHOICE US : ${this.choiceUsDetail.title} - ${this.choiceUsDetail.tarif} crdits / mn`);
            this.filledChoiceUs = true;
          }
        )
    }
  }

}
