import {Component, Inject, OnInit} from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DeleteComponent } from 'src/app/components/client/profil/delete/delete.component';
import { AdminService } from '../../../../../../../services/admin/admin.service';

@Component({
  selector: 'app-motif-reject',
  templateUrl: './motif-reject.component.html',
  styleUrls: ['./motif-reject.component.scss']
})
export class MotifRejectComponent implements OnInit {
  action: any;
  exclamation = "./../../../../../../../../assets/icons/chat-comment.svg";
  motif: string = "";
  error: boolean = false;
  empty: boolean = false;
  constructor(
    public dialogRef: MatDialogRef<MotifRejectComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private adminService: AdminService) {

    }

  onNoClick(): void {
    this.dialogRef.close(0);
  }

  ngOnInit(): void {
    // console.log(this.action);
  }

  async validate(): Promise<void> {
    this.empty = false;
    this.error = false;
    if (this.motif == "") {
        this.empty = true;
        return null;
    }
    this.dialogRef.close(this.motif)
  }
}
