import { EncryptionService } from './../../../services/encryption/encryption.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HeaderService } from '../../../services/header/header.service';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth/auth.service';
import { environment } from 'src/environments/environment';
import { loginUser } from '../../../interfaces/login.interface';
import * as $ from 'jquery';
import { AdminService } from '../../../services/admin/admin.service';

@Component({
  selector: 'app-connexion-admin',
  templateUrl: './connexion-admin.component.html',
  styleUrls: ['./connexion-admin.component.scss']
})
export class ConnexionAdminComponent implements OnInit {
  loginForm : FormGroup;
  submitted :boolean =  false;
  loading: boolean = false;
  checked :boolean =  false;
  pseudoStorage : string = null;
  passStorage : string = null;
  errorAuth  :boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private adminService: AdminService,
    private router: Router,
    private authService: AuthService
  ) {
    this.loginForm = this.formBuilder.group({
      email : ['',[Validators.required, Validators.email]],
      password: ['', Validators.required]
    })
  }

  ngOnInit(): void {

  }

  get form() { return this.loginForm.controls }


  async login(event) {
    this.submitted = true;
    event.preventDefault();
    if (this.loginForm.invalid) {return false;}
    this.loading = true;
    let user: any = this.loginForm.value;
    await this.adminService.login(user).subscribe(
      (res: any) => {
          if (res.error) {
            this.errorAuth = res.message;
            this.loading = false;
          } else {
            this.errorAuth = null;
            if (res.access_token)
              this.authService.saveToken(res.access_token).then(
                () => {
                  this.loading = false;
                  this.router.navigate(['/admin']);
                }
              );
          }
      },
      (err: any) => {
          this.loading = false;
          console.log(err);
      }
    );
    return true;
}

}
