import { Component, Input, OnInit, OnDestroy, HostListener } from '@angular/core';
import { NavigationExtras, Router, RouterStateSnapshot, ActivatedRoute, NavigationEnd } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { ModelService } from '../../../services/model/model.service';
import { RoomService } from '../../../services/room/room.service';
import { ChatService } from '../../../services/chat/chat.service';
import { Chat, ChatSocket } from 'src/app/interfaces/chat.interface';
import { SocketService } from '../../../services/socket/socket.service';
import { Observable, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { ProfilService } from '../../../services/profil/profil.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ClientService } from '../../../services/client/client.service';
import { AlbumService } from '../../../services/album/album.service';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import { TimerService } from '../../../services/timer/timer.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { BanishService } from '../../../services/banish/banish.service';
import { NotificationService } from '../../../services/notification/notification.service';
import { CreditService } from '../../../services/credit/credit.service';
import { TIP } from 'src/app/services/tips/tips.service';
import * as AOS from 'aos';
import { RoomTipsService } from '../../../services/roomTips/room-tips.service';


@Component({
  selector: 'app-live-free',
  templateUrl: './live-free.component.html',
  styleUrls: ['./live-free.component.scss', './../live.scss']
})
export class LiveFreeComponent implements OnInit, OnDestroy {
  context: string = "LIVE FREE";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/live-detail.png";
  close = "assets/icons/close.svg";

  bg = "assets/logos/dark-light.jpg";
  modelPseudo = '';
  modelId: number;
  idRoom: number;
  actif: number;
  message: string = '';
  chatColor = "black";
  id: number;

  toggleDot: boolean = false;
  messages: Chat[] = [];
  leaved: boolean = false;
  passedToPrivate: boolean = false;
  passedToTips: boolean = false;
  passedVIP: boolean = false;
  actifSub = new Subscription();
  joinSub = new Subscription();
  leaveSub = new Subscription();
  messageSub = new Subscription();
  modelPassedPrivateSub = new Subscription();
  modelLeavedSub = new Subscription();
  responseInvitationPrivateSub = new Subscription();
  responsePositiveInvitationVIPSub = new Subscription();
  responseNegativeInvitationVIPSub = new Subscription();
  banishSub = new Subscription();
  modelPassedVipSub  = new Subscription();
  albums = []
  clientId: number;
  clientPseudo: string;
  clientCredit: number;
  clientCreditId: number;

  loading: boolean = false;
  acceptedInvitationToPrivate: boolean = false;

  costShowPrivate: number = null;
  costShowVip: number = null;
  acceptedVIP: boolean = false;

  showEmojiPicker = false;
  sets = [
    'native',
    'google',
    'twitter',
    'facebook',
    'emojione',
    'apple',
    'messenger'
  ]
  set = 'google';

  statPrivate = {
    tarif_show: 0,
    time_show: 0
  }

  statVIP = {
    tarif_show: 0,
    time_show: 0
  }

  // Salon libre
  live_selected = null;

  // Tips
  showTips: boolean = false;

  @HostListener("window:beforeunload", ["$event"]) unloadHandler(event: Event) {
    this.socketService.leave(this.idRoom, 'client', this.clientPseudo, this.clientId);
  }

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private modelService: ModelService,
    private roomService: RoomService,
    private store: LocalStorageService,
    private chatService: ChatService,
    private socketService: SocketService,
    private profilService: ProfilService,
    private popupService: PopupService,
    private clientService: ClientService,
    private albumService: AlbumService,
    private timerService: TimerService,
    private roomVipService: RoomVipService,
    private banishService: BanishService,
    private notificationService: NotificationService,
    private creditService: CreditService,
    private roomTipsService: RoomTipsService
    ) {
      const id = this.route.snapshot.paramMap.get('model');
      this.modelId = parseInt(id);
    }

  ngOnInit(): void {
    AOS.init();
    if (!this.modelId) return null;
    this.getMyInfo();
    this.initColor();
  }

  async getCostPrivateShow() {
    return await this.timerService.getCostShow(TypeTimer.PRIVATE).subscribe(
      (data: any) => {
        // console.log("Show Cost ", data)
        this.statPrivate.tarif_show = data.credit? data.credit : 0;
        this.statPrivate.time_show = data.second ? data.second : 0;
      }
    )
  }

  async getCostVIPShow() {
    return await this.timerService.getCostShow(TypeTimer.VIP).subscribe(
      (data: any) => {
        // console.log("Show Cost ", data)
        this.statVIP.tarif_show = data.credit? data.credit : 0;
        this.statVIP.time_show = data.second ? data.second : 0;
      }
    )
  }

  isBanished() {
    this.banishService.isBanished(this.modelId, this.clientId).subscribe(
      (data: any) => {
        const ok: boolean = data.authorized
        if (!ok) {
          this.popupService.info(null, 'Accs refus',
          `Vous n\' tes pas autoris  entrer dans ce room.`);
          this.router.navigateByUrl('/client/accueil/registered')
        } else {
          this.getModel();
          this.getInfoRoom();
        }
      }
    )
  }

  ngOnDestroy(): void {
    this.roomService.leaveRoom();
    this.socketService.leave(this.idRoom, 'client', this.clientPseudo, this.clientId);

    this.actifSub.unsubscribe();
    this.joinSub.unsubscribe();
    this.leaveSub.unsubscribe();
    this.messageSub.unsubscribe();
    this.modelPassedPrivateSub.unsubscribe();
    this.modelLeavedSub.unsubscribe();
    this.responseInvitationPrivateSub.unsubscribe();
    this.responsePositiveInvitationVIPSub.unsubscribe();
    this.responseNegativeInvitationVIPSub.unsubscribe();
    this.modelPassedVipSub.unsubscribe();
    this.banishSub.unsubscribe();
  }

  initSocket() {
    // if (!this.roomService.joinedRoom()) {
    //   this.roomService.joinRoom();
    //   this.socketService.join(this.idRoom, this.modelId);
    // }
    this.socketService.join(this.idRoom, this.modelId, this.clientPseudo, this.clientId);

    this.socketService.listen(`currentSaloon ${this.idRoom}`).subscribe(
      (data: any) => {
        this.live_selected = data.live_selected;
      }
    );

    this.joinSub = this.socketService.listen(`joined ${this.idRoom}`).subscribe(
      (data: any) => {
        // console.log('Joined ', data);
        this.actif = data.count < 0 ? 0 : data.count;
        // console.log('Actif ', this.actif);
      }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.idRoom}`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        this.actif = data.count < 0 ? 0 : data.count;
        // console.log('Actif ', this.actif);
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.idRoom}`).subscribe(
      (data: Chat) => {
        // console.log('Msg to client ', data);
        this.messages.push(data);
        // this.getMessages();
      }
    );

    this.modelPassedPrivateSub = this.socketService.listen(`Pass to private ${this.idRoom}`).subscribe(
      (data) => {
        // console.log('Pass to private ', data);
        if (this.acceptedInvitationToPrivate) {
          this.livePrivate();
        } else {
          this.passedToPrivate = true;
        }
      }
    );

    this.modelLeavedSub = this.socketService.listen(`model leaved ${this.idRoom}`).subscribe(
      (data) => {
        this.leaved = true;
      }
    );

    this.banishSub = this.socketService.listen(`Banish client ${this.idRoom} ${this.clientId}`).subscribe(
      (data) => {
        this.isBanished()
      }
    );

    this.responseInvitationPrivateSub = this.socketService.listen(`response invitation private ${this.idRoom} ${this.clientId}`).subscribe(
      (data) => {
        this.loading = false;
        // console.log('Answer')
        this.acceptedInvitationToPrivate = true;
        if (this.passedToPrivate) {
          this.livePrivate();
        }
      }
    );

    this.responsePositiveInvitationVIPSub = this.socketService.listen(`response positive invitation model to vip ${this.idRoom} ${this.clientId}`).subscribe(
      (data: any) => {
          this.loading = false;
          this.acceptedVIP = true;
          this.acceptedInvitation(data.special);
      }
    );

    this.responseNegativeInvitationVIPSub = this.socketService.listen(`response negative invitation model to vip ${this.idRoom} ${this.clientId}`).subscribe(
      (data: any) => {
        // console.log(data)
          this.loading = false;
          this.acceptedVIP = false;
          if (data && data.special) {
            if (data.special === 'live choice') {
              this.popupService.info(null, 'LIVE CHOICE US',
              `${this.modelPseudo} n'est pas disponible pour le moment.`);
            } else if (data.special === 'live tips') {
              this.popupService.info(null, 'LIVE TIPS',
              `${this.modelPseudo} n'est pas disponible pour le moment.`);
            }
          } else {
            this.popupService.info(null, 'LIVE VIP',
            `${this.modelPseudo} n'est pas disponible pour le moment.`);
          }
      }
    );

    // Model passed to VIP
    this.modelPassedVipSub = this.socketService.listen(`Pass to VIP ${this.idRoom}`).subscribe(
      (data: any) => {
        if (this.acceptedVIP) this.acceptedInvitation();
        this.passedVIP = true;
      }
    );

    this.socketService.listen(`Pass to tips ${this.idRoom}`).subscribe(
      (data) => {
        // console.log('Pass to private ', data);
        this.live_selected = 'live_tips';
        this.passedToTips = true;
      }
    );

    this.socketService.askCurrentSaloon(this.idRoom, this.modelId);
  }

  getMyInfo() {
    this.loading = true;
    this.clientService.getMyInfos().subscribe( // My info
      (data) => {
        // console.log("My info ", data)
        this.clientId = data.id;
        this.clientPseudo = data.pseudo;
        this.clientCredit = data.credit ? data.credit.credit : 0; // Client Credit
        this.clientCreditId = data.credit ? data.credit.id : 0; // Client Credit Id
        this.isBanished();
        this.getCostVIPShow();
        this.getCostPrivateShow();
      }, (error) => {
        this.loading = false;
      }
    )
  }

  getModel() {
    this.modelService.getModel(this.modelId).subscribe(
      (data) => {
        if (!data) {
          this.invalideRoom();
          this.clientService.lastRoom(null);
          setTimeout(
            () => {
              this.router.navigateByUrl('/client/accueil/registered');
            }, 2000
          )
          return null;
        }
        // console.log('Model ', data);
        this.bg = data.path_soft;
        this.modelPseudo = data.pseudo;
        if (data.profile.status === "Hors ligne") {
          this.popupService.info('/client/accueil/registered','CHAT INDISPONIBLE',
          `${this.modelPseudo} est hors ligne.`);
          this.router.navigateByUrl('/client/accueil/registered');
        } else if (data.profile.status === "En live") {
          this.passedToPrivate = true;
        } else if (data.profile.status === "En tips") {
          this.popupService.info(null,'CHAT INDISPONIBLE',
          `${this.modelPseudo} est en live tips.`);
          this.passedToTips = true;
          this.live_selected = 'live_tips';
        } else if (data.profile.status === "En live choice") {
          this.popupService.info('/client/accueil/registered','CHAT INDISPONIBLE',
          `${this.modelPseudo} est en live CHOICE.`);
          this.live_selected = 'choice_us';
        } else if (data.profile.status === "En vip") {
          this.popupService.info('/client/accueil/registered','CHAT INDISPONIBLE',
          `${this.modelPseudo} est en live VIP.`);
        }
        this.getInfo();
      }, (error) => {
        this.loading = false;
      }
    )
  }

  getInfo() {
    const id: number = parseInt(this.store.get("identification//"));
    this.roomService.getInfo(id).subscribe(
      (data) => {
        this.id = data.id;
        this.getAlbums();
      }
    )
  }

  getInfoRoom() {
    this.roomService.getRoomModel(this.modelId).subscribe(
      (data) => {
        if (!data) return null;
        // console.log('Model room ', data);
        this.idRoom = data.idRoom;
        this.actif = data.actif;
        this.clientService.storeLastChat(this.idRoom);
        this.clientService.lastRoom(this.router.url);
        this.getMessages();
        this.initSocket();
      }
    )
  }

  initColor() {
    if (this.store.get("ticket_chat")) {
      this.chatColor = this.store.get("ticket_chat");
      return true;
    };
    this.roomService.getColor().subscribe(
      (data) => {
        this.chatColor = data.color;
        this.store.set("ticket_chat", data.color);
      }
    )
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    // console.log(this.toggleDot);
  }

  sendMessage() {
    if (!this.message) return null;
    const data: Chat = {
      idRoom: this.idRoom,
      message: this.message,
      type_source: 'client',
      id_source: this.clientId,
      code_couleur: this.chatColor,
      pseudo: this.clientPseudo
    }
    const msg: ChatSocket = {
      room: this.idRoom + '',
      role: 'client',
      id: this.clientId,
      message: this.message,
      idRoom: this.idRoom,
      type_source: 'client',
      id_source: this.clientId,
      code_couleur: this.chatColor,
      pseudo: this.clientPseudo,
      createdAt: new Date().toISOString()
    }
    this.socketService.sendMessage(msg);
    this.chatService.postMessage(data).subscribe(
      (data) => {
        // this.getMessages();
        this.message = null;
      }
    );
  }

  getMessages() {
      this.chatService.getMessage(this.idRoom).subscribe(
        (data) => {
          this.messages = data;
          this.getCostShow();
          // console.log(this.messages);
        }
      )
  }

  livePrivate() {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      (data) => {
        if (this.clientCredit <= this.costShowPrivate) {
          this.popupService.info(null,'CREDIT INSUFFISANT',
          `Votre crdit est insuffisant pour lancer le live. Veuillez recharger votre compte.`);
          return false;
        }
        // console.log('To private ', data);

        if (data.status === 'En tips') {
          this.popupService.info(null,'MODELE EN LIVE TIPS',
          `Veuillez Passer en live TIPS.`);
          return false;
        }

        if (data.status === 'En vip' || data.status === 'En live choice') {
          this.popupService.info(null,'MODELE INDISPONIBLE',
          `Votre modle est dans un autre live pour le moment.`);
          return false;
        } else if (data.status === "En live") {
          this.router.navigate(['/client/live/private', {model: this.modelId}]);
        } else {
          // Send invitation to pass to private
          this.loading = true;
          this.socketService.clientInviteModelToPrivate(this.idRoom, this.clientId, this.clientPseudo,
            this.modelId);
          setTimeout(() => {
            if (this.loading) this.loading = false;
          }, 10000);
          // this.popupService.info(null,'LIVE INDISPONIBLE',
          // `${this.modelPseudo} n\'a pas encore lanc le live.`);
        }
      }
    )
  }

  liveVIP() {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      (data) => {
        if (this.clientCredit <= this.costShowVip) {
          this.popupService.info(null,'CREDIT INSUFFISANT',
          `Veuillez recharger votre compte.`);
          return false;
        }

        if (data.status === 'En live choice') {
          this.popupService.info(null,'MODELE INDISPONIBLE',
          `Votre modle est dans un autre live pour le moment.`);
          return false;
        }

        if (data.status === 'En tips') {
          this.popupService.info(null,'MODELE EN LIVE TIPS',
          `Veuillez Passer en live TIPS.`);
          return false;
        }

        // console.log('To vip ', data);
        if (data.status === "En vip") {
          this.router.navigate(['/client/live/vip', {model: this.modelId}]);
        } else {
          // this.popupService.info(null,'LIVE VIP INDISPONIBLE',
          // `${this.modelPseudo} n\'a pas encore lanc le live en VIP.`);
          this.loading = true;
          this.socketService.clientInviteModelToVIP(this.idRoom, this.clientId, this.clientPseudo,
            this.modelId);
          setTimeout(() => {
              if (this.loading) this.loading = false;
          }, 10000);
        }
      }
    )
  }

  // Special TIPS
  liveTips() {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      (data) => {
        if (this.clientCredit <= 10) {
          this.popupService.info(null,'CREDIT INSUFFISANT',
          `Votre credit est insuffisant pour passer en live tips au moins 10 crdits.`);
          return false;
        }

        if (data.status !== "En tips") {
          this.popupService.info(null,'LIVE TIPS',
          `${this.modelPseudo} n'as pas encore lanc le live TIPS.`);
          return false;
        } else {
          // Debiter d'abord
          this.roomTipsService.debiterRoom(this.clientCreditId, 1).subscribe(
            (data) => {
              this.router.navigate(['/client/live/tips-clients', {model: this.modelId}]);
            }, (error) => {
              console.log(error);
            }
          )
        }
      }
    )
  }

    // Special CHOICE US
    liveChoiceUs() {
      this.profilService.getSpecificProfil(this.modelId).subscribe(
        (data) => {
          if (this.clientCredit <= 10) {
            this.popupService.info(null,'CREDIT INSUFFISANT',
            `Votre credit est insuffisant pour passer en live tips.`);
            return false;
          }

          if (data.status === "En vip") {
            this.popupService.info(null,'MODELE INDISPONIBLE',
            `Votre modle est indisponible pour passer le live.`);
            return false;
          } else {
            this.loading = true;
            this.socketService.clientInviteModelToVIP(this.idRoom, this.clientId, this.clientPseudo,
              this.modelId, 'live choice');
            setTimeout(() => {
                if (this.loading) this.loading = false;
            }, 10000);
          }
        }
      )
    }

  liveOut() {
    this.router.navigate(['/client/accueil/registered']);
  }

  buy() {
    this.router.navigate(['/client/achat_de_credit']);
  }

  invalideRoom() {
    this.popupService.info('/client/accueil/registered','CHAT INDISPONIBLE',
    `Ce chat est indisponible.`);
  }

  getAlbums() {
    const data = {
      modelId : this.modelId,
      type_album: 'free'
    }
    this.albumService.getModelAlbums(data).subscribe(
      (data: any) => {
        for (let i = 0; i < data.length; i++) {
          this.albums.push({
            url: data[i].path_album
          })
        }
        this.loading = false;
      }, (error) => {
        this.loading = false;
      }
    )
  }

  async getCostShow() {
    return await this.timerService.getCostShow(TypeTimer.PRIVATE).subscribe(
      (data: any) => {
        this.costShowPrivate = data.credit;
        this.timerService.getCostShow(TypeTimer.VIP).subscribe(
          (data: any) => {
            this.costShowVip = data.credit;
          }
        )
      }
    )
  }

  acceptedInvitation(special?: string) {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      async (data) => {
        if (!special && data.status === "En vip" && this.getInfoRoomVIP()) {
          this.router.navigate(['/client/live/vip', { model: this.modelId }]);
        } else if (special && data.status === "En live choice" && this.getInfoRoomVIP()) {
          this.router.navigate(['/client/live/choice-us', { model: this.modelId }]);
        } else {
          this.popupService.info(null, 'LIVE VIP INDISPONIBLE',
            `${this.modelPseudo} n\' a pas encore accept votre LIVE.`);
        }
      }
    );
  }

  async getInfoRoomVIP() {
    return await this.roomVipService.getRoomModel(this.modelId).subscribe(
      (data) => {
        if (data.idRoom === null) {return false;}
        if (this.clientId !== data.clientId) {return false;}
        return true;
      })
  }

      // -------- EMOJI -----------------

      toggleEmojiPicker() {
        this.showEmojiPicker = !this.showEmojiPicker;
      }

      addEmoji(event) {
        const {message} = this;
        let text = '';
        if (message) {
          text = `${message}${event.emoji.native}`;
        } else {
          text = `${event.emoji.native}`;
        }
        this.message = text;
      }

      focus() {
        this.showEmojiPicker = false;
      }

      // Tips

      selectTips(value: TIP) {
        // console.log('Tips ', value);
        this.loading = true;
        if (value.credit > this.clientCredit) {
          this.notificationService.infoMsg('Votre crdit est insuffisant');
          this.loading = false;
          return null;
        }
        this.creditService.buyGift(this.clientId, this.modelId, value.credit).
        subscribe(
          (data: any) => {
            console.log(data);
            this.clientCredit = data.creditClient;
            this.sendTips(value.symbole, value.credit, value.designation, value.vip);
          }, (error) => {
            console.log(error);
          }
        )

      }

      toggleTips() {
        this.showTips = !this.showTips;
      }

      sendTips(symbole: string, credit: number, designation, vip: boolean = false) {
        const data: Chat = {
          idRoom: this.idRoom,
          message: vip ? 'vip' : 'This is a gift',
          type_source: 'client',
          id_source: this.id,
          code_couleur: this.chatColor,
          giftSymbol: symbole,
          giftDesign: designation,
          giftSource: this.clientPseudo,
          pseudo: this.clientPseudo
        }
        const msg: ChatSocket = {
          room: this.idRoom + '',
          role: 'client',
          id: this.id,
          message: vip ? 'vip' : 'This is a gift',
          pseudo: this.clientPseudo,
          idRoom: this.idRoom,
          type_source: 'client',
          id_source: this.id,
          code_couleur: this.chatColor,
          giftSymbol: symbole,
          giftDesign: designation,
          giftSource: this.clientPseudo
        }
        this.socketService.sendMessage(msg);
        this.chatService.postMessage(data).subscribe(
          (data) => {
            // this.getMessages();
            this.loading = false;
            this.showTips = false;
          }
        )
      }


}

