import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/services/admin/admin.service';
import { NotificationService } from 'src/app/services/notification/notification.service';

@Component({
  selector: 'app-list-models-admin',
  templateUrl: './list-models-admin.component.html',
  styleUrls: ['./list-models-admin.component.scss']
})
export class ListModelsAdminComponent implements OnInit {
  icon = './../../../../../../assets/icons/paper-clip.svg'

  search = './../../../../../../assets/icons/admin-icons/search.png'
  four = './../../../../../../assets/icons/admin-icons/menu_four.png'
  other = './../../../../../../assets/icons/admin-icons/menu.png'

  deactivate = './../../../../../../assets/icons/admin-icons/deactivate.png'
  activate = './../../../../../../assets/icons/admin-icons/activate.png'
  delete = './../../../../../../assets/icons/admin-icons/delete.png'
  bloqued = './../../../../../../assets/icons/admin-icons/bloquer.png'
  debloquer = './../../../../../../assets/icons/admin-icons/debloquer.png'

  clients = []

  count: number  = 0
  range: number = 10
  filter: string = ''
  selected: string = 'tous'
  selectedStatus: string = 'tous'

  page: number  = 0
  topPage: number = 0
  thirdlyPage: number = 3
  list_pagination: number[] = []
  constructor(
    public adminService: AdminService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    this.countClient()
    this.listClients()
  }

  initPagination() {
    this.list_pagination = []
    let res = this.count / this.range
    res = Math.floor(res)
    this.topPage = res
    for (let i = 0; i <= res; i++) {
      this.list_pagination.push(i)
    }
  }

  countClient() {
    this.adminService.countModels(this.selected).subscribe(
      (data) => {
        this.count = data.count
        this.initPagination()
      }
    )
  }

  listClients() {
    this.adminService.getModels(this.selected, this.range, this.page, this.filter).subscribe(
      (data) => {
        // console.log(data)
        this.clients = data
        this.clients.forEach( (client) => {
          client.createdAt = this.adminService.formatDate(client.createdAt)
          client.status = client.profile ? client.profile.status : 'Hors ligne'
        })
        this.count = this.clients.length
        this.initPagination()
      }, (error) => {
        console.log(error)
      }
    )
  }

  select(event) {
    const value = event.target.value;
    this.selected = value
    this.listClients()
  }

  input_search(event) {
    this.listClients()
  }

  selectPage(page: number) {
    this.page = page
    this.listClients()
  }

  nextPage() {
    this.page += 1
    this.thirdlyPage += 3
    this.listClients()
  }

  prevPage() {
    this.page -= 1
    this.thirdlyPage -= 3
    if (this.thirdlyPage < 0) this.thirdlyPage = 0
    this.listClients()
  }

  tout() {
    this.page = 0
    this.range = this.count
    this.listClients()
  }

  deleteClient(idClient: number) {
    this.adminService.deleteModel(idClient).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error)
      }
    )
  }

  blockClient(idClient: number) {
    const data = {
      idClient,
      reverse: false
    }
    this.adminService.blockModel(data).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error);
      }
    )
  }

  deactivateClient(idClient: number) {
    this.adminService.deactivateModel(idClient).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error)
      }
    )
  }

  activateClient(idClient: number) {
    this.adminService.activateModel(idClient).subscribe(
      (data: any) => {
        if (data.success) this.listClients()
        else if (data.error) this.notificationService.errorMsg(data.message)
      }, (error) => {
        console.log(error)
      }
    )
  }

  selectStatus(event) {
    const value = event.target.value;
    this.selectedStatus = value
    if (value === 'tous') {
      this.listClients()
    } else {
      this.listStatusClients()
    }
  }

  listStatusClients() {
    this.adminService.getStatusModel(this.selectedStatus, this.filter).subscribe(
      (data: any) => {
        // console.log(data)
        this.clients = data
        this.clients.forEach( (client) => {
          client.createdAt = this.adminService.formatDate(client.createdAt)
          client.status = client.profile ? client.profile.status : 'Hors ligne'
        })
        this.count = this.clients.length
        this.initPagination()
      }, (error) => {
        console.log(error)
      }
    )
  }
}
