import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef,
  IterableDiffers} from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { NavigationExtras, Router, RouterStateSnapshot, ActivatedRoute } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { ModelService } from '../../../services/model/model.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { ChatService } from '../../../services/chat/chat.service';
import { Chat, ChatSocket } from 'src/app/interfaces/chat.interface';
import { RoomService } from '../../../services/room/room.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { RoomPrivateService } from '../../../services/roomPrivate/room-private.service';
import { Subscription } from 'rxjs';
import { NotificationService } from '../../../services/notification/notification.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { CreditService } from '../../../services/credit/credit.service';
import { InfoChat, TypeInfoChat } from 'src/app/interfaces/info-chat.interface';
import * as $ from 'jquery';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import { TimerService } from '../../../services/timer/timer.service';
import * as AOS from 'aos';
import { PopupService } from '../../../services/popup/popup.service';
import { isBoolean } from 'util';
import { SettingTipsComponent } from '../setting-tips/setting-tips.component';
import { MatDialog } from '@angular/material/dialog';
import { RoomTipsService } from '../../../services/roomTips/room-tips.service';


interface Invitation {
  clientId: number,
  clientPseudo: string,
  roomId: any,
  special?: string
}

@Component({
  selector: 'app-live-chat-model',
  templateUrl: './live-chat-model.component.html',
  styleUrls: ['./live-chat-model.component.scss', './../../live/live.scss']
})
export class LiveChatModelComponent implements OnInit, OnDestroy {
  context: string = "CHOICE US";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/live-detail.png";
  close = "assets/icons/close.svg";

  info = {
    bg : "assets/logos/dark-light.jpg",
    message : '',
    idRoom: 0,
    pseudo: '',
    chatColor:'#FB77FC',
    modelId: 0,
    actif: 0,
    credit: 0
  }

  roomId = null

  messages: Chat[] = [];

  toggleDot: boolean = false;

  joinSub = new Subscription();
  leaveSub = new Subscription();
  invitePrivateSub = new Subscription();
  messageSub = new Subscription();
  inviteVIPSub = new Subscription();

  loading: boolean = false;

  listInvitation: Invitation[] = [];
  // @HostListener("window:beforeunload", ["$event"]) unloadHandler(event: Event) {
  //   this.socketService.leave(this.info.idRoom, '');
  // }

  showEmojiPicker = false;
  sets = [
    'native',
    'google',
    'twitter',
    'facebook',
    'emojione',
    'apple',
    'messenger'
  ]
  set = 'google';

  statPrivate = {
    tarif_show: 0,
    time_show: 0
  }

  statVIP = {
    tarif_show: 0,
    time_show: 0
  }

  // WEB RTC VIDEO
  @ViewChild('video_live_model', {static: true}) video: ElementRef<HTMLVideoElement>;

  infos: InfoChat[] = []
  iterableDiffer: any;

  sound_notification: boolean = false;
  sound_message: boolean = false;

  // Salon libre
  live_selected = null;
  selected_orig = null;

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private store: LocalStorageService,
    private modelService: ModelService,
    private chatService: ChatService,
    private roomService: RoomService,
    private roomPrivateService: RoomPrivateService,
    private socketService: SocketService,
    private notificationService: NotificationService,
    private roomVipService: RoomVipService,
    private creditService: CreditService,
    private iterableDiffers: IterableDiffers,
    private profileService: ProfilService,
    private timerService: TimerService,
    private popupService: PopupService,
    private dialog: MatDialog,
    private roomTipsService: RoomTipsService) {
      this.iterableDiffer = iterableDiffers.find([]).create(null);
  }

  ngOnInit(): void {
    AOS.init();
    this.verifySound();
    this.getInfo();
    this.getCredit();
    // this.initLiveVideo();
    this.getLastSaloonFree();
  }

  getLastSaloonFree() {
    const lastTypeSaloon = this.store.get('live_selected');
    if(lastTypeSaloon) {
      this.live_selected = lastTypeSaloon;
    } else {
      this.live_selected = null;
    }
  }

  ngDoCheck() {
    let changes = this.iterableDiffer.diff(this.infos);
    if (changes) {
      $("#message_list_info").animate({ scrollTop: $('#message_list_info').prop("scrollHeight")}, 10);
    }
  }

  initInfoChat(pseudoModel: string) {
    const info: InfoChat = {
      label: TypeInfoChat.info,
      message: `> Bienvenue ${pseudoModel}`
    }
    this.infos.push(info)
    this.infos.push({
      label: TypeInfoChat.info,
      message: '_   vous tes actuellement en free chat avec vos clients favoris, '
    })
    this.infos.push({
      label: TypeInfoChat.info,
      message: '_   dans ce salon de discussion, votre camra ne sera pas activ , '
    })
    this.infos.push({
      label: TypeInfoChat.info,
      message: '_   discutez avec eux et incitez les  passer en live avec vous.'
    })
    this.infos.push({
      label: TypeInfoChat.info,
      message: '_  Choisissez votre type de salon libre.'
    })
    this.showMessage(this.live_selected);
  }

  joinChat(pseudoClient) {
    if(!pseudoClient) return null
    const info: InfoChat = {
      label: TypeInfoChat.join,
      message: `> ${pseudoClient} vient de rejoindre le chat`
    }
    this.infos.push(info)
  }

  leaveChat(pseudoClient) {
    if(!pseudoClient) return null
    const info: InfoChat = {
      label: TypeInfoChat.leave,
      message: `> ${pseudoClient} a quitt le chat`
    }
    this.infos.push(info)
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data: any) => {
        // console.log(data)
        this.info.credit = data.credit;
        this.getCostPrivateShow()
        this.getCostVIPShow()
      }
    )
  }

  async getCostPrivateShow() {
    return await this.timerService.getCostShow(TypeTimer.PRIVATE).subscribe(
      (data: any) => {
        // console.log("Show Cost ", data)
        this.statPrivate.tarif_show = data.credit? data.credit : 0;
        this.statPrivate.time_show = data.second ? data.second : 0;
      }
    )
  }

  async getCostVIPShow() {
    return await this.timerService.getCostShow(TypeTimer.VIP).subscribe(
      (data: any) => {
        // console.log("Show Cost ", data)
        this.statVIP.tarif_show = data.credit? data.credit : 0;
        this.statVIP.time_show = data.second ? data.second : 0;
      }
    )
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    // this.joinSub.unsubscribe();
    // this.leaveSub.unsubscribe();
    // this.messageSub.unsubscribe();
    this.invitePrivateSub.unsubscribe();
    this.inviteVIPSub.unsubscribe();
  }

  verifySound() {
    this.profileService.getInfo().subscribe(
      (data: any) => {
        // console.log(data.setting);
        this.sound_notification = data.setting.sound_notification === 1 ? true : false;
        this.sound_message = data.setting.sound_message === 1 ? true : false;
        // console.log(this.sound_notification);
      }
    )
  }


  initSocket() {
    if (!this.roomService.joinedRoom()) {
      this.roomService.joinRoom();
      this.socketService.join(this.info.idRoom, this.info.modelId);
    }

    this.socketService.listen(`ask currentSaloon ${this.info.idRoom} ${this.info.modelId}`).subscribe(
      (data: any) => {
        this.socketService.sendCurrentSaloon(this.info.idRoom, this.live_selected);
      }
    );

    this.joinSub = this.socketService.listen(`joined ${this.info.idRoom}`).subscribe(
      (data: any) => {
        // console.log('joined ', data);
        this.info.actif = data.count < 1 ? 1 : data.count;
        this.joinChat(data.clientPseudo);
        if (this.sound_notification) {
          this.socketService.soundIncome();
        }
      }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.info.idRoom}`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        this.info.actif = data.count < 1 ? 1 : data.count;
        this.leaveChat(data.clientPseudo)
        if (this.sound_notification) {
          this.socketService.soundOutcome();
        }
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.info.idRoom}`).subscribe(
      (data: Chat) => {
        // console.log('Message from client ', data);
        this.messages.push(data);
        // this.getMessages();
      }
    );

    this.invitePrivateSub = this.socketService.listen(`invite model to private ${this.info.idRoom} ${this.info.modelId}`).subscribe(
      (data: {roomId: number, clientId: number, clientPseudo: string, modelId: number}) => {
      // console.log('Invitation to private ', data)
      this.notificationService.invitationPrivate('LIVE PRIVE', 'Un client y participe !');
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
        this.socketService.responseInvitationModelToPrivate(this.info.idRoom, data.clientId,
          this.info.modelId, this.info.pseudo)
        this.launchLive(true);
      }, 3000);
      }
    );

    this.inviteVIPSub = this.socketService.listen(`invite model to vip ${this.info.idRoom} ${this.info.modelId}`).subscribe(
      (data: {roomId: any, clientId: number, clientPseudo: string, modelId: number, special?: string}) => {
        // console.log('Invitation to vip ', data)
        this.addInvitationToVIP(data.clientId, data.clientPseudo, data.roomId, data.special);
      }
    );
  }

  getInfo() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log(data);
        if (data.profile.status === 'En vip') {
          this.router.navigate(['/modele/live-vip']);
        }
        if (data.profile.status === 'En live') {
          this.router.navigate(['/modele/live-private']);
        }
        this.info.bg = data.path_soft;
        this.info.modelId = data.id;
        this.info.pseudo = data.pseudo;
        this.getInfoRoom();
        this.initInfoChat(this.info.pseudo)
      }
    )
  }

  getInfoRoom() {
    this.roomService.getRoomModel(this.info.modelId).subscribe(
      (data) => {
        // console.log('Model room ', data);
        this.info.idRoom = data.idRoom;
        this.roomId = data.idRoom;
        this.info.actif = data.actif < 1 ? 1 : data.actif;
        this.getMessages();
        this.initSocket();
      }
    )
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    // console.log(this.toggleDot);
  }

  sendMessage() {
    const data: Chat = {
      idRoom: this.info.idRoom,
      message: this.info.message,
      type_source: 'model',
      id_source: this.info.modelId,
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo
    }
    const msg: ChatSocket = {
      room: this.info.idRoom + '',
      role: 'model',
      id: this.info.modelId,
      message: this.info.message,
      idRoom: this.info.idRoom,
      type_source: 'model',
      id_source: this.info.modelId,
      code_couleur: this.info.chatColor,
      pseudo: this.info.pseudo,
      createdAt: new Date().toISOString()
    }
    this.socketService.sendMessage(msg);
    this.chatService.postMessage(data).subscribe(
      (data) => {
        // this.getMessages();
        this.info.message = null;
      }
    )
  }

  getMessages() {
    this.chatService.getMessage(this.info.idRoom).subscribe(
      (data) => {
        // console.log(data);
        this.messages = data;
      }
    )
  }

  async launchLive(invitedPrivate: boolean = false) {
    if (this.live_selected === 'live_private' || invitedPrivate) {
      this.loading = true;
      return this.roomPrivateService.createRoom().subscribe(
        async (data) => {
          await this.askLeave().then(
            (data) => {
              this.store.set("privateRoomId", data.room);
              this.router.navigate(['/modele/live-private']);
              this.loading = false;
            }
          )
        }
      );
    }

    if (!this.live_selected || this.live_selected === "null") {
      this.popupService.info(null, 'CHOIX DU LIVE',
      `Veuillez choisir le live`);
      return null;
    }

    if (this.live_selected === 'live_vip') {
      this.popupService.info(null, 'LIVE VIP',
      `Live disponible sur invitation abonn`);
      return null;
    }

    if (this.live_selected === 'choice_us') {
      this.popupService.info(null, 'CHOICE US',
      `Live disponible sur invitation abonn`);
      return null;
    }

    if (this.live_selected === 'live_tips') {
      // create popup to complete informations
      const dialogRef = this.dialog.open(SettingTipsComponent, {
        width: '100vh',
        height: '90vh',
        data: {}
      });

      dialogRef.afterClosed().subscribe((result: any) => {
        this.loading = true;
        if (result && result.success === true){
          this.roomTipsService.createRoom(result.tips, result.description).subscribe(
            (data) => {
              // console.log(data);
              this.askLeaveTips();
              this.store.set("tipsRoomId", data.room);
              this.router.navigate(['/modele/live-tips-model']);
              this.loading = false;
            }, (error) => {
              console.log(error);
            }
          )
        } else {
          this.loading = false;
        }
      });

    }

  }

  async askLeave() {
    const data = {
      room: this.info.idRoom + '',
      role: 'model',
      id: this.info.modelId,
      pseudo: this.info.pseudo,
      message: 'I pass to private live'
    }
    return await this.socketService.emit('Pass to private', data);
  }

  async askLeaveTips() {
    const data = {
      room: this.info.idRoom + '',
      role: 'model',
      id: this.info.modelId,
      pseudo: this.info.pseudo,
      message: 'I pass to live tips'
    }
    return await this.socketService.emit('Pass to tips', data);
  }


  async askLeaveVIP() {
    const data = {
      room: this.info.idRoom,
      role: 'model',
      id: this.info.modelId,
      pseudo: this.info.pseudo,
      message: 'I pass to live VIP'
    }
    return await this.socketService.emit('Pass to VIP', data);
  }

  addInvitationToVIP(clientId: number, clientPseudo: string, roomId: any, special?: string) {
    const data: Invitation = {
      clientId, clientPseudo, roomId, special
    }
    let exist = false;
    this.listInvitation.forEach(item => {
      if (item.clientId === clientId) {
        exist = true
      }
    })
    if (!exist) this.listInvitation.push(data)
    // console.log(data)
  }

  closeInvitation(clientId: number, clientPseudo, roomId) {
    const index = this.listInvitation.indexOf({clientId, clientPseudo, roomId});
    this.listInvitation.splice(index, 1);
  }

  acceptInvitationToVIP(clientId: number, clientPseudo: string, roomId: any, special?: string) {
    this.closeInvitation(clientId, clientPseudo, roomId);
    this.liveVIP(clientId, roomId, special);
  }

  denyInvitationToVIP(clientId: number, clientPseudo: string, roomId: any, special?: string) {
    this.socketService.responseNegativeInvitationModelToVIP(roomId, clientId, this.info.modelId, this.info.pseudo, special)
    this.closeInvitation(clientId, clientPseudo, roomId)
  }

  async liveVIP(clientId: number, roomId: any, special?: string) {
      this.loading = true;
      this.roomVipService.createRoom(clientId, special).subscribe(
        (data) => {
          this.store.set("vipRoomId", data.room);
          const msg = {
            roomId: roomId,
            role: 'model',
            clientId: clientId,
            message: 'Let\'s go to VIP',
            roomVIP: data.room + 'V'
          }
          this.askLeaveVIP().then(
            (data) => {
              this.loading = false;
              this.socketService.responsePositiveInvitationModelToVIP(roomId, clientId, this.info.modelId, this.info.pseudo, special);
              if (!special) {
                this.router.navigate(['/modele/live-vip']);
              } else if(special && special === 'live choice') {
                this.router.navigate(['/modele/live-choice-us']);
              }
            }
          )
        }
      )
  }

      // WEB RTC - LIVE VIDEO
      initLiveVideo() {
        this.onStart();
      }

      onStart(){
        if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
          navigator.mediaDevices.getUserMedia({video: true, audio: true}).then((ms: MediaStream) => {
            const _video = this.video.nativeElement;
            _video.srcObject = ms;
            _video.play();
          });
        }
      }

      onStop() {
        if (this.video && this.video.nativeElement.srcObject) {
          this.video.nativeElement.pause();
          if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
            (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
          }
          this.video.nativeElement.srcObject = null;
        }
      }


  // -------- EMOJI -----------------

  toggleEmojiPicker() {
    this.showEmojiPicker = !this.showEmojiPicker;
  }

  addEmoji(event) {
    const {info} = this;
    console.log(this.info.message);
    console.log(`${event.emoji.native}`);
    let text = '';
    if (this.info.message) {
      text = `${info.message}${event.emoji.native}`;
    } else {
      text = `${event.emoji.native}`;
    }
    this.info.message = text;
    console.log(this.info.message)
    // this.showEmojiPicker = false;
  }

  focus() {
    this.showEmojiPicker = false;
  }

  changeSalonLibre(event) {
    const choice = event.target.value;
    this.store.set('live_selected', choice);
    this.showMessage(choice);
    this.socketService.sendCurrentSaloon(this.info.idRoom, this.live_selected);
  }

  showMessage(choice: string) {
    if (choice == 'live_tips') {
      this.infos.push({
        label: TypeInfoChat.info,
        message: '_   Vous tes pass en Salon libre - LIVE TIPS : paiement par tips.'
      })
    } else if (choice == 'choice_us') {
      this.infos.push({
        label: TypeInfoChat.info,
        message: '_   Vous tes pass en Salon libre - CHOICE US : changement de taux de crdit.'
      })
      this.infos.push({
        label: TypeInfoChat.leave,
        message: '_   Live CHOICE US : Disponible sur invitation des abonns.'
      })
    } else if (choice == 'live_private') {
      this.infos.push({
        label: TypeInfoChat.info,
        message: '_   Vous tes pass en Live Priv avec 5 crdits / mn.'
      })
    } else if (choice == 'live_vip') {
      this.infos.push({
        label: TypeInfoChat.info,
        message: '_   Vous tes pass en Live VIP avec un taux de 15 crdits / mn'
      })
      this.infos.push({
        label: TypeInfoChat.leave,
        message: '_   Live VIP : Disponible sur invitation des abonns.'
      })
    }
  }

}

