import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash';
import { AdminService } from '../../../../../services/admin/admin.service';

@Component({
  selector: 'app-statistique-clients-admin',
  templateUrl: './statistique-clients-admin.component.html',
  styleUrls: ['./statistique-clients-admin.component.scss']
})
export class StatistiqueClientsAdminComponent implements OnInit {
  years : number[] = []

  options: any;
  updating: boolean = false;
  inscriptions = []
  original = []
  currentYear: number = new Date().getFullYear()
  currentMonth: string = '01'

  isIntegral: boolean = false

  constructor(
    private adminService: AdminService
  ) {
    this.options = {
      autoSize: true,
      data: this.getData(),
      series: [
        {
          xKey: 'date',
          yKey: 'count',
          yName: 'Inscriptions',
          stroke: '#FB77FC',
          marker: {
            size: 2,
            fill: '#FB77FC',
            stroke: '#FB77FC',
          },
        }
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          nice: false,
          // tick: { count: agCharts.time.second.every(3), color:  "rgb(0, 0, 0)"},
          tick: { count: 0,color:  "rgb(0, 0, 0)"},
          label: { format: '%m/%d/%Y', color:  "rgb(0, 0, 0)" },
        },
        {
          type: 'number',
          position: 'left',
          nice: false,
          tick: { count: 1,color:  "rgb(0, 0, 0)"},
          label: { format: '#{.0f}', color:  "#000"}
        }
      ],
      background: {
        fill: "#ffffff5f",
        visible: true
      },
      tooltip: {
        enabled: true
      },
      navigator: {
        enabled: true
      },
      title: { text: '' },
      legend: {
        position: 'bottom',
        enabled: true,
        item: {
          label: {
            color: 'black'
          }
        }
      }
    };
  }

  ngOnInit(): void {
    this.initYears();
    this.startUpdates();
    this.useDefaultGridStyle();
    this.getInscriptions()
  }

  initYears() {
    const initial = 2021;
    for(let i = 0; i < 50; i++ ) {
      this.years.push(initial+i)
    }
  }

  useDefaultGridStyle = () => {
    const options = cloneDeep(this.options);

    var gridStyle = [
      {
        stroke: 'white',
        lineDash: [2, 1],
      },
    ];
    options.axes[0].gridStyle = gridStyle;
    options.axes[1].gridStyle = gridStyle;

    this.options = options;
  };

  update = () => {
    const options = cloneDeep(this.options);

    options.data = this.getData();

    this.options = options;
  };

  startUpdates = () => {
    if (this.updating) {
      return;
    }
    this.updating = true;
    this.update();
    setInterval(this.update, 1000);
  };

  getData() {
    this.updating = false;
    return this.inscriptions;
  }

  getInscriptions() {
    this.adminService.getStatInscriptionClient().subscribe(
      (data: any) => {
        let tmp = data
        tmp.forEach(inscrit => {
          inscrit.date = this.adminService.formatDate(inscrit.date)
          inscrit.count = parseInt(inscrit.count)
        })
        this.inscriptions = tmp
        this.original = tmp
      }, (error) => {
        console.log(error)
      }
    )
  }

  selectMonth(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentMonth = value
    const search = this.currentMonth+'/'+this.currentYear
    this.inscriptions =  this.original.filter(element => {
      const year = element.date.substring(3, 10)
      if (year === search) return true
      return false
    })
  }

  selectYear(event) {
    this.isIntegral = false
    const value = event.target.value;
    this.currentYear = value
    this.inscriptions =  this.original.filter(element => {
      const year = element.date.substring(6, 10)
      if (year === this.currentYear) return true
      return false
    })
  }

  integral() {
    this.isIntegral = !this.isIntegral
    if (this.isIntegral) {
        this.inscriptions = this.original
    } else {
        const search = this.currentMonth+'/'+this.currentYear
        this.inscriptions =  this.original.filter(element => {
          const year = element.date.substring(3, 10)
          if (year === search) return true
          return false
      })
    }
  }
}
