    getActifRoom() {
      // console.log('Id room ', this.idRoom);
      this.roomPrivateService.getActifRoom(this.idRoom).subscribe(
        (data: any) => {
          // console.log(data);
          const actifs: any[] = data;
          actifs.forEach(actif => {
            if (actif.id && actif.peerId) {
              this.callPeerClient(actif.id, actif.peerId);
            }
          });
        }, (error) => {
          console.log(error);
        }
      )
    }

    removeStream(clientId: number) {
      console.log('Remove stream ', clientId);
      const index = this.clientStream.findIndex(element => element.clientId === clientId);
      this.clientStream.splice(index, 1);
    }

    toggleAudioClientStream(peerId: any, isAudio: boolean) {
      this.clientStream.forEach(
        (stream) => {
          if (stream.peerId === peerId) {
            stream.isAudio = isAudio
          }
        }
      )
    }

    toggleVideoClientStream(peerId: any, isVideo: boolean) {
      this.clientStream.forEach(
        (stream) => {
          if (stream.peerId === peerId) {
            stream.isVideo = isVideo
          }
        }
      )
    }

    toggleAudioModelStream(isAudio: boolean) {
      if (isAudio) {
        $('#video_live_model').prop('volume', 1)
      } else {
        $('#video_live_model').prop('volume', 0)
      }
    }

    toggleVideoModelStream(isVideo: boolean) {
      var filterVal = 'blur(0px)';
      var filterFull = 'blur(200px)';
      if (isVideo) {
        $('#video_live_model')
        .css('filter',filterVal)
        .css('webkitFilter',filterVal)
        .css('mozFilter',filterVal)
        .css('oFilter',filterVal)
        .css('msFilter',filterVal);
      } else {
        $('#video_live_model')
        .css('filter',filterFull)
        .css('webkitFilter',filterFull)
        .css('mozFilter',filterFull)
        .css('oFilter',filterFull)
        .css('msFilter',filterFull);
      }
    }

    toggleVideo() {
      this.showVideo = !this.showVideo;
      if (this.showVideo) {
        this.lazyStream.getVideoTracks().forEach(
          (track) => {
            track.enabled = true;
          }
        );
      } else {
        this.lazyStream.getVideoTracks().forEach(
          (track) => {
            track.enabled = false;
          }
        );
      }
      this.clientStream.forEach(
        (stream) => {
          if (stream.clientId === this.clientId) {
            stream.isVideo = this.showVideo
          }
        }
      )
      // this.onStop();
      // this.onStart();
      const room = this.idRoom+'P';
      this.socketService.toggleVideo(room, this.clientId, this.peerId, null, this.showVideo);
    }

    toggleAudio() {
      this.showAudio = !this.showAudio;
          if (!this.showAudio) {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    } else {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    }
      this.clientStream.forEach(
        (stream) => {
          if (stream.clientId === this.clientId) {
            stream.isAudio = this.showAudio
          }
        }
      )
      // this.onStop();
      // this.onStart();
      const room = this.idRoom+'P';
      this.socketService.toggleAudio(room, this.clientId, this.peerId, null, this.showAudio);
    }

