import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { GalleryItem, Gallery, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { ModelService } from 'src/app/services/model/model.service';
import { addAlbum } from '../../../interfaces/add-album.interface';
import { AlbumService } from '../../../services/album/album.service';
import { Title, Meta } from '@angular/platform-browser';

const data = [
  {
    path_album: 'https://res.cloudinary.com/team-stenny/image/upload/v1627759167/light_fumemz.jpg',
  }
];

@Component({
  selector: 'app-home-live',
  templateUrl: './home-live.component.html',
  styleUrls: ['./home-live.component.scss'],
})
export class HomeLiveComponent implements OnInit {
  title = 'BetweenUs Live - Sex cam';

  defaultImage = './../../../../assets/images/blured.jpg';
  mainModel = {
    path_soft: null,
    pseudo: '',
    id: 0
  };

  allLive = [];
  other = [];

  currentAlbum : addAlbum[] = [];

  items: GalleryItem[];

  imageData = data;

  loading: boolean = false;

  // SEO
  name = "BetweenUs Live";
  angular = "Angular";
  bootstrap = "Bootstrap4";

  constructor(
    private modelSrv: ModelService,
    private albumService: AlbumService,
    public gallery: Gallery,
    public lightbox: Lightbox,
    private titleService: Title,
    private metaTagService: Meta
    ) {
  }

  ngOnInit(): void {
    this.titleService.setTitle(this.title);

    this.metaTagService.addTags([
      { name: 'keywords', content: 'betweenUs, Betweenus, betweenus live, bul, BUL, sex live' },
      { name: 'robots', content: 'index, follow' },
      { name: 'author', content: 'Dev BUL' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' },
      { name: 'date', content: '2021-12-22', scheme: 'YYYY-MM-DD' },
      { charset: 'UTF-8' }
    ]);

    this.metaTagService.updateTag(
      { name: 'description', content: 'Web Platform betweens models and clients for a live sex in cam' }
    );

    this.init();
    this.getLive();
  }


  init() {
    // Creat gallery items
    this.items = this.imageData.map(item => new ImageItem({ src: item.path_album, thumb: item.path_album }));
    /** Lightbox Example */
    // Get a lightbox gallery ref
    const lightboxRef = this.gallery.ref('lightbox');

    // Add custom gallery config to the lightbox (optional)
    lightboxRef.setConfig({
      imageSize: ImageSize.Cover,
      thumbPosition: ThumbnailsPosition.Top
    });
    // Load items into the lightbox gallery ref
    lightboxRef.load(this.items);
  }

  getLive() {
    this.loading = true;
    this.modelSrv.getLive().subscribe(
      (data) => {
        this.allLive = data;
        this.getNoLive();
        // console.log("Live ", this.allLive);
      }, (error) => {
        this.loading = false;
      }
    )
  }

  getNoLive() {
    this.modelSrv.getNoLive().subscribe(
      (data) => {
        this.other = data;
        this.loading = false;
        let random = 0;
        if (this.allLive.length > 0) {
          random = Math.floor(Math.random() * (this.allLive.length));
          random = random < -1 ? 0 : random;
          this.mainModel = this.allLive[random];
        } else {
          random = Math.floor(Math.random() * (this.other.length));
          random = random < -1 ? 0 : random;
          this.mainModel = this.other[random];
        }
      }, (error) => {
        this.loading = false;
      }
    )
  }

  seeModel(modelId: number, path_soft: string) {
    this.imageData = [];
    const data = {
      modelId,
      type_album: 'free'
    }
    this.albumService.getModelAlbums(data).subscribe(
      (data: any) => {
        const path_album = {
          path_album : path_soft // Profil Model
        }

        this.imageData = data;
        this.imageData.push(path_album);
        // console.log("Image dada ", this.imageData);
        this.init();
      }
    )
  }
}
