import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { NotificationService } from '../../../../../../services/notification/notification.service';
import { MatDialog } from '@angular/material/dialog';
import { MotifRejectComponent } from './motif-reject/motif-reject.component';
import { isBoolean } from 'util';

@Component({
  selector: 'app-detail-model-admin',
  templateUrl: './detail-model-admin.component.html',
  styleUrls: ['./detail-model-admin.component.scss']
})
export class DetailModelAdminComponent implements OnInit {
  defaultImage = './../../../../../../../assets/images/blured.jpg';

  idClient: number  = null
  client: any = {
    firstname : '',
    lastname : '',
    date_birth : '',
    path_recto : './../../../../../../../assets/images/blured.jpg',
    path_verso : './../../../../../../../assets/images/blured.jpg',
    path_soft : './../../../../../../../assets/images/blured.jpg',
    path_cin : './../../../../../../../assets/images/blured.jpg',
    state : ''
  }

  loading: boolean = false

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private adminService: AdminService,
    private notificationService: NotificationService,
    private dialog: MatDialog
  ) {
    const idClient = this.route.snapshot.params.idClient
    this.idClient = parseInt(idClient)
    // console.log(this.idClient)
  }

  ngOnInit() {
    if (!this.idClient) {
      this.router.navigate(['/admin/les-demandes-modeles'])
      return false
    }
    this.getInfos()
  }

  getInfos() {
    this.loading = true
    this.adminService.getInfosModel(this.idClient).subscribe(
      (data) => {
        this.loading = false
        // console.log(data)
        this.client = data
      }, (error) => {
        this.loading = false
        console.log(error)
      }
    )
  }

  accept(accepted: boolean) {

    if (accepted) {
      this.loading = true
      this.adminService.acceptInscriptionModel(this.idClient, true, 'valid').subscribe(
        (data) => {
          this.loading = false
          this.notificationService.info('Inscription', 'Inscription valid')
          this.router.navigate(['/admin/les-demandes-modeles'])
        }, (error) => {
          this.loading = false
          console.log(error)
        }
      )
    } else {
      const dialogRef = this.dialog.open(MotifRejectComponent, {
        width: '50vh',
      });

      dialogRef.afterClosed().subscribe(result => {
        // console.log(result);
        if (isBoolean(result)) {
          // console.log('Boolean iz io')
        } else {
          this.loading = true
          this.adminService.acceptInscriptionModel(this.idClient, false, result).subscribe(
            (data) => {
              this.loading = false
              this.notificationService.success('Inscription', 'Inscription rejet')
              this.router.navigate(['/admin/les-demandes-modeles'])
            }, (error) => {
              console.log(error)
            }
          )
        }
      });
    }
  }

}
