import { Component, Input, OnInit } from '@angular/core';
import { GalleryItem, Gallery, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { NotificationService } from '../../../services/notification/notification.service';

const data = [
  // {
  //   url : 'https://res.cloudinary.com/team-stenny/image/upload/v1627759167/light_fumemz.jpg'
  // }
];


@Component({
  selector: 'app-album-live',
  templateUrl: './album-live.component.html',
  styleUrls: ['./album-live.component.scss']
})
export class AlbumLiveComponent implements OnInit {
  @Input()
  albums: any[] = [];

  showed: boolean = false;
  loading: boolean = false;

  items: GalleryItem[];

  imageData = data;

  constructor(
    public gallery: Gallery,
    public lightbox: Lightbox,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    this.init()
  }

  init() {
    this.items = this.imageData.map(item => new ImageItem({ src: item.url, thumb: item .url}));
    const lightboxRef = this.gallery.ref('lightbox');
    lightboxRef.setConfig({
      imageSize: ImageSize.Cover,
      thumbPosition: ThumbnailsPosition.Top
    });
    lightboxRef.load(this.items);
  }

  hoverImg() {
    this.imageData = [];
    this.albums.forEach(
      (album) => {
        this.imageData.push({url: album.url}
      );
      }
    )
    this.init()
  }

  show() {
    this.showed = !this.showed;
    if (this.albums.length == 0) {
      this.notificationService.infoMsg('Aucun album disponible');
    }
    // if (this.showed) {
    //   this.loading = true;
    //   setTimeout(() => {
    //     this.loading = false
    //   }, 500);
    // }
  }

}
