import { isPlatformBrowser } from '@angular/common';
import { Component, ElementRef, Inject, OnInit, PLATFORM_ID, ViewChild } from '@angular/core';
import * as $ from 'jquery';
// import AgoraRTC from 'agora-rtc-sdk-ng';
import { NotificationService } from '../../../../services/notification/notification.service';

declare var initSettingVideoModel: any;

@Component({
  selector: 'app-try-webcam',
  templateUrl: './try-webcam.component.html',
  styleUrls: ['./try-webcam.component.scss']
})
export class TryWebcamComponent implements OnInit {
  loading: boolean = false;
  showVideo: boolean = true;
  showAudio: boolean = true;
  lazyStream: MediaStream;
  img = "assets/logos/dark-light.jpg";

  @ViewChild('video_live_model', {static: true}) video: ElementRef<HTMLVideoElement>;

  @ViewChild('map') map;
  private isVisible: boolean = false;

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    $("#video_live_model").autoplay = true;
    // const client = AgoraRTC.createClient({ codec: 'h264', mode: 'rtc' });
    // client.init('7ccc3498185144be9e844555f6da1a85');
  }

  ngOnDestroy(): void {
    this.onStop();
  }

  ngAfterContentChecked(): void
  {
    setTimeout(
      () => {
        if (this.isVisible == false && this.map.nativeElement.offsetParent != null)
        {
            this.onStart();
            this.isVisible = true;
            initSettingVideoModel();
            // var localStream = AgoraRTC.createStream({ audio: true, video: true })
            // localStream.init();
            // localStream.play(elementID);
            $('.video_live_model').trigger('play');
        }
        else if (this.isVisible == true && this.map.nativeElement.offsetParent == null)
        {
            this.onStop();
            this.isVisible = false;
        }
      }, 1000
    )
  }

  onStart(){
    this.loading = true;
    if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
      navigator.mediaDevices.getUserMedia({video: true, audio: false}).then((ms: MediaStream) => {
        this.lazyStream = ms;
        this.video.nativeElement.srcObject = this.lazyStream;
        // this.lazyStream.getVideoTracks()[0].onended = function(event) {
        //   console.log('inactive cam');
        // }
        this.loading = false;
        // this.lazyStream.getVideoTracks().forEach(
        //   (track) => {
        //     this.showVideo = false;
        //     track.enabled = false;
        //     this.notificationService.infoMsg('Votre camra est dsactiv par defaut');
        //   }
        // );
      });
    }
  }

  onStop() {
    if (this.lazyStream) {
      this.lazyStream.getTracks().forEach(
        (track) => {
          track.stop();
          // track.dispatchEvent(new Event("ended"));
        }
      );
      this.lazyStream = null;
    }

    // if (this.video && this.video.nativeElement.srcObject) {
    //   this.video.nativeElement.pause();
    //   if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
    //     (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
    //   }
    //   this.video.nativeElement.srcObject = null;
    // }
  }

  toggleVideo() {
    this.showVideo = !this.showVideo;
    if (this.showVideo) {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    } else {
      this.lazyStream.getVideoTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    }
  }

  toggleAudio() {
    this.showAudio = !this.showAudio;
    if (!this.showAudio) {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = false;
        }
      );
    } else {
      this.lazyStream.getAudioTracks().forEach(
        (track) => {
          track.enabled = true;
        }
      );
    }
  }

}
