import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

interface loginAdmin {
  email,
  password
}

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class AdminService {
  endpoint = environment.endpoint + '/admin'

  constructor(
    private http: HttpClient
  ) { }

  formatDate(date: string) {
    let year = date.substring(0, 4);
    let month = date.substring(5, 7);
    let day = date.substring(8, 10);
    let hour = date.substring(11, 16);
    const result = day+'/'+month+'/'+year+' '+hour;
    return result;
  }

  login(user : loginAdmin) : Observable<any>{
    return this.http.post(`${this.endpoint}/login`, user, httpOptions)
  }

  countClients(motif: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/count-clients/${motif}`, httpOptions)
  }

  getClients(motif: string, range: number, page: number, filter: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/list-clients/${motif}/${range}/${page}/${filter}`, httpOptions)
  }

  deleteClient(idClient: number) {
    return this.http.delete(`${this.endpoint}/client/${idClient}`, httpOptions)
  }

  blockClient(data : { idClient: number, reverse: boolean }) {
    return this.http.post(`${this.endpoint}/block-client`, data, httpOptions)
  }

  deactivateClient(idClient: number) {
    return this.http.post(`${this.endpoint}/deactivate-client`, {idClient}, httpOptions)
  }

  activateClient(idClient: number) {
    return this.http.post(`${this.endpoint}/activate-client`, {idClient},  httpOptions)
  }

  countClientsBlocked() : Observable<any>{
    return this.http.get(`${this.endpoint}/count-client-blocked`, httpOptions)
  }

  getClientsBlocked(range: number, page: number, filter: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/list-client-blocked/${range}/${page}/${filter}`, httpOptions)
  }

  getStatInscriptionClient() {
    return this.http.get(`${this.endpoint}/stat-inscription-client`, httpOptions)
  }

  // Model
  countModels(motif: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/count-models/${motif}`, httpOptions)
  }

  getModels(motif: string, range: number, page: number, filter: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/list-models/${motif}/${range}/${page}/${filter}`, httpOptions)
  }

  deleteModel(idClient: number) {
    return this.http.delete(`${this.endpoint}/model/${idClient}`, httpOptions)
  }

  blockModel(data : { idClient: number, reverse: boolean }) {
    return this.http.post(`${this.endpoint}/block-model`, data, httpOptions)
  }

  deactivateModel(idClient: number) {
    return this.http.post(`${this.endpoint}/deactivate-model`, {idClient}, httpOptions)
  }

  activateModel(idClient: number) {
    return this.http.post(`${this.endpoint}/activate-model`, {idClient},  httpOptions)
  }

  countModelsBlocked() : Observable<any>{
    return this.http.get(`${this.endpoint}/count-model-blocked`, httpOptions)
  }

  getModelsBlocked(range: number, page: number, filter: string) : Observable<any>{
    return this.http.get(`${this.endpoint}/list-model-blocked/${range}/${page}/${filter}`, httpOptions)
  }

  getStatInscriptionModel() {
    return this.http.get(`${this.endpoint}/stat-inscription-model`, httpOptions)
  }

  getStatSuppressionModel() {
    return this.http.get(`${this.endpoint}/stat-suppression-model`, httpOptions)
  }


  getStatusModel(status: string, filter: string) {
    return this.http.get(`${this.endpoint}/model-status/${status}/${filter}`, httpOptions)
  }

  countRequestsModel() {
    return this.http.get(`${this.endpoint}/count-requests-models`, httpOptions)
  }

  getRequestsModel(motif: string, range: number, page: number, filter: string) {
    return this.http.get(`${this.endpoint}/model-requests/${motif}/${range}/${page}/${filter}`, httpOptions)
  }

  acceptRequestModel(idClient: number, accepted: boolean) {
    const data = {
      idClient,
      accepted
    }
    return this.http.post(`${this.endpoint}/result-request-model`, data,  httpOptions)
  }

  acceptInscriptionModel(idClient: number, accepted: boolean, motif: string) {
    const data = {
      idClient,
      accepted,
      motif
    }
    return this.http.post(`${this.endpoint}/result-inscription-model`, data,  httpOptions)
  }

  getInfosModel(idClient) {
    return this.http.get(`${this.endpoint}/getInfosModel/${idClient}`, httpOptions)
  }

  countModelActif() {
    return this.http.get(`${this.endpoint}/countModelActif`, httpOptions)
  }

  countClientActif() {
    return this.http.get(`${this.endpoint}/countClientActif`, httpOptions)
  }

  countModelState(state: string) {
    return this.http.get(`${this.endpoint}/countModelState/${state}`, httpOptions)
  }

  newLastClients() {
    return this.http.get(`${this.endpoint}/newLastClients`, httpOptions)
  }

  newLastModels() {
    return this.http.get(`${this.endpoint}/newLastModels`, httpOptions)
  }

  countRoom(type) {
    return this.http.get(`${this.endpoint}/countRoom/${type}`, httpOptions)
  }

  getChiffreAffaire() {
    return this.http.get(`${this.endpoint}/getChiffreAffaire`, httpOptions)
  }

  getAverageClient() {
    return this.http.get(`${this.endpoint}/getAverageClient`, httpOptions)
  }

  get10LastShowPrivate() {
    return this.http.get(`${this.endpoint}/get10LastShowPrivate`, httpOptions)
  }

  get10LastShowTips() {
    return this.http.get(`${this.endpoint}/get10LastShowTips`, httpOptions)
  }

  get10LastShowVIP() {
    return this.http.get(`${this.endpoint}/get10LastShowVIP`, httpOptions)
  }

  get10LastShowChoiceUS() {
    return this.http.get(`${this.endpoint}/get10LastShowChoiceUS`, httpOptions)
  }

  getTop10Model() {
    return this.http.get(`${this.endpoint}/getTop10Model`, httpOptions)
  }

  getCreditActifsModels() {
    return this.http.get(`${this.endpoint}/getCreditActifsModels`, httpOptions)
  }

  // Paiement
  countPay() {
    return this.http.get(`${this.endpoint}/countPay`, httpOptions)
  }

  getListPaiement(flux: string, range: number, page: number, filter: string) {
    return this.http.get(`${this.endpoint}/getListPaiement/${flux}/${range}/${page}/${filter}`, httpOptions)
  }

  deletePaiement(idPay: number) {
    return this.http.post(`${this.endpoint}/deletePaiement`, {idPay},  httpOptions)
  }

  getModelsActif() {
    return this.http.get(`${this.endpoint}/getModelsActif`, httpOptions)
  }


  payCreditModel(idModel: number, pseudoModel: string, emailModel: string,
    credit: number, lastPayement: string) {
    const data = {
      idModel,
      pseudoModel,
      emailModel,
      credit,
      lastPayement
    }
    return this.http.post(`${this.endpoint}/pay-credit-model`, data,  httpOptions)
  }

  payModel(idModel: number, pseudoModel: string, emailModel: string, credit: number) {
    const data = {
      idModel,
      pseudoModel,
      emailModel,
      credit
    }
    return this.http.post(`${this.endpoint}/pay-model`, data,  httpOptions)
  }

  sendMail(email: string, objet: string, message: string) {
    const data  = {
      email,
      objet,
      message
    }
    return this.http.post(`${this.endpoint}/sendMail`, data,  httpOptions)
  }

  // Taboo
  getTaboo() {
    return this.http.get(`${this.endpoint}/list-taboo`, httpOptions)
  }

  deleteTaboo(idTaboo: number) {
    return this.http.delete(`${this.endpoint}/delete-taboo/${idTaboo}`, httpOptions)
  }

  addTaboo(word: string) {
    return this.http.post(`${this.endpoint}/add-taboo`, {word},  httpOptions)
  }

  // Special ADMIN
  getInfosAdmin() {
    return this.http.get(`${this.endpoint}/get-info-admin`, httpOptions)
  }

  updateInfoAdmin(pseudo: string, url: string) {
    const data = {
      pseudo,
      url
    }
    console.log('Update service ', data)
    return this.http.post(`${this.endpoint}/update-info-admin`, data,  httpOptions)
  }

  changePasswordAdmin(oldPassword: string, newPassword: string) {
    const data = {
      oldPassword,
      newPassword
    }
    return this.http.post(`${this.endpoint}/change-password-admin`, data,  httpOptions)
  }
}
