import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../../services/admin/admin.service';
import { TimerService } from '../../../../../services/timer/timer.service';

@Component({
  selector: 'app-main-dashboard-admin',
  templateUrl: './main-dashboard-admin.component.html',
  styleUrls: ['./main-dashboard-admin.component.scss']
})
export class MainDashboardAdminComponent implements OnInit {
  labelsShow = [
    {
      title: '10 DERNIERS SHOW',
      label : [
        'Modle', 'Date', 'Dure',
        'Gains', 'Type'
      ]
    },
    {
      title: 'TOP 10 MODELES',
      label : [
        'Modle', 'Show cre', 'Gains',
        'Dernier show', 'Rang'
      ]
    },
    {
      title: 'TOP 3 DES CLIENTS LES PLUS ACTIFS',
      label : [
        'Pseudo', 'Show regard', 'Dure moyenne',
        'Rang'
      ]
    }
  ]

  lastShowPrivate = []
  lastShowVIP = []
  lastShowTIPS = []
  lastShowChoiceUS = []
  valueShow = []

  topModels = []

  currentShowLabel = 'private'
  labelShow = this.labelsShow[0]

  constructor(
    private adminService: AdminService,
    private timerService: TimerService
  ) { }

  ngOnInit(): void {
    this.getLastShowPrivate()
    this.getLastShowVIP()
    this.getLastShowTips()
    this.getLastShowChoiceUS()
    this.getTopModels()
  }

  getLastShowPrivate() {
    this.adminService.get10LastShowPrivate().subscribe(
      (data: any[]) => {
        // console.log(data)
        let tmp = data
        tmp.forEach(value => {
            value.pseudo = value.model.pseudo
            value.date = this.adminService.formatDate(value.createdAt)
            value.duree = this.getDuree(value.createdAt, value.updatedAt)
            value.gain = value.gain
            value.type = 'Private'
        })
        this.lastShowPrivate = tmp
        this.valueShow = this.lastShowPrivate
      }, (error) => {
        console.log(error)
      }
    )
  }

  getLastShowTips() {
    this.adminService.get10LastShowTips().subscribe(
      (data: any[]) => {
        // console.log('Show tips ', data)
        let tmp = data
        tmp.forEach(value => {
            value.pseudo = value.model.pseudo
            value.date = this.adminService.formatDate(value.createdAt)
            value.duree = this.getDuree(value.createdAt, value.updatedAt)
            value.gain = value.gain
            value.type = 'TIPS'
        })
        this.lastShowTIPS = tmp
      }, (error) => {
        console.log(error)
      }
    )
  }

  getLastShowVIP() {
    this.adminService.get10LastShowVIP().subscribe(
      (data: any[]) => {
        let tmp = data
        tmp.forEach(value => {
            value.pseudo = value.model.pseudo
            value.date = this.adminService.formatDate(value.createdAt)
            value.duree = this.getDuree(value.createdAt, value.updatedAt)
            value.gain = value.gain
            value.type = 'VIP'
        })
        this.lastShowVIP = tmp.filter((data) => data.free === null)
        this.lastShowVIP = this.lastShowVIP.slice(0, 10);
        console.log('Show VIP ', this.lastShowVIP);
      }, (error) => {
        console.log(error)
      }
    )
  }

  getLastShowChoiceUS() {
    this.adminService.get10LastShowChoiceUS().subscribe(
      (data: any[]) => {
        let tmp = data
        tmp.forEach(value => {
            value.pseudo = value.model.pseudo
            value.date = this.adminService.formatDate(value.createdAt)
            value.duree = this.getDuree(value.createdAt, value.updatedAt)
            value.gain = value.gain
            value.type = 'CHOICE US'
        })
        this.lastShowChoiceUS = tmp.filter((data) => data.free !== null)
        this.lastShowChoiceUS = this.lastShowChoiceUS.slice(0, 10);
        console.log('Show CHOICE US ', this.lastShowChoiceUS);
      }, (error) => {
        console.log(error)
      }
    )
  }

  getTopModels() {
    this.adminService.getTop10Model().subscribe(
      (data: any[]) => {
        // console.log(data)
        let tmp = data
        let countLoop = 0
        tmp.forEach(value => {
          value.pseudo = value.pseudo
          value.showCreated = value.privateRooms.length + value.vipRooms.length
          value.gain = value.credit.credit
          value.lastShow = this.adminService.formatDate(value.credit.updatedAt)
          value.rang = countLoop + 1
          countLoop++
        })
        // console.log(tmp)
        this.topModels = tmp
      }, (error) => {
        console.log(error)
      }
    )
  }

  changeSelectionShow(text) {
    this.currentShowLabel = text
    if (text === 'private') {
      this.labelShow = this.labelsShow[0]
      this.valueShow = this.lastShowPrivate
    } else if (text === 'vip') {
      this.labelShow = this.labelsShow[0]
      this.valueShow = this.lastShowVIP
    } else if (text === 'choice-us') {
      this.labelShow = this.labelsShow[0]
      this.valueShow = this.lastShowChoiceUS
    } else if (text === 'tips') {
      this.labelShow = this.labelsShow[0]
      this.valueShow = this.lastShowTIPS
    } else if (text === 'top') {
      this.labelShow = this.labelsShow[1]
      this.valueShow = this.topModels
    }
  }

  getDuree(created: string, updated: string) {
    const result = this.timerService.convertTime(created, updated)
    const hourStr = result.hour < 10 ? '0'+result.hour : result.hour
    const minStr = result.minute < 10 ? '0'+result.minute : result.minute
    const secStr = result.second < 10 ? '0'+result.second : result.second

    if (result.hour > 0) {
      return hourStr+':'+minStr+':'+secStr
    }
    return minStr+':'+secStr
  }
}
