import { Component, Input, OnInit } from '@angular/core';
import { SlideInOutAnimation } from 'src/app/animation';
import { HeaderService } from '../../../services/header/header.service';
import * as AOS from 'aos';

@Component({
  selector: 'app-disconnected',
  templateUrl: './disconnected.component.html',
  styleUrls: ['./disconnected.component.scss'],
  animations: [SlideInOutAnimation]
})
export class DisconnectedComponent implements OnInit {
  animationState = 'out';
  connect: boolean = false;
  showContact: boolean = false;

  @Input()
  actif: string;
  active: number = 0;

  defaultLogoBul = './../../../../assets/logos/bul_blured.jpg';
  logoBUL = './../../../../assets/logos/dark-light-removebg.png';

  defaultLogoCP = './../../../../assets/logos/centralPay_blured.jpg';
  logoCP = './../../../../assets/logos/logo-centralpay-fullservice-blanc-transparent.png';
  constructor(private headerSrv: HeaderService) { }

  ngOnInit(): void {
    AOS.init();
    if (this.actif) this.active = parseInt(this.actif);
  }

  toggleShowDiv(divName: string) {
    if (divName === 'contain_disconnected') {
      console.log(this.animationState);
      this.animationState = this.animationState === 'out' ? 'in' : 'out';
      // console.log(this.animationState);
    }
  }

  toggleContact() {
    this.showContact = !this.showContact;
  }

}
