import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../../services/client/client.service';
import { TipsService, TIP } from '../../../services/tips/tips.service';
import { NotificationService } from '../../../services/notification/notification.service';
import { CreditService } from '../../../services/credit/credit.service';
import { BetweenShopService } from '../../../services/between-shop/between-shop.service';

@Component({
  selector: 'app-between-shop',
  templateUrl: './between-shop.component.html',
  styleUrls: ['./between-shop.component.scss']
})
export class BetweenShopComponent implements OnInit {
  coinImg = "assets/icons/coin-stack.svg";

  client = {
    id: 0,
    pseudo: '',
    credit: 0,
    creditId: null,
    promo: null,
    vip: 0
  }

  subscribed: boolean = false;

  tipsVIP: TIP[] = [];

  freeCash = [
    {
      designation: '5 crdits', symbole: '5 crdits', credit: 50, get: 5, src : './../../../../assets/icons/coin-stack-gold.svg',
    },
    {
      designation: '10 crdits', symbole: '10 crdits', credit: 100, get: 10, src : './../../../../assets/icons/coin-stack-gold.svg',
    },
    {
      designation: '15 crdits', symbole: '15 crdits', credit: 150, get: 15, src : './../../../../assets/icons/coin-stack-gold.svg',
    }
  ];

  listItem: any[] = [];

  constructor(
    private clientService: ClientService,
    private tipsService: TipsService,
    private notificationService: NotificationService,
    private creditService: CreditService,
    private betweenShopService: BetweenShopService
  ) { }

  ngOnInit(): void {
    this.tipsVIP = this.tipsService.tipsVIP;
    this.getMyCredit();
    this.getMyItems();
  }

  async getMyItems() {
    this.betweenShopService.getItems().subscribe(
      (data: any) => {
        this.listItem = data;
        this.tipsVIP.forEach(
          (tips) => {
            this.listItem.forEach(
              (item) => {
                if (item.symbol === tips.symbole) {
                  tips.got = true;
                }
              }
            )
          }
        )
      }, (error) => {
        console.log(error);
      }
    )
  }


  // Get client infos
  async getMyCredit() {
    await this.clientService.getMyInfos().subscribe(
      (data) => {
        this.client.id = data.id;
        this.client.pseudo = data.pseudo;
        this.client.credit = data.credit ? data.credit.credit : 0;
        this.client.creditId = data.credit ? data.credit.id : 0;
        this.client.vip = data.credit ? data.credit.vip : 0;
      }
    )
  }

  buyTips(symbol: string, designation: string) {
    const credit = 25;
    if (this.client.vip < credit) {
      this.notificationService.failure('B-COINS INSUFFISANT', 'Votre b-coins est insuffisant pour cet item');
      return null;
    }
    this.betweenShopService.addItems(symbol, credit).subscribe(
      (data) => {
        // console.log(data);
        this.client.vip = this.client.vip - credit;
        this.notificationService.success('TIPS VIP', `Vous avez dploqu ${designation}`);
        this.getMyItems();
      }, (error) => {
        console.log(error);
      }
    )
  }

  buyCreditByVIP(montantVIP: number, creditGain: number) {
    if (this.client.vip < montantVIP) {
      this.notificationService.failure('B-COINS INSUFFISANT', 'Votre b-coins est insuffisant pour cet item');
      return null;
    }
    this.creditService.buyCreditInShop(this.client.creditId, montantVIP, creditGain).subscribe(
      (data) => {
        // console.log(data);
        this.client.vip = this.client.vip - montantVIP;
        this.notificationService.success('CREDITS OBTENUS', `Vous avez obtenu ${creditGain} crdits`);
      }, (error) => {
        console.log(error);
      }
    )
  }
}
