import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../services/admin/admin.service';

@Component({
  selector: 'app-topbar-admin',
  templateUrl: './topbar-admin.component.html',
  styleUrls: ['./topbar-admin.component.scss']
})
export class TopbarAdminComponent implements OnInit {
  icon_notification = './../../../../../assets/icons/admin-icons/notification.png'
  value_notification = 0

  info = {
    pseudo: '',
    url: '',
    email: ''
  }

  constructor(
    private adminService: AdminService
  ) { }

  ngOnInit(): void {
    this.getInfos()
  }

  getInfos() {
    this.adminService.getInfosAdmin().subscribe(
      (data: any) => {
        this.info = data
      }, (error) => {
        console.log(error)
      }
    )
  }

}
