import { Component, ElementRef, EventEmitter, Input, IterableDiffers, OnInit, Output, ViewChild } from '@angular/core';
import { ClientCam } from '../../../../interfaces/chat.interface';
import * as $ from 'jquery'
import { MatMenuTrigger } from '@angular/material/menu';
import { BanishService } from 'src/app/services/banish/banish.service';
import { SocketService } from '../../../../services/socket/socket.service';
import { ClientService } from '../../../../services/client/client.service';


interface Stream {
  clientId: number,
  stream: MediaStream,
  peerId: any,
  isAudio: boolean,
  isVideo: boolean
}

@Component({
  selector: 'app-live-private-cam',
  templateUrl: './live-private-cam.component.html',
  styleUrls: ['./live-private-cam.component.scss']
})
export class LivePrivateCamComponent implements OnInit {
  @Input()
  streams: Stream[];

  @Input()
  myId: number;

  @Input()
  clients: ClientCam[];

  @Input()
  canSelect: boolean;

  @Input()
  showListUser: boolean;

  @Input()
  roomId: string;

  @Input()
  modelId: number;

  @Output()
  clientSelected = new EventEmitter<number>();

  idSelected: number = null;

  currentPseudo: string = null;

  img = "assets/logos/dark-light.jpg";
  // img = "assets/images/user_chat.png";
  iterableDiffer: any;

  @ViewChild(MatMenuTrigger)
  contextMenu: MatMenuTrigger;

  contextMenuPosition = { x: '0px', y: '0px' };

  @ViewChild('map') map;
  private isVisible: boolean = false;

  constructor(
    private iterableDiffers: IterableDiffers,
    private banishService: BanishService,
    private socketService: SocketService,
    private clientService: ClientService
  ) {
    this.iterableDiffer = iterableDiffers.find([]).create(null);
  }

  ngOnInit(): void {
    $(".live_cam").autoplay = true;
  }


  ngAfterContentChecked(): void
  {
    setTimeout(
      () => {
        if (this.isVisible == false && this.map.nativeElement.offsetParent != null)
        {
            this.isVisible = true;
            // console.log('visible');
            $('.live_cam').trigger('play');
        }
        else if (this.isVisible == true && this.map.nativeElement.offsetParent == null)
        {
            // console.log('invisible')
            this.isVisible = false;
        }
      }, 1000
    )
  }

  selectClient(id: number) {
    if (this.canSelect) {
      this.idSelected = this.idSelected == id ? null : id;
      this.clientSelected.emit(this.idSelected);
    }
  }

  ngDoCheck() {
    let changes = this.iterableDiffer.diff(this.streams);
    if (changes) {
      const theId = '#'+this.myId;
      $(theId).prop('volume', 0);
      this.toggleAudio();
    }
  }

  toggleAudio() {
    this.streams.forEach(
      stream => {
        const currentId = '#'+stream.clientId
        if (stream.isAudio) {
          $(currentId).prop('volume', 1)
        } else if (!stream.isAudio) {
          $(currentId).prop('volume', 0)
        }
      }
    )
  }

  getPseudo(clientId: number) {
    if (!clientId) return null;
    this.clientService.getClient(clientId).subscribe(
      (data) => {
        if (data && data.pseudo) {
          this.currentPseudo = data.pseudo;
        }
        // console.log(this.currentPseudo);
      }
    )
  }

  clearPseudo() {
    this.currentPseudo = null;
  }

  onContextMenu(event: MouseEvent, item: any) {
    event.preventDefault();
    this.contextMenuPosition.x = event.clientX + 'px';
    this.contextMenuPosition.y = event.clientY + 'px';
    this.contextMenu.menuData = { 'item': item };
    this.contextMenu.menu.focusFirstItem('mouse');
    this.contextMenu.openMenu();
  }

  banishClient(clientId: number) {
    this.banishService.banishClient(this.modelId, clientId).subscribe(
      (data) => {
        this.socketService.banishClient(this.roomId, clientId)
        // console.log(data)
      }
    )
  }

  clearStream(streamId: number) {
    if (!streamId || (streamId === undefined)) return null;
    console.log(streamId);
    const index = this.streams.findIndex(element => element.clientId === streamId);
    console.log('Index ', index);
    let video = $(`#${streamId}`);
    if (video && video.nativeElement.srcObject) {
      video.nativeElement.pause();
      if ((video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
        (video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
      }
      video.nativeElement.srcObject = null;
    }
  }
}
