import { HttpEventType } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { AdminService } from '../../../../../../services/admin/admin.service';
import { ProfilService } from '../../../../../../services/profil/profil.service';
import { NotificationService } from '../../../../../../services/notification/notification.service';

@Component({
  selector: 'app-setting-admin',
  templateUrl: './setting-admin.component.html',
  styleUrls: ['./setting-admin.component.scss']
})
export class SettingAdminComponent implements OnInit {
  listTaboo: any
  imgDelete = './../../../../../../../assets/icons/admin-icons/delete.png'

  default = './../../../../../../../assets/images/user_chat.png'

  text: string = null

  info = {
    pseudo: '',
    url: null,
    email: '',
    id: 0
  }

  file: File;
  formData = new FormData();
  changedProfil: boolean = false

  oldPassword: string = null
  newPassword: string = null
  errorPassword: any = null;

  constructor(
    private adminService: AdminService,
    private profilService: ProfilService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    this.getTaboos()
    this.getInfosAdmin()
  }

  getTaboos() {
    this.adminService.getTaboo().subscribe(
      (data) => {
        this.listTaboo = data
      }, (error) => {
        console.log(error)
      }
    )
  }

  deleteTaboo(idTaboo) {
    this.adminService.deleteTaboo(idTaboo).subscribe(
      (data) => {
        this.getTaboos()
      }, (error) => {
        console.log(error)
      }
    )
  }

  addTaboo() {
    if (!this.text || this.text === '') return false
    this.adminService.addTaboo(this.text).subscribe(
      (data) => {
        this.text = null
        this.getTaboos()
      }, (error) => {
        console.log(error)
      }
    )
  }

  getInfosAdmin() {
    this.adminService.getInfosAdmin().subscribe(
      (data: any) => {
        console.log(data)
        this.info = data
      }, (error) => {
        console.log(error)
      }
    )
  }

  async profilChoose(fileInput) {
    this.file =  fileInput.target.files[0];
    var mimeType: any;
    var reader = new FileReader();
    mimeType = this.file.type;
    if (!(mimeType === "image/png" || mimeType === "image/jpeg")) return false;
    reader = new FileReader();
    reader.readAsDataURL(this.file);
    reader.onload = (event) => {
      this.info.url = reader.result;
      this.changedProfil = true
      // console.log('New url ', this.info.url)
      // this.nameRecto = this.fileRecto.name;
    };

    this.formData.delete('file');
    this.formData.append('file', this.file, this.file.name);
  }

  async uploadProfil() {

    if (!this.changedProfil) {
      this.saveInfo(this.info.pseudo, this.info.url)
      return false
    }

    this.profilService.updatePhoto(this.formData).subscribe(
      async (events) => {
        if (events.type === HttpEventType.UploadProgress) {
          // this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
        }
        else if (events.type === HttpEventType.Response) {
          const path  = events.body.path;
          this.info.url = path;
          this.saveInfo(this.info.pseudo, path)
        }
      }, (error) => {
        console.log(error);
      }
    )
  }

  async saveInfo(pseudo: string, url: string) {
    this.adminService.updateInfoAdmin(pseudo, url).subscribe(
      (data: any) => {
        if (data.success) {
          this.notificationService.infoMsg("Information modifie")
        } else if (data.error) {
          this.notificationService.errorMsg(data.message)
        }
      }, (error) => {
        console.log(error)
      }
    )
  }

  async changePassword() {
    this.errorPassword = null
    if (!this.oldPassword || this.oldPassword === '' || !this.newPassword || this.newPassword === '') {
      this.errorPassword = 'Veuillez complter les champs'
      return false
    }
    this.adminService.changePasswordAdmin(this.oldPassword, this.newPassword).subscribe(
      (data: any) => {
        if (data.error) {
          this.errorPassword = data.message
        } else if (data.success) {
          this.notificationService.infoMsg('Mot de passe modifi')
        }
      }, (error) => {
        console.log(error)
      }
    )
  }
}
