import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SubscribeService } from '../../../services/subscribe/subscribe.service';
import { PaiementService } from '../../../services/paiement/paiement.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { AlbumService } from '../../../services/album/album.service';
import { addAlbum } from '../../../interfaces/add-album.interface';
import { ModelService } from '../../../services/model/model.service';
import { PopupImageComponent } from 'src/app/modals/popup-image/popup-image.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-albums-model',
  templateUrl: './albums-model.component.html',
  styleUrls: ['./albums-model.component.scss']
})
export class AlbumsModelComponent implements OnInit {
  modelId: number = null;
  profilModel: any =  {
    pseudo: '',
    path_soft: null,
    sex_orientation: '',
    like: '',
    dislike: '',
    status: '',
    social_network: '',
    description: '',
    date_last_connection: '',
    actualStatus: ''
  };
  albumsFree: addAlbum[] = [];
  albumsPrivate: addAlbum[] = [];
  albumsVIP: addAlbum[] = [];
  hasPaid: boolean = false;

  subscription = {
    subscribed: false,
    expired: false,
    subscribe: {},
    rest: 0
  }

  bannier = "./../../../../assets/logos/dark-light.jpg";
  defaultImage = './../../../../assets/images/blured.jpg';
  totalAlbum: number  = 0;
  sectionAlbum: number = 9;
  page: number = 0;

  contextAlbum = "free";
  currentAlbum: addAlbum[] = [];
  textAlbum: any = 'Albums Free';
  count = {
    "vip": 0,
    "private": 0,
    "free": 0
  }
  loading: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private subscribeService: SubscribeService,
    private paiementService: PaiementService,
    private profilService: ProfilService,
    private albumService: AlbumService,
    private modelService: ModelService,
    private dialog: MatDialog
  ) {
    const id = this.route.snapshot.paramMap.get('model');
    this.modelId = parseInt(id);
  }

  ngOnInit(): void {
    this.verifyPayment();
    this.verifySubscription();
    this.getProfilModel();
  }

  verifyPayment() {
    this.paiementService.hasPaid().subscribe(
      (data: any) => {
        // console.log(data);
        if (data.success) this.hasPaid = true;
        // console.log('HasPaid ', this.hasPaid);
      }, (error) => {
        console.log(error);
      }
    )
  }

  verifySubscription() {
    this.subscribeService.verify().subscribe(
      (data) => {
        // console.log('Sub ', data);
        this.subscription = data;
      }
    )
  }

  getProfilModel() {
    this.profilService.getSpecificProfil(this.modelId).subscribe(
      (data: any) => {
        // console.log('Profil ', data);
        this.profilModel = data;
      }
    )
    this.modelService.getModel(this.modelId).subscribe(
      (data: {pseudo: string, path_soft: string}) => {
        // console.log('Info ',data);
        this.profilModel.pseudo = data.pseudo;
        this.profilModel.path_soft = data.path_soft;
      }
    )
    this.getAlbums('free');
    this.getAlbums('private');
    this.getAlbums('vip');
  }

  getAlbums(type: string) {
    const data = {
      modelId: this.modelId,
      type_album: type
    }

    this.albumService.getModelAlbums(data).subscribe(
      (data: any) => {
        // console.log("Album ", type, data);
        switch(type) {
          case 'free':
            this.albumsFree = data;
            this.totalAlbum = this.albumsFree.length;
            this.currentAlbum = this.albumsFree;
            break;
          case 'private':
            this.albumsPrivate = data;
            break;
          case 'vip':
            this.albumsVIP = data;
            break;
        }
      }
    )
  }


  // context : private |vip | free
  initAlbum(context: string) {
    this.contextAlbum = context;
    var verified  = false;
    switch (context) {
      case 'private':
        verified = true;
        this.totalAlbum = this.albumsPrivate.length;
        this.textAlbum = "Albums Private";
        this.currentAlbum = this.albumsPrivate;
        break;
      case 'vip':
        verified = true;
        this.totalAlbum = this.albumsVIP.length;
        this.textAlbum = "Albums VIP";
        this.currentAlbum = this.albumsVIP;
        break;
      case 'free':
        verified = true;
        this.totalAlbum = this.albumsFree.length
        this.textAlbum = "Albums Free";
        this.currentAlbum = this.albumsFree;
        break;
      default:
        break;
    }
  }

  openImage(id: number, url: string) {
    let dialogRef = this.dialog.open(PopupImageComponent, {
      width: '100%',
      data: {
        id,
        url,
        type: this.contextAlbum,
        client: true
      },
    });
    dialogRef.afterClosed().subscribe((data) => {
      if (data === 'success') this.ngOnInit();
    });
  }

}
