import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { BetweenShopService } from 'src/app/services/between-shop/between-shop.service';
import { NotificationService } from 'src/app/services/notification/notification.service';
import { TIP } from 'src/app/services/tips/tips.service';
import { TipsService, PALIER } from '../../../services/tips/tips.service';

@Component({
  selector: 'app-stat-tips',
  templateUrl: './stat-tips.component.html',
  styleUrls: ['./stat-tips.component.scss']
})
export class StatTipsComponent implements OnInit {
  tipsNormaux: TIP[] = [];
  tipsVIP: TIP[] = [];

  listItem: any[] = [];

  @Output() newItemEvent = new EventEmitter<TIP>();

  constructor(
    private tipsService: TipsService,
    public betweenShopService: BetweenShopService,
    private notificationService: NotificationService
  ) {
    this.tipsNormaux = this.tipsService.tipsNormaux;
    this.tipsVIP = this.tipsService.tipsVIP;
  }

  ngOnInit(): void {
    this.getMyItems();
  }

  async getMyItems() {
    this.betweenShopService.getItems().subscribe(
      (data: any[]) => {
        this.tipsVIP.forEach(
          (tips) => {
            data.forEach(
              (item) => {
                if (item.symbol === tips.symbole) {
                  this.listItem.push(tips);
                }
              }
            )
          }
        )
        this.listItem = data;
      }, (error) => {
        console.log(error);
      }
    )
  }

  isItem(symbol: string) {
    const index = this.listItem.findIndex(
      (element) => element.symbol === symbol
    )
    return (index >= 0) ? true : false;
  }

  payTips(symbole: string, credit: number, designation: string, vip: boolean = false) {
    if (vip) {
      if (!this.isItem(symbole)) {
        this.notificationService.infoMsg('Item  dbloquer dans Between Shop');
        return null;
      }
    }
    const data: TIP = {
      symbole,
      credit,
      src: null,
      designation,
      vip
    }
    this.newItemEvent.emit(data);
  }

}
