import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class BanishService {
  endpoint = environment.endpoint;

  constructor(
    private http: HttpClient
  ) { }

  banishClient(modelId: number, clientId: number) {
    const data = {
      modelId,
      clientId
    }
    return this.http.post(`${this.endpoint}/banish`, data, httpOptions)
  }

  getBanished(modelId: number) {
    return this.http.get(`${this.endpoint}/banish/${modelId}`, httpOptions)
  }

  isBanished(modelId: number, clientId: number) {
    const data = {
      modelId,
      clientId
    }
    return this.http.post(`${this.endpoint}/banish/isBanished`, data, httpOptions)
  }

}
