import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class BetweenShopService {
  endpoint = environment.endpoint;

  constructor(private http: HttpClient) { }

  addItems(symbol: string, price: number) {
    const data = {
      symbol,
      price
    }
    return this.http.post(`${this.endpoint}/between-shop`, data, httpOptions)
  }

  getItems(symbol?: string) {
    if (!symbol) {
      return this.http.get(`${this.endpoint}/between-shop`, httpOptions)
    }
    return this.http.get(`${this.endpoint}/between-shop/${symbol}`, httpOptions)
  }
}
